import { Course } from '../../KED.Backend.API/schema';
import migrateTask from './migrate-task';

export default function migrateCourse(course: Course, graphs?: string[]) {
  if (!course.modules) course.modules = [];
  course.modules.forEach(module => {
    if (!module.resources) {
      module.resources = [];
    }
    if (!module.taskIds) {
      module.taskIds = [];
    }
  });

  if (!course.responsibleTeachers) {
    course.responsibleTeachers = []
  }

  // Earlier wrong spelling of resources
  if ('resourses' in (course as any) && !('resources' in (course as any))) {
    course.resources = (course as any).resourses;
    delete (course as any).resourses;
  }
  if (!course.resources) {
    course.resources = [];
  }

  if (graphs) {
    graphs.forEach(label => {
      switch (label) {
        case 'tasks':
          course.tasks.forEach(task => migrateTask(task));
          break;
      }
    });
  }
}
