import * as React from 'react';
import { L, showInfo, compareProp } from '../../../utils/utils';
import env from '../../../globals/KED.env';
import { Subject } from 'kedbackend/models';
import $ from 'jquery';
import { SkolverketSubject } from './skolverket-subject';
import { UploadedSubject } from './uploaded-subject';
export { ShowSubject } from './show-subject';
import { HashRouter as Router, Route, Link, Redirect } from 'react-router-dom';
import { readBlobAsText, allowCopy } from "../utils";
import { CourseBanner } from "../courses/course-banner";
import { Spinner } from "../sub-components/spinner";

interface Props {
  feedbackUrl: string
}

interface State {
  subjects: Subject[];
  isListingSubjects: boolean;
  uploadedSubject: SkolverketSubject | null;
}

export class Subjects extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isListingSubjects: true,
      subjects: [],
      uploadedSubject: null
    };
  }

  componentWillMount() {
    this.load();
  }

  async load() {
    const subjects = await env.kedBackendClient.list("subjects");
    subjects.sort(compareProp("name"));
    this.setState({ subjects, isListingSubjects: false });
  }

  async handleFileSelect(ev: React.DragEvent<HTMLDivElement>) {
    ev.stopPropagation();
    ev.preventDefault();
    const files = ev.dataTransfer.files;
    const xml = await readBlobAsText(files[0]);
    const doc = $($.parseXML(xml));
    const model : SkolverketSubject = {
      name: doc.find("subject>name").text(),
      code: doc.find("subject>code").text(),
      purpose: doc.find("subject>purpose").text(),
      courses: []
    };
    const courses = doc.find("subject>courses");
    for (let i=0; i<courses.length; ++i) {
      const course = $(courses[i]);
      const knownledgeRequirements = [].slice.call(course.find("knowledgeRequirements"))
        .map(r => ({gradeStep: $(r).find('gradeStep').text(), text: $(r).find('text').text()}));

      model.courses.push({
        name: course.find("name").text(),
        code: course.find("code").text(),
        centralContent: course.find("centralContent").text(),
        points: parseInt(course.find("point").text()),
        knownledgeRequirements,
      });
    }
    this.setState({uploadedSubject: model});
  }

  onImportSuccess(subject) {
     showInfo(L`Importen av ${subject} lyckades`);
     this.setState({uploadedSubject: null});
     this.load();
  }

  render() {
    return <div><CourseBanner
      title={L`mnen`}
      activePage="subjects"
      routes={{feedback: this.props.feedbackUrl}}
     />
      {this.state.uploadedSubject ?
      <UploadedSubject
        onCancel={()=>this.setState({uploadedSubject: null})}
        onImportSuccess={subject => this.onImportSuccess(subject)}
        {...this.state.uploadedSubject} />
    : this.state.isListingSubjects ?
      <div><p><Spinner/>Var god vnta medan mnen hmtas...</p></div> :
      <div>
        <h2>{L`mnen`}</h2>
        <ul>{this.state.subjects.map(s =>
          <li key={s.id} className={s.publishable ? "complete" : "incomplete"}>
            <Link to={"/subjects/" + s.id}>{s.name}</Link>
          </li>)}
        </ul>
        <div className="drop-zone" onDragOver={allowCopy} onDrop={ev=>this.handleFileSelect(ev)}>
          Droppa Subject-fil hr frn skolverket (Hmtas frn <a href="http://opendata.skolverket.se" target="skolverket">opendata.skolverket.se</a>)
        </div>
      </div>
    }</div>
  }
}
