import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { Course, CourseModule, Image, Task, Document, Ability } from 'kedbackend/models';
import { createUUID } from 'kedbackend/client';
import $ from 'jquery';
import update from 'react-addons-update';
import { ICourseEditorHost } from '../interfaces/course-editor-host';
import { CourseModuleComponent } from './course-module';

interface Props {
  course: Course;
  host: ICourseEditorHost;
  idsNotCoveredByAnyTask: {[id: string]: true};
}

interface State {
}

export class CourseModuleList extends React.Component<Props,State> {
  _elem: HTMLDivElement;
  _moduleRefs: {[moduleId: string]: CourseModuleComponent};

  constructor(props: Props) {
    super(props);
    this._moduleRefs = {};
    this.state = {
    }
  }

  addModule() {
    this.props.host.update({modules: {$push: [{
      name: "Ny kursmodul",
      id: createUUID(),
      resources: [],
      taskIds: []
     } as CourseModule
    ]}});
  }

  getOrderedModules() {
    const { course } = this.props;
    const moduleOrder = course.moduleOrder || course.modules.map(m => m.id);
    return moduleOrder
      // Map ordered id to module
      .map(id => course.modules.find(m => m.id === id))
      // Ignore entries that only exists in moduleOrder but not in modules
      .filter(m => !!m)
      // Append modules at end that are not listed in moduleOrder
      .concat (course.modules.filter(m => !moduleOrder.find(id => m.id === id)));
  }

  async reorder (moduleId: string, direction: -1 | 1) {
    const { course } = this.props;
    const orderedModules = this.getOrderedModules();
    const moduleIndex = orderedModules.findIndex(m => m.id === moduleId);
    const newModuleIndex = moduleIndex + direction;
    if (newModuleIndex < 0 || newModuleIndex >= orderedModules.length) {
      // Cannot order outside limits.
      return;
    }
    const currentModuleAtPosition = orderedModules[newModuleIndex];
    const moduleOrder = orderedModules.map(m => m.id);
    moduleOrder[newModuleIndex] = moduleId;
    moduleOrder[moduleIndex] = currentModuleAtPosition.id;
    const origScrollPos = document.documentElement.scrollTop;
    await this.props.host.update({
      moduleOrder: {$set: moduleOrder}
    });
    // Now, scroll smoothly into view after reordering:
    const elemModule: HTMLElement = findDOMNode(this._moduleRefs[moduleId]);
    if (elemModule) {
      try {
        window.scrollTo({top: origScrollPos}); // Prohibit default scrolling directly upwards on move up.
        elemModule.scrollIntoView({behavior: 'smooth', block: 'start'});
      } catch (e) {
        // Only Chrome beta supports the arguments given above. Default for older browsers.
        window.scrollTo(null, origScrollPos);
        elemModule.scrollIntoView(true);
      }
    }
  }

  render() {
    const { course, host, idsNotCoveredByAnyTask} = this.props;
    const moduleOrder = course.moduleOrder || course.modules.map(m => m.id);
    const orderedModules = this.getOrderedModules();

    return <div className="sv-html-portlet sv-portlet" ref={elem => this._elem = elem}>
      {orderedModules.map(module => <CourseModuleComponent
        ref={elem => this._moduleRefs[module.id] = elem}
        key={module.id}
        course={course}
        module={module}
        onReorder={direction => this.reorder(module.id, direction)}
        getOrderedModules={()=>this.getOrderedModules()}
        host={host}
        idsNotCoveredByAnyTask={idsNotCoveredByAnyTask}
       />)}
      <div className="btn btn-large" onClick={()=>this.addModule()}>
        Lgg till kursmodul
      </div>
    </div>
  }
}

