import * as React from 'react';
import {Ability, KnowledgeRequirement} from 'kedbackend/models';
import env from '../../../globals/KED.env';
import {RemoveItem} from './remove-item';
import update from 'react-addons-update';

interface Props {
  knowledgeRequirements: KnowledgeRequirement[];
  idsToMarkNotOk?: {[id: string]: true};
  // Props used in edit mode (editing course templates)
  coveredIds?: string[];
  // Props used in mark mode (editing tasks)
  markMode?: boolean;
  markedIds?: string[];
  onMarkChanged: (id: string, isMarked: boolean) => void;
}

export class KnowledgeMatrix extends React.Component<Props, {}> {
  constructor(props: Props) {
    super(props);
  }

  render() {
    const { knowledgeRequirements, markedIds } = this.props;
    const { idsToMarkNotOk, markMode, onMarkChanged } = this.props;

    const columns = ["E", "C", "A"];
    const rows: KnowledgeRequirement[][] = [];
    const reqs = knowledgeRequirements.slice();
    let row: KnowledgeRequirement[] | null = null;
    while (true) {
      row = columns.map(grade => {
        const pNext = reqs.findIndex(r => r.gradeStep.toUpperCase() === grade);
        if (pNext < 0) return null;
        const rv = reqs[pNext];
        reqs.splice(pNext, 1);
        return rv;
      });
      if (row.every(r => r === null))
        break;
      rows.push(row);
    }

    return <div>
      <table>
        <thead>
          <tr>
            {columns.map(c => <th key={c}>{c}</th>)}
          </tr>
        </thead>
        <tbody>
          {rows.map((row,i) =>
            <tr key={i}>
              {row.map((requirement,j) => {
                const isMarkedOK = requirement && markedIds && markedIds.indexOf(requirement.id) >= 0;
                const isMarkedNotOK = requirement && idsToMarkNotOk && idsToMarkNotOk[requirement.id];
                return <td
                  key={j}
                  dangerouslySetInnerHTML={{__html: requirement ? requirement.name : ""}}
                  className={(markMode ? "selectable" : "") +
                  (isMarkedOK ?
                    " markedGreen" :
                    (isMarkedNotOK ?
                      " markedRed":
                      ""))}
                    onClick={markMode && requirement ?
                      ()=>onMarkChanged(requirement.id, !isMarkedOK) :
                      undefined} />
              })}
            </tr>
          )}
        </tbody>
      </table>
    </div>

    {/*return <div>
      <table>
        <thead>
          <tr>
            <th>E</th>
            <th>C</th>
            <th>A</th>
          </tr>
        </thead>
        <tbody>
          {tbody.map((row, i) => 
          <tr key={i}>{row.map((cell, j) => {
            const isMarkedOK = props.markedIds && props.markedIds.indexOf(cell.id) >= 0;
            const isMarkedNotOK = props.idsToMarkNotOk && props.idsToMarkNotOk[cell.id];
            return <td
              key={j}
              rowSpan={cell.rowSpan}
              className={(props.markMode ? "selectable" : "") +
                (isMarkedOK ?
                  " markedGreen" :
                  (isMarkedNotOK ?
                    " markedRed":
                    ""))}
              onClick={props.markMode && (()=>props.onMarkChanged(cell.id, !isMarkedOK))}>
              {cell.html && <div style={{position: 'relative'}}>
                <p
                  className={cell.type === 'ability' ? 'abilityText' : 'criteriaText'}
                  dangerouslySetInnerHTML={{__html: cell.html}} />
                {props.editMode && <RemoveItem className="upperRight" onClick={()=>
                  this.setCellIds(
                    cell.type,
                    cell.rowIndex,
                    this.getCellIds(
                      cell.type,
                      cell.rowIndex).filter(id => id != cell.id) )} /> }
              </div>}
            </td>})}
          </tr>)}
        </tbody>
      </table>
    </div>*/}
  }
}
