import * as React from 'react';
import exec from './exec';
import {washHtml} from './wash-html';
import imageEditActions from './image-edit-actions';
import actions from './actions';

export interface Action {
  name?: string;
  icon: string;
  title: string;
  result: (ev: React.MouseEvent<HTMLButtonElement>, component: Wysiwyg) => void;
}

interface Props {
  className?: string;
  html: string;
  onChange?: (html: string) => void;
  defaultActions?: {[name: string]: Action};
  actions?: (Partial<Action> | string)[];
  readOnly?: boolean;
}

interface State {
  focusRect: ClientRect | null;
}

const classes = {
  actionbar: 'wysiwyg-actionbar',
  button: 'wysiwyg-button',
  content: 'wysiwyg-content',
  focusrect: 'wysiwyg-focusrect',
  focuspoint: 'wysiwyg-focuspoint',
  readonlyContent: 'wysiwyg-content readonly'
}

export class Wysiwyg extends React.Component<Props,State> {
  contentDiv: HTMLDivElement;
  focusRectDiv: HTMLDivElement;
  focusElem: HTMLElement | null;
  corner: string;
  resizeStartX: number;

  constructor (props: Props) {
    super(props);
    this.state = {focusRect: null};
    this.onFocus = this.onFocus.bind(this);
    this.onBlur = this.onBlur.bind(this);
    this.onMouseDown = this.onMouseDown.bind(this);
    this.onMouseMove = this.onMouseMove.bind(this);
    this.onMouseUp = this.onMouseUp.bind(this);
  }

  componentDidMount() {
    Array.from(this.contentDiv.querySelectorAll("img,a")).map(elem => (elem as (HTMLImageElement | HTMLAnchorElement)))
    .forEach(elem => {
      elem.tabIndex = 1;
      elem.onfocus = this.onFocus;
      elem.onblur = this.onBlur as any;
    });
    document.addEventListener('mousedown', this.onMouseDown);
    document.addEventListener('mousemove', this.onMouseMove);
    document.addEventListener('mouseup', this.onMouseUp);
  }

  componentDidUpdate() {
    Array.from(this.contentDiv.querySelectorAll("img,a")).map(elem => (elem as (HTMLImageElement | HTMLAnchorElement)))
    .forEach(elem => {
      elem.tabIndex = 1;
      elem.onfocus = this.onFocus;
      elem.onblur = this.onBlur as any;
    });
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.onMouseDown);
    document.removeEventListener('mousemove', this.onMouseMove);
    document.removeEventListener('mouseup', this.onMouseUp);
  }

  shouldComponentUpdate(nextProps: Props, nextState: State) {
    //this.contentDiv.onfocus = this.onFocus;
    //this.contentDiv.onblur = this.onBlur;
    return !this.contentDiv ||
      nextState != this.state ||
      nextProps.readOnly !== this.props.readOnly ||
      nextProps.html !== this.contentDiv.innerHTML;
  }

  triggerOnChange(html: string) {
    this.props.onChange && this.props.onChange(washHtml(html));
  }

  onFocus(ev: FocusEvent) {
    const elem = ev.target as HTMLElement;
    if (!elem || !elem.tagName) return;
    if (elem.tagName !== 'IMG' && elem.tagName !== 'A') return;
    const contentParent = this.contentDiv.parentElement;
    const newState = {
      focusRect: getRelatativeClientRect(contentParent, elem),
    }
    switch (elem.tagName) {
      case 'A':
      case 'IMG':

      default: break;
    }
    this.setState(newState);
    this.focusElem = elem;
  }

  onBlur(ev: React.FocusEvent<HTMLDivElement>) {
    /*if (ev.relatedTarget) {
      const relatedTarget = ev.relatedTarget as HTMLElement;
      if (relatedTarget.className && relatedTarget.className.split(' ').indexOf(classes.button) >= 0) {
        // A image action button was pressed
        set
        return;
      }
    }*/
    if ((ev.target === this.focusElem && ev.relatedTarget !== this.focusRectDiv) ||
      ev.target === this.focusRectDiv)
    {
      this.setState({focusRect: null});
    }
  }

  makeClickable(elem: HTMLElement) {
     elem.tabIndex = 1;
  }

  onMouseDown(ev) {
    if ((ev.target.className || "").split(' ').indexOf(classes.focuspoint) >= 0) {
      const corner = this.getRectCorner(ev);
      this.corner = corner;
      this.resizeStartX = ev.clientX;
    }
  }

  onMouseMove(ev: MouseEvent) {
    if (this.corner && this.state.focusRect && this.focusElem) {
      ev.preventDefault();
      // TODO: Rkna ut baserat p this.corner hur bildens storlek borde ndras.
      // Leta upp bilden per ID frn this.contentDiv
      // Stt DIV:ens style attribut width till ny width.
      // Om DIV:en redan hade height, stt ny height med samma aspect ratio som innan,
      // annars, stt inte height alls (eller mjligtvis till auto))
      const focusRect = this.focusRectDiv.getBoundingClientRect();
      if (focusRect.width < 32) return;
      //const currentWidth = focusRect.width;
      //const currentHeight = focusRect.height;
      //const hasHeightStyle = !this.focusElem.style.height || this.focusElem.style.height === "auto";
      const newWidth = Math.max(32, this.corner.endsWith('l') ?
        focusRect.width + (this.resizeStartX - ev.clientX) :
        //focusRect.right - ev.clientX :
        focusRect.width - (this.resizeStartX - ev.clientX));
      this.resizeStartX = ev.clientX;
        //ev.clientX - focusRect.left;
      const factor = newWidth / focusRect.width;
      const newHeight = focusRect.height * factor;
      this.focusElem.style.width = newWidth + "px";
      this.focusElem.style.height = newHeight + "px";
      this.setState({
        focusRect: getRelatativeClientRect(this.contentDiv.parentElement, this.focusElem),
      });
    }
  }

  onMouseUp(ev) {
    if (this.corner && this.state.focusRect && this.focusElem) {
      this.corner = null;
      this.triggerOnChange(this.contentDiv.innerHTML);
    }
  }

  getRectCorner(ev) {
    for (const className of (ev.target.className || '').split(' ')) {
      switch (className) {
        case 'fpul':
          return 'ul';
        case 'fpur':
          return 'fpur';
        case 'fplr':
          return 'lr';
        case 'fpll':
          return 'll';
      }
    }
    return null;
  }

  execImageEditAction (cmd: string) {
    //console.log(cmd);
    if (!this.focusElem) return;
    //console.log("doing it");
    switch (cmd) {
      case 'float-left':
        this.focusElem.style.cssFloat = 'left';
        break;
      case 'float-right':
        this.focusElem.style.cssFloat = 'right';
        break;
      case 'unfloat':
        this.focusElem.style.cssFloat = '';
        break;
    }
    this.triggerOnChange(this.contentDiv.innerHTML);
    this.setState({focusRect: getRelatativeClientRect(this.contentDiv.parentElement, this.focusElem)});
  }

  render() {
    const defaultActions = this.props.defaultActions || actions;
    let actionsToUse = this.props.actions ? this.props.actions.map(action =>
      typeof action === 'string' ?
        defaultActions[action] :
        defaultActions[action.name] ?
          { ...defaultActions[action.name], ...action } :
          action)
    : Object.keys(defaultActions).map(action => defaultActions[action]);

    if (this.state.focusRect) {
      actionsToUse = actionsToUse.concat(imageEditActions(cmd => this.execImageEditAction(cmd)));
    }

    const {focusRect} = this.state;
    const {readOnly} = this.props;

    return <div className={this.props.className}>
      {!readOnly && <div className={classes.actionbar}>
        {actionsToUse.map((action, idx) =>
          <button key={idx} className={classes.button}
            dangerouslySetInnerHTML={{__html: action.icon}}
            title={action.title}
            onMouseDown={ev=>{action.result(ev, this)}}
            onMouseUp={ev=>setTimeout(()=>this.contentDiv.focus(), 10)}
          />)}
      </div>}
      <div className={readOnly ? classes.readonlyContent : classes.content} style={{position: 'relative', top: 0, left: 0}}>
        <div className="editor"
          ref={div => this.contentDiv = div}
          dangerouslySetInnerHTML={{__html: washHtml(this.props.html)}}
          contentEditable={!readOnly}
          onInput={ev => this.triggerOnChange((ev.target as any).innerHTML)}
          onKeyDown={ev => {
            if (readOnly) return;
            //console.log("Key: " + ev.which);
            if (ev.which >= 35 && ev.which <= 40) {// home/end/up/down/left/right
              ev.stopPropagation(); // Prevent entire page from scrolling??
            }
            if (ev.which === 9) ev.preventDefault();
            if ((ev.keyCode === 8 || ev.keyCode === 46) && this.focusElem && this.state.focusRect) {
              if (this.focusElem && this.focusElem.parentElement) {
                this.focusElem.parentElement.removeChild(this.focusElem);
              }
              this.focusElem = null;
              this.setState({focusRect: null});
              this.triggerOnChange(this.contentDiv.innerHTML);
            }
          }}
        />
        {focusRect && <div
          ref={div => this.focusRectDiv = div}
          className={classes.focusrect}
          onBlur={this.onBlur}
          tabIndex={1}
          style={{
            outline: 0,
            position: 'absolute',
            top: this.state.focusRect.top,
            left: this.state.focusRect.left,
            width: this.state.focusRect.width,
            height: this.state.focusRect.height}}>
          <div className={classes.focuspoint + " fpul"} style={{position:'absolute', top:0, left:0}} />
          <div className={classes.focuspoint + " fpur"} style={{position:'absolute', top:0, right:0}} />
          <div className={classes.focuspoint + " fplr"} style={{position:'absolute', bottom:0, right:0}} />
          <div className={classes.focuspoint + " fpll"} style={{position:'absolute', bottom:0, left:0}} />
        </div>}
      </div>
    </div>
  }
}

function getRelatativeClientRect (parent: HTMLElement, child: HTMLElement) : ClientRect {
  const parentRect = parent.getBoundingClientRect();
  const childRect = child.getBoundingClientRect();
  return {
    top: childRect.top - parentRect.top + parent.scrollTop,
    left: childRect.left - parentRect.left + parent.scrollLeft,
    bottom: childRect.bottom - parentRect.top + parent.scrollTop,
    right: childRect.right - parentRect.left + parent.scrollLeft,
    width: childRect.width,
    height: childRect.height
  };
}
