import { EDSCourse, EDSAssessment, EDSStudyPlan, EDSStudentFutureAbility } from '../contracts/eds';
import { RestClient } from 'kedbackend/js/dist/ked-backend-client/restclient';
import { IsomorphicEnv, BearerProvider, HttpError } from 'kedbackend/client';
import { SchoolMoment } from '../utils/school-moment';
import { SchoolTerm } from '../utils/school-term';
import { dateTimeReviver, L } from '../utils/utils';
import { courses } from './eds-stubdata/ActiveCourses';
import { assessments } from './eds-stubdata/LatestAssessments';
import { studyPlans } from './eds-stubdata/StudentGoals';
import env from '../globals/KED.env';
//Gamla: const API_URL = "https://test-dev-eds-cs.cloudapp.net/WebAPI/";
//NYA: const API_URL = "https://edsportalowinapi.azurewebsites.net/api/";

function privatizingCacheBust () {
  return {user: env.currentUser.mail};
}
export class EdsClient {
  http: RestClient;

  constructor (
    isomorphic: IsomorphicEnv,
    baseUrl: string,
    bearerProvider: BearerProvider
  ) {
    this.http = new RestClient (isomorphic, baseUrl, {bearerProvider});
    this.getActiveCourses = avoidSimultanousCalls(this.getActiveCourses);
    this.getLatestAssessments = avoidSimultanousCalls(this.getLatestAssessments);
    this.getStudentGoals = avoidSimultanousCalls(this.getStudentGoals);
    this.getStudentFutureAbilities = avoidSimultanousCalls(this.getStudentFutureAbilities);
  }

/**
   * Retrieve active courses for current logged in student.
   *
   * @param courseCode Short-name of the course. Optional.
   */
  async getActiveCourses(q: {courseCode?: string, periodName: string}) {
    try {
      const query:any = privatizingCacheBust();
      if (q) {
        if (q.courseCode) query.CourseCode = q.courseCode;
        if (q.periodName) query.PeriodName = q.periodName;
      }
      const res = await this.http.get("studentactivecourses", query);
      if (res.status != 200) throw new HttpError(res.status, await res.text());
      const json = await res.json();
      return json.courses as EDSCourse[];
    } catch (ex) {
      console.error("Error from EDS: " + ex);
      throw new Error(L`Kunde tyvrr inte ladda terminsml eller avklarade steg frn EDS. Frsk igen senare...`);
    }
  }

  /**
   * Retrieve latest assessments for current logged in user.
   * 
   * @param limit Optional limit
   */
  async getLatestAssessments(limit?: number) {
    const query:any = privatizingCacheBust();
    if (!isNaN(limit)) query.Count = limit;
    const res = await this.http.get("studentassessments", query);
    if (res.status != 200) throw new HttpError(res.status, await res.text());
    const json = await res.json();
    return json.assessments as EDSAssessment[];    
  }

  /**
   * Retrieve studyplans for current logged-in user
   */
  async getStudentGoals() {
    const res = await this.http.get("studentgoals", privatizingCacheBust());
    if (res.status != 200) throw new HttpError(res.status, await res.text());
    const json = await res.json();
    return json.studentGoals as EDSStudyPlan[];    
  }

  async getStudentFutureAbilities() {
    const res = await this.http.get("studentFutureAbilities", privatizingCacheBust());
    if (res.status != 200) throw new HttpError(res.status, await res.text());
    const json = await res.json();
    return json.studentFutureAbilities as EDSStudentFutureAbility[];
  }
}

export class EDSPeriod {
  period: string;
  term: 'AT' | 'ST';
  year: number;
  constructor (periodStringOrSchoolTerm: string | SchoolMoment) {
    if (typeof periodStringOrSchoolTerm === 'string') {
      this.period = periodStringOrSchoolTerm;
      this.term = this.period.startsWith('HT') ? 'AT' : 'ST';
      this.year = parseInt(this.period.substr(2));
      if (isNaN(this.year)) throw new Error (`Invalid period: ${this.period}`);
    } else {
      const schoolTerm = new SchoolTerm(periodStringOrSchoolTerm);
      this.period = (schoolTerm.term === 'AT' ? "HT" : "VT") + schoolTerm.year;
      this.term = schoolTerm.term;
      this.year = schoolTerm.year;
    }
  }

  get schoolTerm () : SchoolTerm {
    return new SchoolTerm({
      academicYear: this.term === 'AT' ?
        `${this.year}/${this.year + 1}` :
        `${this.year-1}/${this.year}`,
      term: this.term
    });
  }

  toString() {
    return this.period;
  }

  valueOf() {
    return this.year + ":" + (this.term === 'ST' ? "1" : "2");
  }
}

export function parseJsonDate_old (jsonDateStr: string) : Date {
  const date = dateTimeReviver("", jsonDateStr);
  if (!(date instanceof Date)) throw new Error("Invalid JSON date string: " + jsonDateStr);
  return date;
}

export class StubEdsClient implements EdsClient {
  http: RestClient;

  constructor () {
    this.http = null;
  }

  async getActiveCourses(q?: { courseCode?: string; periodName?: string; }) {
    let result = courses.courses;
      if (q) {
        if (q.courseCode) result = result.filter (c => c.shortName === q.courseCode);
        if (q.periodName) result = result.filter(c => c.periodName === q.periodName);
      }
      return result;
  }

  async getLatestAssessments(limit?: number) {
    return limit ?
        assessments.assessments.slice(0, limit) :
        assessments.assessments;
  }

  async getStudentGoals() {
    return studyPlans.StudyPlans;
  }

  async getStudentFutureAbilities() {
    return [] as EDSStudentFutureAbility[];
  }  
}

function avoidSimultanousCalls<T,A>(method: (...args:A[])=>Promise<T>) : (...args:A[])=>Promise<T> {
  let ongoingPromises: {[argsJson: string]: Promise<T>} = {};
  return function() {
    const argsJson = JSON.stringify([].slice.call(arguments));
    if (!ongoingPromises[argsJson]) {
      ongoingPromises[argsJson] = method.apply(this, arguments).then(result=>{
        delete ongoingPromises[argsJson];
        return result;
      });
    }
    return ongoingPromises[argsJson];
  }
}
