import { User } from '../contracts/ked-models';
import { parseQueryString } from '../utils/query-string';
import env from '../globals/KED.env';

export class Features {
  EditUserTasksViaPenOnly?: boolean;
  SubTasks?: boolean;
  AssignmentTasks?: boolean;

  constructor () {
    const user = env.currentUser!;
    const query = parseQueryString(location.search, {toLower: true});
    const features = ((query.features||'').split(',') as string[])
      .map(feature => feature.toLowerCase()); // Can be used on forwards...

    this.EditUserTasksViaPenOnly =
      user.schoolType !== 'Grundskolor' && query.schooltype !== "Grundskolor";
    this.SubTasks =
      user.schoolType !== 'Grundskolor' && query.schooltype !== "Grundskolor";
    this.AssignmentTasks = !!query.testversion;
  }
}
