import { AccessClaim, DocumentAccess, hasAccess as _hasAccess, AccessRight } from 'kedbackend/client';
import { Document } from 'kedbackend/models';
import getUserClaims from './get-user-claims';
import env from '../globals/KED.env';
import { parseQueryString, generateQueryString } from "../utils/query-string";
export { getUserClaims };

export const IMPERSONATION_QUERY_PARAMS = ["user","role","school","debug","testVersion","testversion","schoolType","schooltype"];

export function hasAccess(doc: Document, requestedRight: AccessRight) {
  var claims = getUserClaims(env.currentUser);
  var result = _hasAccess(DocumentAccess.fromStringArray(doc.acl || []), claims, requestedRight);
  //console.log(`Has ${requestedRight} access to ${doc.name}/${doc.id}: ${result}`);
  return result;
}

export function hasReadAccess(doc: Document) {
  return hasAccess(doc, 'R');
}

export function hasWriteAccess(doc: Document) {
  return hasAccess(doc, 'W');
}

export function hasShareAccess(doc: Document) {
  return hasAccess(doc, 'S');
}

export function isTeacherAtSchool(school: string) {
  const isTeacher = env.currentUser.roles.some(role => role === 'EMPLOYEE' || role === 'ADMIN');
  const belongsToSchool = (school||"").toLowerCase() === env.currentUser.school.toLowerCase();
  return (isTeacher && belongsToSchool);
}

export const impersonationEnv = {
  actAs (options: {role, school, url?}) {
    const {role, school, url} = options;
    const currentQuery = parseQueryString(location.search);
    const newQuery = {...currentQuery, role, school};
    const newQueryString = generateQueryString(newQuery);
    if (url) {
      location.href = `${url}${newQueryString}`;
    } else {
      location.hash = "#";
      location.search = newQueryString;
    }
  }
}

export function actAs(options: {role, school, url?}) {
  impersonationEnv.actAs(options);
}

export function preserveImpersonationQuery (url: string, query: {[name: string] : string}) {
  const currentQuery = parseQueryString(location.search);
  const newQuery = {...query};
  for (const param of IMPERSONATION_QUERY_PARAMS) {
    if (currentQuery[param]) newQuery[param] = currentQuery[param];
  }
  const newQueryString = generateQueryString(newQuery);
  return `${url}${newQueryString}`;
}
