import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { L, showInfo, showError, flatten, compareProp } from '../../../utils/utils';
import { Course, Resource, KnowledgeRequirement, Subject, Ability, Task, School } from 'kedbackend/models';
import { BatchRunner, HttpError, createUUID } from 'kedbackend/client';
import env from '../../../globals/KED.env';
import { CourseBanner } from './course-banner';
import { InnerEditCourse } from './inner-edit-course';
import moment from 'moment';
import update from 'react-addons-update';
import { updateDocumentGraphs, applyEtags, updateCourseBuilderStatus, loadCourse, updateModificationAndCreationStamps, updateCreationStamp, updateModificationStamp } from '../utils';
import { ICourseEditorHost, Dialog, DialogContainer } from '../interfaces/course-editor-host';
import { RemoveItem } from '../sub-components/remove-item';
import $ from 'jquery';
import { getUncoveredKnowledgeRequirements, getIdsNotCoveredByTasks } from './business-logic';
import cfg from '../../../globals/KED.cfg';
import { Spinner } from "../sub-components/spinner";
import { mySchoolCoursesRepo } from '../../../repos/school-courses';
import { preserveImpersonationQuery } from "../../../access-control";

const catcher = cfg.ENVIRONMENT === 'production' ?
  showError : null;

interface Props {
  id?: string;
  subjectId?: string;
  isTemplate?: boolean;
  templateId?: string;
  view?: 'studentPage' | 'courseBuilder' | 'templateBuilder';
  viewCourseUrl: string;
  feedbackUrl: string;
}

interface State {
  origCourse: Course | null;
  origTemplate: Course | null;
  course: Course;
  allCourses?: Course[];
  dialogContainers: DialogContainer[]; // When editing items, it may show as a dialog
  hasUnsavedData: boolean;
  view: 'studentPage' | 'courseBuilder' | 'templateBuilder';
}

const CONFIRM_NAVIGATE_MESSAGE =
  L`Du har osparat data. Vlj avbryt och tryck CTRL+S om du vill spara frst.`;
  
const DEFAULT_STATE: State = {
  origCourse: null,
  origTemplate: null,
  course: null,
  dialogContainers: [],
  hasUnsavedData: false,
  view: 'courseBuilder'
};

export class EditCourse extends React.Component<Props,State> implements ICourseEditorHost {
  pageYOffset: number;
  contentDiv: HTMLDivElement;
  contentYPos: number;
  isSaving: boolean;
  dialogs: Dialog[];

  setStatePromised (state: Partial<State>) : Promise<any> {
    return this._setState({hasUnsavedData: true, ...state});
  }

  setNonSaveableDataState (state: Partial<State>) : Promise<any> {
    return this._setState(state);
  }

  _setState (state: Partial<State>) : Promise<any> {
    if (state.course) {
      {
        //
        // Maintain course task links (remove links to tasks that are not used
        // by any module)
        //
        const {tasks, modules} = state.course;
        const usedTaskIds = {};
        modules.forEach(module => module.taskIds.forEach(id => usedTaskIds[id] = true));
        const filteredCourseTasks = tasks.filter(t => usedTaskIds[t.id]);
        const modifiedCourse = {...state.course};
        modifiedCourse.tasks = filteredCourseTasks;
        state = {...state, course: modifiedCourse};
      }
    }

    // Invoke our course into state property allCourses so that the
    // right-hand side list of courses gets update immediately with
    // the publishable state of this course.
    let allCourses = state.allCourses || this.state.allCourses;
    const course = state.course || this.state.course;
    if (allCourses) {
      const idxOurCourse = allCourses.findIndex(c => c.id === this.props.id);
      allCourses = update(allCourses, {$splice: [[idxOurCourse, 1, course]]});
      state = {...state};
      state.allCourses = allCourses;
    }

    return new Promise(resolve => super.setState(()=>state as State, resolve));
  }

  constructor(props: Props) {
    super(props);
    this.state = {...DEFAULT_STATE};
    this.update = this.update.bind(this);
    this.refDialog = this.refDialog.bind(this);
    this.contentYPos = 0;
    this.onKeyDown = this.onKeyDown.bind(this);
    this.beforeUnload = this.beforeUnload.bind(this);
    this.onClickPreventUnload = this.onClickPreventUnload.bind(this);
    this.isSaving = false;
    this.dialogs = [];
    if (props.view) this.state = {
      ...this.state,
      view: props.view
    };
  }

  componentWillMount() {
    this.load().catch(catcher);
    window.addEventListener('keydown', this.onKeyDown);
    window.addEventListener('beforeunload', this.beforeUnload);
    document.body.addEventListener('click', this.onClickPreventUnload);
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.onKeyDown);
    window.removeEventListener('beforeunload', this.beforeUnload);
    document.body.removeEventListener('click', this.onClickPreventUnload);
    updateCourseBuilderStatus('');
  }

  componentWillReceiveProps(nextProps: Props) {
    if (this.state && this.state.course) {
      if (nextProps.id !== this.props.id || nextProps.templateId !== this.props.templateId) {
        this.setStatePromised(DEFAULT_STATE);
      }
    }
    if (nextProps.view !== this.props.view) {
      this.setNonSaveableDataState({view: nextProps.view});
    }
  }

  componentDidUpdate(prevProps: Props, prevState: State) {
    if (this.contentDiv) {
      const contentDiv: HTMLDivElement = findDOMNode(this.contentDiv);
      if (contentDiv) {
        this.contentYPos = contentDiv.getBoundingClientRect().top;
      }
    }

    if (prevState && prevState.course) {
      // This was not the initial update.
      // If props have been updated to point out another course or template, reset component:
      if (this.props.id !== prevProps.id || this.props.templateId !== prevProps.templateId) {
        this.load().catch(catcher);
      }
    }

    if (!prevState.hasUnsavedData && this.state.hasUnsavedData) {
      updateCourseBuilderStatus('unsaved');
    }
  }

  async loadFromTemplate(templateId: string) {
    showInfo("Laddar data frn mall...");
    const template = await loadCourse(templateId, {includeTemplateChain: true});
    const course = updateCreationStamp(Date.now(), template, env.currentUser);
    course.id = createUUID();
    course.parentId = templateId;
    course.active = false;
    course.isTemplate = false;
    course.templateChain = [...course.templateChain];
    course.templateChain.push({id: templateId, $meta: 'ref'} as Course);
    const mySchool = env.currentUser.school;
    course.school = mySchool;
    course.description = "";
    course.responsibleTeachers = [];
    course.acl = [
      "role:USER:R", // All users may view it. (Could have restricted it to school, but not nescessary!)
      `schoolRole:${mySchool}/EMPLOYEE:S` // Other teachers at same school should be able to modify the course.
    ];
    delete course.$etag;
    delete course.createdBy;
    delete course.createdDate;
    delete course.modifiedBy;
    delete course.modifiedDate;
    delete course.activatedBy;
    delete course.activatedDate;
    delete course.googleClassRoomCode;
    delete course.googleClassRoomURL;
    await this.setStatePromised({ course, origCourse: null, origTemplate: template });
    showInfo("");
  }

  async load() {
    updateCourseBuilderStatus('');
    if (this.props.id) {
      await this.loadExistingCourse(this.props.id);
    } else if (this.props.templateId) {
      await this.loadFromTemplate(this.props.templateId);
    } else {
      throw new Error(L`Ogiltig parameter. id eller templateId mste anges`);
    }
  }

  async loadExistingCourse(courseId: string) {
    showInfo("Laddar kurs");
    const course = await loadCourse(courseId);
    if (!course.responsibleTeachers) course.responsibleTeachers = [];
    await this.setNonSaveableDataState({ course, origCourse: course });
    showInfo("");
  }

  async update(courseUpdates) {
    await this.setStatePromised({ course: update(this.state.course, courseUpdates) });
  }

  async deleteCourse() {
    showInfo("Raderar kursen...");
    const courseSchool = this.state.course.school;
    const parentId = this.state.course.parentId;
    const courseId = this.props.id;

    const children = await env.kedBackendClient.list<Course>("courses", {
      hasEdgesTo: [this.props.id]
    });
    const br = new BatchRunner();
    for (const childCourse of children) {
      /* CANNOT DO THIS. MAY NOT HAVE ACCESS! if (childCourse.parentId === courseId) {
        // Reconnect child with my parent instead, as I will be disappearing...
        childCourse.parentId = parentId;
        br.put("courses", childCourse);
      }*/
    }

    br.delete("courses", courseId);
    await env.kedBackendClient.batch(br.mutationRequests);
    if (courseSchool === env.currentUser.school) {
      mySchoolCoursesRepo.delete(courseId);
    }
    showInfo("");
    location.hash = "#/courses/new";
  }

  async save () {
    if (this.isSaving) return;
    try {
      this.isSaving = true;
      await this._save();
    } catch (err) {
      let errorMessage = err && err.message ? err.message : L`Ett oknt fel har intrffat...`;
      if (err) {
        if (err.name === 'http409' || err.name === 'http412' || err.name === 'http404') {
          // Conflict. Show a nicer error message:
          errorMessage = L`Kursen har redigerad av annan anvndare. Ladda om sidan och gr om ndrinarna.`;
        } else if (err.name === 'http403') {
          if (this.state.course.isTemplate)
            errorMessage = L`Ingen behrighet att spara kursmallen. Endast administratrer kan redigera kursmallar.`;
          else if (this.state.course.school)
            errorMessage = L`Ingen behrighet att spara kursen ${this.state.course.name}. Kursen kan bara redigeras av anstllda p ${this.state.course.school}`;
        }
      }
      showError(errorMessage);
      updateCourseBuilderStatus('error');
    } finally {
      this.isSaving = false;
    }
  }

  private async _save() {
    const { course, origCourse, origTemplate } = this.state;
    const doesAllTasksCoverAllRequirements = getUncoveredKnowledgeRequirements(course).length === 0;

    showInfo(L`Sparar...`);
    let modifiedCourse: Course = updateModificationStamp(Date.now(), course, env.currentUser);

    const batch = new BatchRunner();

    // Resolve manipulated graphs of each task and remove $meta, generate UUID etc for
    // each added / updated task.
    // The result of this operation is two-fold:
    //  1) Remove $meta and create id for each task. To be reflected into next state.
    //  2) Generate mutation requests into 'batch' to include in the single batch request to the server.
    const orig = origCourse || origTemplate;
    const newCourseTasks = modifiedCourse.tasks.map(task => {
      if (task.$meta === 'add' || task.$meta === 'update') {
        const origTask = orig.tasks.find(t => t.id === task.id);
        return updateDocumentGraphs(origTask || {}, task, "tasks", {
          knowledgeRequirements: 'knowledge-requirements',
          centralContent: 'central-content',
          abilities: 'abilities'
        }, batch);
      } else {
        const updatedTask = {...task};
        delete updatedTask.$meta;
        return updatedTask;
      }
    });
    modifiedCourse = update(modifiedCourse, {tasks: {$set: newCourseTasks}});

    modifiedCourse = updateDocumentGraphs(origCourse || {}, modifiedCourse, 'courses', {
      templateChain: 'courses',
      centralContent: 'central-content',
      knowledgeRequirements: 'knowledge-requirements',
      abilities: 'abilities',
      images: 'images',
      tasks: 'tasks'
    }, batch);

    modifiedCourse = {...modifiedCourse };
    modifiedCourse.publishable = doesAllTasksCoverAllRequirements;
    const tags = (modifiedCourse.tags || []).filter(tag => tag !== 'incomplete');
    modifiedCourse.tags = modifiedCourse.publishable ?
      tags : tags.concat("incomplete");

    if (!origCourse) {
      // A new course should be added:
      batch.add('courses', modifiedCourse);
      if (course.school) {
        const schoolId = await mySchoolCoursesRepo.getSchoolId();
        batch.link("schools", schoolId, "courses", course.id, "courses");
        if (course.active) {
          batch.link("schools", schoolId, "courses", course.id, "activeCourses");
        }
      }
    } else {
      // An existing course should be updated:
      batch.put('courses', modifiedCourse);
      if (!origCourse.active && course.active) {
        // Course was activated. Add a reference to the course on school.activeCourses.
        const schoolId = await mySchoolCoursesRepo.getSchoolId();
        batch.link("schools", schoolId, "courses", course.id, "activeCourses");
      } else if (origCourse.active && !course.active) {
        // Course was inactivated. Remove from school.activeCourses.
        const schoolId = await mySchoolCoursesRepo.getSchoolId();
        batch.unlink("schools", schoolId, "courses", course.id, "activeCourses");
      }
    }

    // Perform the entire batch through a single POST request:
    const res = await env.kedBackendClient.batch(batch.mutationRequests);
    showInfo (origCourse ?
      L`ndringarna har sparats` :
      L`Kursen har skapats`);
    // Update state to reflect what has been posted:
    if (res && res.newEtags)
        modifiedCourse = applyEtags(modifiedCourse, res.newEtags, ["images", "tasks"]) as Course;
    
    if (course.school === env.currentUser.school) {
      // Update mySchoolCoursesRepo
      if (!origCourse) {
        mySchoolCoursesRepo.insert(modifiedCourse);
      } else {
        mySchoolCoursesRepo.update(modifiedCourse);
      }
    }

    await this.setNonSaveableDataState({course: modifiedCourse, origCourse: modifiedCourse, hasUnsavedData: false});
    updateCourseBuilderStatus('');
    if (!origCourse) {
      // This was the creation of a new course. Correct the route now when the user enters an edit state:
      location.hash = `#/courses/${modifiedCourse.id}/edit`;
    }
  }

  activateAndSave() {
    const course = {...this.state.course};
    if (!course.active) {
      /*const uncoveredRequirements = getUncoveredKnowledgeRequirements(course);
      if (uncoveredRequirements.length > 0) 
        return showError (L`Det finns kunskapskrav som inte uppfylls av ngon modul. Kan inte aktivera kursen frrn alla kunskapskrav finns med.`);
      */

      course.active = true;
      course.activatedBy = {
        name: env.currentUser.displayName,
        url: "mailto:" +  env.currentUser.mail
      };
      course.activatedDate = Date.now();
      this.setStatePromised({course})
        .then(()=>this.save())
        .catch(catcher);
    }
  }

  inactivateAndSave() {
    const course = {...this.state.course};
    if (course.active) {
      course.active = false;
      course.inactivatedBy = {
        name: env.currentUser.displayName,
        url: "mailto:" +  env.currentUser.mail
      };
      course.inactivatedDate = Date.now();
      this.setStatePromised({course})
        .then(()=>this.save())
        .catch(catcher);
    }
  }

  openDialog(dialogContainer: DialogContainer) {
    this.setNonSaveableDataState({dialogContainers: this.state.dialogContainers.concat(dialogContainer)});
  }

  closeDialog() {
    const {dialogContainers} = this.state;
    const {length} = dialogContainers;
    length && this.setNonSaveableDataState({dialogContainers: dialogContainers.slice(0, length - 1)});
    // Now sync the direct 'dialogs' property with the new state:
    while (this.dialogs.length >= length) this.dialogs.pop();
  }

  refDialog(dialog: Dialog & React.Component, index: number) {
    this.dialogs[index] = dialog; // Setting per index instead of push(). Works even if the ref attribute could be invoked multiple times.
  }

  onKeyDown (ev: KeyboardEvent) {
    if (ev.which === 83 && (ev.ctrlKey || ev.metaKey)) { // CTRL-S
      ev.preventDefault();
      if (this.state.dialogContainers.length > 0) {
        this.saveDialog();
        return;
      }
      this.save();
    } else if (ev.which === 27 && this.state.dialogContainers.length > 0) {
      this.closeDialog();
      ev.preventDefault();
    } else if (ev.which === 13) {
      const targetElem = ev.target as HTMLElement;
      if (this.state.dialogContainers.length > 0 &&
        targetElem.tagName !== 'TEXTAREA' &&
        targetElem.tagName !== 'INPUT' &&
        targetElem.tagName !== 'SELECT' &&
        targetElem.tagName !== 'OPTION' &&
        !targetElem.contentEditable)
      {
        this.saveDialog();
      }
    }
  }

  beforeUnload(ev: Event) {
    if (this.state.dialogContainers.length > 0 || this.state.hasUnsavedData) {
      if (!env.kedBackendClient.http.bearerProvider.wantsRedirect) {
        ev.returnValue = CONFIRM_NAVIGATE_MESSAGE;
        return ev.returnValue;
      }
    }
  }

  onClickPreventUnload(ev: Event) {
    if (ev.target && (''+(ev.target as any).tagName).toLowerCase() === 'a') {
      const a: HTMLAnchorElement = ev.target as HTMLAnchorElement;
      if (a.href && a.getAttribute('href') !== '#' && !a.target) {
        if (this.state.dialogContainers.length > 0 || this.state.hasUnsavedData) {
          if (!confirm(CONFIRM_NAVIGATE_MESSAGE)) {
            ev.preventDefault();
            ev.stopPropagation();
            return false;
          }
        }
      }
    }
  }

  saveDialog() {
    if (this.state.dialogContainers.length > 0) {
      const dialog = this.dialogs[this.state.dialogContainers.length-1]; // YES, should reference this.dialogs by length of dialogContainers. Strange yes, but leave it so!
      dialog && dialog.save(); 
    }
  }

  render() {
    const isAdmin = env.currentUser.roles.indexOf("ADMIN") >= 0;
    const course = this.state.course;
    return !this.state.course ? <div style={{outline: 0}}>
      <CourseBanner
        title=""
        activePage={this.state.view}
        routes={{feedback: this.props.feedbackUrl}}
      /><p><Spinner/>V.g. vnta medan kursen laddas...</p>
      </div> :
      <div style={{outline: 0}}>
      <CourseBanner
        title={course.name + " - " + course.points + "p"}
        activePage={this.state.view}
        routes={{
          studentPage: preserveImpersonationQuery(this.props.viewCourseUrl, {courseId: this.props.id}),
          feedback: this.props.feedbackUrl
        }}
        host={this}
        course={course}
        origCourse={this.state.origCourse}
        backgroundImage={course.images.filter(img => img.id === course.imageId).map(img=>img.url)[0]}
        isTemplate={course.isTemplate}
       />
      <div className="sv-row sv-layout">
        <div className="sv-layout sv-skip-spacer sv-column-12">
          <div className="sv-spacer-20pxvt sv-vertical sv-layout sv-skip-spacer">
            <div className="pagecontent sv-layout sv-spacer-20pxvt sv-skip-spacer" ref={
              div => this.contentDiv = div}>
              <InnerEditCourse
                course={this.state.course}
                origCourse={this.state.origCourse}
                host={this} />
              <br/>
              {this.state.dialogContainers.map(({Component, props}, i) => {
                return <div key={i}>
                  <div className="darken">
                  </div>
                  <div className="modal-page-wrap">
                    <div className="modal-page">
                      <Component ref={dialog=>this.refDialog(dialog, i)} {...props} />
                      <RemoveItem onClick={()=>this.closeDialog()} />
                    </div>
                  </div>
                </div>
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  }
}
