/* REFACTOR: Move this component outside coursebuilder!
   This is a general-purpose component
*/

import * as React from 'react';

interface Props {
  title: string | JSX.Element;
  className?: string;
  headerClassName?: string;
  contentClassName?: string;
  headerOpen?: boolean;
  onOpenClose?: (becameOpen: boolean) => void;
  children;
}

interface State {
  headerOpen: boolean;
}

export class OpenCloseBox extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      headerOpen: props.headerOpen || false
    };
  }

  componentWillReceiveProps (nextProps: Props) {
    if (nextProps.headerOpen !== this.props.headerOpen) {
      this.setState({headerOpen: nextProps.headerOpen});
    }
  }

  render() {
    const { title, className, children, headerClassName, contentClassName } = this.props;
    const { headerOpen } = this.state;

    return <div className={(className||'') + " openClose" + (headerOpen ? " open" : "")}>
      <div className={"openHeader" + (headerClassName ? " " + headerClassName : "")}
        onClick={() => {
          if (this.props.onOpenClose) this.props.onOpenClose(!this.state.headerOpen);
          this.setState({ headerOpen: !this.state.headerOpen });
        }} >
        {title}
      </div>
      <div className={"openContent" + (contentClassName ? " " + contentClassName : "")}>
        {children}
      </div>
    </div>;
  }
}
