import * as React from 'react';
import { Course } from 'kedbackend/models';
import { getTasksPerId } from "../courses/business-logic";
import { futureAbilities } from "../../../contracts/ked-models";
import { OpenCloseBox } from "../../utility-components/open-close-box";

interface Props {
  course: Course;
}

interface State {
  isOpen: boolean;
}

export class WeightedItemsTable extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isOpen: false
    };
  }

  render() {
    const { course } = this.props;
    const { isOpen } = this.state;
    const rows = [];
    const tasksPerId = getTasksPerId(course);

    rows.push(<tr key="centralContent" className="covered-item-label"><td colSpan={2}>Centralt innehll</td></tr>);
    course.centralContent.forEach((cc,idx) => rows.push(<tr key={cc.id} className={idx % 2 ? "tableOdd" : ""}>
      <td><p className="criteriaText" dangerouslySetInnerHTML={{__html: cc.name}}/></td>
      <td><p>{(tasksPerId[cc.id] || []).length}</p></td>
    </tr>));

    rows.push(<tr key="abilities" className="covered-item-label"><td colSpan={2}>Frmgor</td></tr>);
    course.abilities.forEach((ability,idx) => rows.push(<tr key={ability.id} className={idx % 2 ? "tableOdd" : ""}>
      <td><p className="abilityText" dangerouslySetInnerHTML={{__html: ability.name}}/></td>
      <td><p>{(tasksPerId[ability.id] || []).length}</p></td>
    </tr>));

    rows.push(<tr key="futureAbilities" className="covered-item-label"><td colSpan={2}>Framtidsfrmgor</td></tr>);
     futureAbilities.forEach((futureAbilityText, idx) => rows.push(<tr key={futureAbilityText} className={idx % 2 ? "tableOdd" : ""}>
      <td><p className="abilityText">{futureAbilityText}</p></td>
      <td><p>{(tasksPerId[futureAbilityText] || []).length}</p></td>
    </tr>));

    return <OpenCloseBox
      className="larger"
      title={<p>Kursens tckningstabell</p>}
      contentClassName="weighted-items-table">
        <p>Tabellen anger hur mnga uppgifter som berr varje del.</p>
        <table><tbody>{rows}</tbody></table>
    </OpenCloseBox>
  }
}