import * as React from 'react';
import { L, showError, compareProp } from '../../../utils/utils';
import { Course } from "kedbackend/models";
import { mySchoolCoursesRepo } from "../../../repos/school-courses";
import { Spinner } from "./spinner";
import { Link } from 'react-router-dom';
import { shortDateFormat } from "../utils";

interface Props {
}

interface State {
  schoolCourses: Course[] | null;
}

export class SchoolCourses extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      schoolCourses: null
    }
    this.onSchoolCoursesUpdated = this.onSchoolCoursesUpdated.bind(this);
  }

  componentDidMount() {
    mySchoolCoursesRepo.subscribe(this.onSchoolCoursesUpdated).catch(showError);
  }

  componentWillUnmount() {
    mySchoolCoursesRepo.unsubscribe(this.onSchoolCoursesUpdated);
  }

  onSchoolCoursesUpdated(schoolCourses: Course[]) {
    const compareName = compareProp("name");
    const compareCreatedDate = compareProp("createdDate");
    schoolCourses.sort((a,b) => compareName(a,b) || compareCreatedDate(a,b));
    this.setState({schoolCourses});
  }

  render() {
    const {schoolCourses} = this.state;
    return <div className="sv-html-portlet sv-portlet sv-skip-spacer">
      <div className="ked_boxed">
        <h3>Skolans kurser</h3>
        <div className="taskContainer odd-even">
          {!schoolCourses ?
            <p><Spinner /> Laddar skolans kurser</p> :
            schoolCourses.map(course => <div className="schoolCourse" key={course.id}>
            <div className="align-horizontal">
              <div className="horizontalItem top pull-right">
                <Link
                  to={`/courses/${course.id}/edit`}
                  className="editItem"
                />
              </div>
              <div className="horizontalItem top">
                <Link to={`/courses/${course.id}/edit`}>{course.name}</Link></div>
              <div className="horizontalItem top">
                {course.active && <div className="pill active">Aktiv</div>}
              </div>
              {course.active && !course.publishable && <div className="horizontalItem top">
                <div className="pill incomplete">Inkomplett</div>
              </div>}
            </div>
            <div className="horizontalItem top">
              <p>{course.description}</p>
            </div>
            <div className="horizontalItem top">
              {course.modifiedBy ?
                <p className="small">Redigerad av {course.modifiedBy.name} / {shortDateFormat(course.modifiedDate)}</p> :
                <p className="small" style={{fontStyle: 'italic'}}> </p>}
            </div>
          </div>)}
        </div>
        {location.hash !== "#/courses/new" && <Link
          className="btn"
          to="/courses/new"
          onClick={()=>window.scroll(0, 0)}>
          Skapa ny kurs
        </Link>}
      </div>
    </div>
  }
}
