import exec from './exec';

export default {
  bold: {
    icon: '<b>F</b>',
    title: 'Fetstil',
    result: () => exec('bold')
  },
  italic: {
    icon: '<i>K</i>',
    title: 'Kursiv',
    result: () => exec('italic')
  },
  underline: {
    icon: '<u>U</u>',
    title: 'Understruken',
    result: () => exec('underline')
  },
  strikethrough: {
    icon: '<strike>S</strike>',
    title: 'Struken',
    result: () => exec('strikeThrough')
  },
  heading1: {
    icon: '<b>H<sub>1</sub></b>',
    title: 'Rubrik 1',
    result: () => exec('formatBlock', '<H1>')
  },
  heading2: {
    icon: '<b>H<sub>2</sub></b>',
    title: 'Rubrik 2',
    result: () => exec('formatBlock', '<H2>')
  },
  heading3: {
    icon: '<b>H<sub>3</sub></b>',
    title: 'Rubrik 3',
    result: () => exec('formatBlock', '<H3>')
  },
  paragraph: {
    icon: '&#182;',
    title: 'Paragraf',
    result: () => exec('formatBlock', '<P>')
  },
  quote: {
    icon: '&#8220; &#8221;',
    title: 'Citat',
    result: () => exec('formatBlock', '<BLOCKQUOTE>')
  },
  olist: {
    icon: '<i class="fa fa-list-ol" aria-hidden="true"></i>',
    title: 'Ordnad lista',
    result: () => exec('insertOrderedList')
  },
  ulist: {
    icon: '<i class="fa fa-list" aria-hidden="true"></i>',
    title: 'Punktlista',
    result: () => exec('insertUnorderedList')
  },
  code: {
    icon: '&lt;/&gt;',
    title: 'Programkod',
    result: () => exec('formatBlock', '<PRE>')
  },
  line: {
    icon: '&#8213;',
    title: 'Vgrt linje',
    result: () => exec('insertHorizontalRule')
  },
  link: {
    icon: '<i class="fa fa-link" aria-hidden="true"></i>',
    title: 'Infoga lnk',
    result: () => {
      const url = window.prompt('Ange lnkens URL')
      if (url) exec('createLink', url)
    }
  },
  image: {
    icon: '<i class="fa fa-picture-o" aria-hidden="true"></i>',
    title: 'Infoga bild',
    result: (ev, component) => {
      const url = window.prompt('Ange bildens URL')
      if (url) {
        //exec('insertImage', url);
        const img = document.createElement("img");
        img.src = url;
        img.tabIndex = 1;
        insertElement(img);
        img.onfocus = component.onFocus;
        img.onblur = component.onBlur;
        component.props.onChange(component.contentDiv.innerHTML);
      }
    }
  }
}

function insertElement(element: HTMLElement) {
    var sel, range;
    if (window.getSelection && (sel = window.getSelection()).rangeCount) {
        range = sel.getRangeAt(0);
        range.collapse(true);
        range.insertNode(element);
        // Move the caret immediately after the inserted span
        range.setStartAfter(element);
        range.collapse(true);
        sel.removeAllRanges();
        sel.addRange(range);
    }
}
