import { KedBackendClient } from '../ked-backend-client';
import migrate from './migrate';

export const KedModelMigratorMixin = (client: KedBackendClient) => {
    if ((client as any).__migrator_mixed_in) return;
    (client as any).__migrator_mixed_in = true;
    
    const get = client.get;
    const list = client.list;

    client.get = function (table, id, options) {
        const include = options && options.include;
        return get.apply(this, arguments).then(result => {
            migrate(result, table, include && include.toString().split(','));
            return result;
        });
    }

    client.list = function (table, options) {
        const include = options && options.include;
        return list.apply(this, arguments).then(result => {
            result.forEach(doc => migrate(doc, table, include && include.toString().split(',')));
            return result;
        });
    }

    return client;
}

