import * as React from 'react';
import { CourseBanner } from "./course-banner";
import { L, showError, flatten, compareProp, showInfo, maxLength, capitalizeFirst } from "../../../utils/utils";
import { Course, Subject } from "kedbackend/models";
import env from '../../../globals/KED.env';
import { Link } from 'react-router-dom';
import { Spinner } from "../sub-components/spinner";
import { SchoolCourses } from "../sub-components/school-courses";

interface Props {
  feedbackUrl: string;
}

interface State {
  courseOptions: CourseOption[];
  selectedTemplateId: string;
  courses: CourseOption[] | null;
  selectedCourseId: string;
}

interface CourseOption {
  id: string;
  name: string;
}

export class CreateNewCourse extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      courseOptions: null,
      selectedTemplateId: "--",
      courses: null,
      selectedCourseId: ""
    }
  }

  componentDidMount() {
    this.load().catch(showError);
  }

  async load() {
    showInfo("Laddar kurser");
    const subjects = await env.kedBackendClient.list<Subject>("subjects", { include: "courseTemplates" });
    const courses = flatten(subjects.map(s => s.courseTemplates)).sort(compareProp("name"));
    const courseOptions: CourseOption[] =
      [{ id: "--", name: "--" }].concat(courses);
    showInfo("");
    this.setState({ courseOptions });
  }

  async onSelectCourse(id: string) {
    const derivedCourses = await env.kedBackendClient.list<Course>("courses", { hasEdgesTo: [id] });
    const courseOptions = derivedCourses
      .filter(course => course.active)
      .map(({ id, school, description, createdBy, modifiedBy }) => {
        if (description) description = maxLength(description, 80);
        let descriptionOrAuthor = description ||
          (createdBy ? `${createdBy.name}s version` :
          modifiedBy && `${modifiedBy.name}s version`);
        return {
          id,
          name: school === 'standard' ?
            description ? `Standard - ${description}` : "Standard" :
            `${school} - ${maxLength(descriptionOrAuthor, 90)}`,
          school
        };
      }).sort(compareProp("name"));
    const kedStandardOptions = courseOptions.filter(option => option.school === "standard");
    const teacherMade = courseOptions.filter(option => option.school !== "standard");
    const courses = [
      ...kedStandardOptions,
      { id, name: "Tom" },
      ...teacherMade];
    this.setState({
      courses,
      selectedTemplateId: id,
      selectedCourseId: courses[0].id
    });
  }

  render() {
    const { courseOptions, selectedTemplateId, courses, selectedCourseId } = this.state;
    return <div style={{ outline: 0 }}>
      <CourseBanner
        title={L`Kursbyggaren`}
        activePage="courseBuilder"
        routes={{feedback: this.props.feedbackUrl}}
      />
      <div className="sv-row sv-layout">
        <div className="sv-layout sv-skip-spacer sv-column-12">
          <div className="sv-spacer-20pxvt sv-vertical sv-layout sv-skip-spacer">
            <div className="pagecontent sv-layout sv-spacer-20pxvt sv-skip-spacer">

              <div className="sv-row sv-layout sv-skip-spacer">
                <div className={"sv-layout sv-skip-spacer sv-column-7"}>
                  <div className="sv-html-portlet sv-portlet sv-skip-spacer">
                    <h2>Skapa ny kurs</h2>
                    <hr />
                    <div className="align-horizontal spaced">
                      <div className="horizontalItem top">
                        <p>Vlj gymnasiekurs :</p>
                      </div>
                    </div>
                    <div className="align-horizontal spaced">
                      <div className="horizontalItem top">
                        {!courseOptions ?
                          <div><Spinner /><select disabled value=""><option>--</option></select></div> :
                          <select
                            value={this.state.selectedTemplateId}
                            onChange={ev => this.onSelectCourse(ev.target.value).catch(showError)}
                          >
                            {courseOptions.map(course =>
                              <option key={course.id} value={course.id}>{course.name}</option>
                            )}
                          </select>}
                      </div>
                    </div>
                    {courses && <div className="align-horizontal spaced">
                      <div className="horizontalItem top">
                        <p>Utg frn version: </p>
                      </div>
                    </div>}
                    {courses && <div className="align-horizontal spaced">
                      <div className="horizontalItem top">
                        <select value={selectedCourseId} onChange={ev => this.setState({
                          selectedCourseId: ev.target.value
                        })}>
                          {courses.map(course =>
                            <option key={course.id} value={course.id}>{course.name}</option>
                          )}
                        </select>
                      </div>
                    </div>}
                    <br /><br />
                    {courses && selectedCourseId && <Link
                      to={`/courses/create-from-template/${this.state.selectedCourseId}`}
                      className="btn btn-large"
                    >Skapa</Link>}
                  </div>
                </div>
                <div className="sv-layout sv-column-5">
                  <SchoolCourses />
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  }
}