import * as React from 'react';
import {L} from './utils';
import $ from 'jquery';

interface Props {
}

interface State {
  error: string | undefined;
  info: string | undefined;
}

export class ErrorSuccessFeedback extends React.Component<Props,State> {
  constructor (props: Props) {
    super(props);
    this.state={
      error: undefined,
      info: undefined
    };
    this._unhandledRejection = this._unhandledRejection.bind(this);
    this._error = this._error.bind(this);
    this._customError = this._customError.bind(this);
    this._onInfo = this._onInfo.bind(this);
  }

  componentDidMount() {
    window.addEventListener('unhandledrejection', this._unhandledRejection);
    window.addEventListener('error', this._error);
    window.addEventListener('customerror', this._customError);
    window.addEventListener('info', this._onInfo);
  }

  componentWillUnmount() {
    window.removeEventListener('unhandledrejection', this._unhandledRejection);
    window.removeEventListener('error', this._error);
    window.removeEventListener('customerror', this._customError);
    window.removeEventListener('info', this._onInfo);
  }

  _unhandledRejection (ev: Event & {reason: Error}) {
    this.setState({error: ev.reason ? ev.reason.message : L`Ett oknt fel intrffade...`, info: null});
    setTimeout(()=>this.setState({error: null}), 10000);
  }

  _error (ev: Event & {error: Error}) {
    this.setState({error: ev.error ? ev.error.message : L`Ett oknt fel intrffade...`, info: null});
    setTimeout(()=>this.setState({error: null}), 10000);
  }

  _customError (ev: CustomEvent) {
    this.setState({error: ev.detail, info: null});
    setTimeout(()=>this.setState({error: null}), 10000);
  }

  _onInfo(ev: CustomEvent) {
    this.setState({error: null, info: ev.detail });
    setTimeout(()=>this.setState({info: null}), 10000);
  } 

  render() {
    return <div className="error-success-feedback" style={{
      position: "fixed",
      left: 0,
      top: 0,
      width: "100%",
      pointerEvents: 'none'
    }}>
      <div style={{display: 'table', margin: "0 auto"}}>
        <p className="error" style={{display: this.state.error ? "" : "none"}}>{this.state.error && this.state.error}</p>
        <p className="info" style={{display: this.state.info ? "" : "none"}}>{this.state.info}</p>
      </div>
    </div>;
  }
}
