import {BearerProvider, RestClient, BearerTokenInfo} from './restclient';
import {IsomorphicEnv} from './isomorphic-env';
import {TokenQuery} from './token-query';
import {avoidSimultanousCalls} from './utils';

export interface BearerPersistentStorage {
    save(id: string, tokenInfo: BearerTokenInfo);
    load(id: string) : Promise<BearerTokenInfo>;
}

export class KedBearerProvider implements BearerProvider {
    tokenInfo: BearerTokenInfo;
    client: RestClient;
    
    constructor (
        public isomorphic: IsomorphicEnv,
        public storage: BearerPersistentStorage,
        public tokenId:string,
        public clientId: string,
        public clientSecret: string,
        public tokenUrl: string,
        public tokenQuery: TokenQuery)
    {
        this.tokenInfo = {token: null, expires: 0};
        this.client = new RestClient(isomorphic, "", {
            username: this.clientId,
            password: this.clientSecret
        });
        this.getBearer = avoidSimultanousCalls(this.getBearer.bind(this));
        this.refreshBearer = avoidSimultanousCalls(this.refreshBearer.bind(this));
    }

    async getBearer(): Promise<BearerTokenInfo> {
        const {token, expires} = this.tokenInfo;
        if (token && expires >= Date.now()) return this.tokenInfo;
        try {
            this.tokenInfo = await this.storage.load(this.clientId + "/" + this.tokenId);
            if (this.tokenInfo.token && this.tokenInfo.expires >= Date.now()) return this.tokenInfo;
            await this.refreshBearer();
            return this.tokenInfo;
        } catch (e) {
            await this.refreshBearer();
            return this.tokenInfo;
        }
    }

    async refreshBearer(): Promise<BearerTokenInfo> {
        let res: Response;
        for (let retries=0;retries<6;++retries) {
            console.log(`Retrieving token for ${this.tokenId}`);
            res = await this.client.get(this.tokenUrl, this.tokenQuery, {cache: 'reload'});
            if (res.status !== 200) {
                console.warn(`Got ${res.status} ${res.statusText}`);
                continue;
            }
            this.tokenInfo = {token: await res.text(), expires: Date.now() + 59 * 60 * 1000};
            console.log(`Got token for ${this.tokenId}: ${JSON.stringify(this.tokenInfo)}`);
            this.storage.save(this.clientId + "/" + this.tokenId, this.tokenInfo);
            return this.tokenInfo;
        }
        throw new Error("Failed to retrieve token for " + JSON.stringify(this.tokenId));
    }
}
