import * as model from '../../KED.Backend.API/schema';
import migrateCourse from './migrate-course';
import migrateTask from './migrate-task';

export default function migrate(doc, table: string, graphs?: string[]) {
  switch (table) {
    case "courses":
      migrateCourse(doc, graphs);
      break;
    case "tasks":
      migrateTask(doc);
      break;
  }
}
