import {BearerPersistentStorage, BearerTokenInfo} from '../ked-backend-client';

export class BearerStorageSessionStorage implements BearerPersistentStorage {
    save(id: string, tokenInfo: BearerTokenInfo) {
        sessionStorage.setItem("bearer-" + id, JSON.stringify(tokenInfo));
    }

    load(id: string): Promise<BearerTokenInfo> {
        try {
            const json = sessionStorage.getItem("bearer-" + id);
            return Promise.resolve(json ? JSON.parse(json) : {token: null, expires: 0});
        } catch (ex) {
            return Promise.resolve({token: null, expires: 0});
        }
    }
}
