import * as React from 'react';
import { School } from 'kedbackend/models';
import { DialogContainer } from "../interfaces/course-editor-host";
import { L } from "../../../utils/utils";
import env from '../../../globals/KED.env';
import { updateModificationAndCreationStamps } from "../utils";
import { EditSchool } from './edit-school';
import { Link } from 'react-router-dom';
import { actAs } from "../../../access-control";
import { Spinner } from "../sub-components/spinner";

interface Props {
  schools?: School[]
  viewCourseUrl: string
}

export const EditableSchoolList = (props: Props) => {
  const { schools, viewCourseUrl } = props;
  const isAdmin = env.currentUser.roles.some(role => role === "ADMIN");
  return <div className="editable-school-list">
    <div>
      {schools ? <table>
        <tbody>
          {schools.map(school =>
            <tr className="align-horizontal" key={school.id}>
              <td>
                <Link className="editItem" to={`/schools/${school.id}/edit`}></Link>
              </td>
              <td>
                <p>{school.name}</p>
              </td>
              <td>
                <p><a style={{cursor: 'pointer'}} onClick={()=>actAs({role: "EMPLOYEE", school: school.name})}>Agera som lrare p denna skola</a></p>
              </td>
              <td>
                <p><a style={{cursor: 'pointer'}} onClick={()=>actAs({role: "STUDENT", school: school.name, url: viewCourseUrl})}>Agera som elev p denna skola</a></p>
              </td>
            </tr>)}
        </tbody>
      </table> : <p><Spinner />V.g. vnta medan skolor laddas</p>}
    </div>
    <br />
    {schools && isAdmin && <Link to="/schools/new" className="btn">Lgg till skola</Link>}
  </div >
}
