import * as React from 'react';
import { L } from '../../../utils/utils';
import { SchoolCourses } from '../sub-components/school-courses';
import { Resource } from 'kedbackend/models';
import { dtFormat } from '../utils';
import { Dialog } from '../interfaces/course-editor-host';
import validUrl from 'valid-url';

interface Props {
  resource?: Resource;
  title: string;
  onSave: (newResource: Resource) => void;
  onDelete?: ()=>void;
}

type State = Resource;

export class EditResource extends React.Component<Props,State> implements Dialog {
  constructor(props: Props) {
    super(props);
    this.state = this.props.resource || {name: '', url: ''};
  }

  save() {
    const resource = this.state;
    if (!validUrl.isUri(resource.url)) throw new Error(L`Angiven URL '${resource.url}' r ogiltig. Ska brja med exempelvis http:, https: eller mailto:`);
    this.props.onSave(resource);
  }

  render() {
    const {title, onSave, onDelete} = this.props;
    const resource = this.state;
    const isValidUrl = validUrl.isUri(this.state.url);

    return <div><div className="sv-html-portlet sv-portlet sv-skip-spacer">
      <h2>{title}</h2>
      <hr />
      <div className="align-horizontal">
        <div className="horizontalItem top">Namn:</div>
        <div className="horizontalItem top">
          <input type="text" autoFocus
            tabIndex={1}
            size={50}
            value={resource.name}
            onChange={ev=>this.setState({name: ev.target.value})} />
        </div>
        <div className="horizontalItem top spaced"></div>
      </div>
      <div className="align-horizontal">
        <div className="horizontalItem top">Lnk:</div>
        <div className="horizontalItem top ">
          <input type="text"
            tabIndex={1}
            size={50}
            value={resource.url}
            onChange={ev=>this.setState({url: ev.target.value})} />
        </div>
        <div className="horizontalItem top spaced"></div>
      </div>
      <br /></div>
      <div className="sv-html-portlet sv-portlet">
        {(resource.createdDate || resource.modifiedDate) && <div>
          {resource.createdDate && <p>
            Resursen/Lnken skapades {dtFormat(resource.createdDate)} av <strong>{resource.createdBy.name}</strong>.
          </p>}
          {resource.modifiedDate && <p>
            Resursen/Lnken redigerades senast {dtFormat(resource.modifiedDate)} av <strong>{resource.modifiedBy.name}</strong>.
          </p>}
          <br /><br/>
        </div>}
        {onDelete && <div tabIndex={2} className="btn btn-warning btn-large pull-right" onClick={()=>onDelete()}>
          Ta bort resurs/lnk
        </div>}
        <a tabIndex={1} className={"btn btn-large" + (isValidUrl ? "" : " btn-inactive")} onClick={isValidUrl && (() => onSave(resource))}>Spara</a>
      </div>
    </div>
  }
}
