import moment from 'moment';
import { L } from '../../utils/utils';
import { Document, Resource, ModificationTracked, Course, Task } from 'kedbackend/models';
import { BatchRunner, createUUID } from 'kedbackend/client';
import update from 'react-addons-update';
import { User } from '../../contracts/ked-models';
import $ from 'jquery';
import env from '../../globals/KED.env';

export function updateDocumentGraphs(oldDoc, newDoc, table: string, graphs: { [label: string]: string }, batch: BatchRunner) {
  const docUpdates = {};
  const docId = newDoc.id;
  for (let navProp of Object.keys(graphs)) {
    const foreignTable = graphs[navProp];
    const oldList: Document[] = oldDoc[navProp] || [];
    const newList: Document[] = newDoc[navProp];
    if (!newList) continue;
    const tuples = newList
      .map((doc, idx)=>({doc, idx})); // Create tubles of {doc, array-index} so we can update result
    const added = tuples.filter(tuple => !oldList.some(o => o.id === tuple.doc.id)); // Find added items

    for (let a of added) {
      const mutatedSubDoc = {...a.doc};
      const meta = mutatedSubDoc.$meta;
      delete mutatedSubDoc.$meta; // Delete $meta so that "add" or "update" does not persiste to next state.
      if (meta === 'add') {
        if (!mutatedSubDoc.id) mutatedSubDoc.id = createUUID(); // Generate ID if not done yet.
        // Now put an 'add' mutation in the batch queue.
        batch.add(foreignTable, mutatedSubDoc);
      } else if (meta === 'update') {
        batch.put(foreignTable, mutatedSubDoc);
      }
      batch.link(table, docId, foreignTable, mutatedSubDoc.id, navProp);

      // Update result so that state is reflected after succesful POST to server.
      if (!docUpdates[navProp]) docUpdates[navProp] = {};
      docUpdates[navProp][a.idx] = {$set: mutatedSubDoc};
    }
    const removed = oldList.filter(o => !newList.some(n => n.id === o.id));
    for (let r of removed) {
      batch.unlink(table, docId, foreignTable, r.id, navProp);
    }
    const updated = tuples.filter(tuple => oldList.some(o => o.id === tuple.doc.id && tuple.doc.$meta === 'update'));
    for (let u of updated) {
      const mutatedSubDoc = {...u.doc};
      delete mutatedSubDoc.$meta;
      batch.put(foreignTable, mutatedSubDoc);

      // Update result so that $meta is removed from navigation prop after successful POST to server.
      if (!docUpdates[navProp]) docUpdates[navProp] = {};
      docUpdates[navProp][u.idx] = {$set: mutatedSubDoc};
    }
  }
  return update(newDoc, docUpdates);
}

export function dtFormat(dateTime: number | Date | moment.Moment) {
  return moment(dateTime).format('YYMMDD HH:mm');
}

export function shortDateFormat(dateTime: number | Date | moment.Moment) {
  return moment(dateTime).format('YYMMDD');
}

export function updateModificationStamp (now: number, obj: ModificationTracked, user: User) {
  return update(obj, {
    dateTime: {$set: now}, // For queries...
    modifiedDate: {$set: now},
    modifiedBy: {
      $set: {
        name: user.displayName,
        url: "mailto:" + user.mail
      }
    }
  });
}

export function updateCreationStamp (now: number, obj: ModificationTracked, user: User) {
  return update(obj, {
    createdDate: {$set: Date.now()},
    createdBy: {
      $set: {
        name: user.displayName,
        url: "mailto:" + user.mail
      }
    }
  });
}

export function updateModificationAndCreationStamps(obj: ModificationTracked, user: User) {
  const now = Date.now();
  obj = updateModificationStamp(now, obj, user);
  if (!obj.createdBy) obj = updateCreationStamp(now, obj, user);
  return obj;
}

export function applyEtags(doc: Document, newEtags: {[id: string]: string}, graphs: string[]) {
  const res = {...doc};
  const etag = newEtags[doc.id];
  if (etag)
    res.$etag = etag;
  for (let label of graphs) {
    const newList = doc[label].map(d => applyEtags(d, newEtags, []));
    res[label] = newList;
  }
  return res;
}

export function readBlob(blob: Blob, m: (r:FileReader)=>void) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = ev => resolve((ev.target as any).result);
    reader.onabort = ev => reject(new Error("file read aborted"));
    reader.onerror = ev => reject((ev.target as any).error);
    m(reader);
  });
}

export function readBlobAsText (blob: Blob) {
  return readBlob(blob, r=>r.readAsText(blob));
}

export function readBlobAsDataUrl (blob: Blob) {
  return readBlob(blob, r=>r.readAsDataURL(blob));
}

export function allowCopy(e: React.DragEvent<HTMLDivElement>) {
  e.stopPropagation();
  e.preventDefault();
  e.dataTransfer.dropEffect = 'copy';
}

export function updateCourseBuilderStatus(status: 'unsaved' | 'saved' | 'error' | '') {
  const div = $('div.course-builder')[0];
  if (div) div.className = "course-builder" + (status ? " status " + status : "");
}

export async function loadCourse(id: string, options?: {
    includeTemplateChain?: boolean
  }): Promise<Course>
{
  const client = env.kedBackendClient;
  const includeTemplateChain = options && options.includeTemplateChain;
  const [course, courseTasks, templateChain] = (await Promise.all([
    client.get("courses", id, {
      include: [
        "centralContent",
        "knowledgeRequirements",
        "abilities",
        "images",
        "acl" // Don't include tasks here...
      ]
    }),
    // ... but include tasks here instead so that we can load the graphs for the
    // tasks as well!
    client.list("tasks", {
      hasEdgesFrom: id,
      include: ['knowledgeRequirements', 'centralContent', 'abilities', 'acl'],
      flags: ['includeIdsOnly'] // Don't need redundant info that's already on course
    }),
    includeTemplateChain && client.list("courses", {
      hasEdgesFrom: id,
      flags: ['idsOnly']
    })
  ])) as [Course, Task[], Course[]];
  course.tasks = courseTasks;

  // Correct the order of KnowledgeRequirements
  if (course.knowledgeRequirementsOrder) {
    course.knowledgeRequirements = course.knowledgeRequirementsOrder.map(id =>
      course.knowledgeRequirements.find(c => c.id === id));
  }

  // Correct the order of CentralContent
  if (course.centralContentOrder) {
    course.centralContent = course.centralContentOrder.map(id =>
      course.centralContent.find(cc => cc.id === id));
  }
  
  // Include template chain if requested.
  if (includeTemplateChain) course.templateChain = templateChain;
  return course;
}

