import * as React from 'react';
import { Course, CourseModule, Task, Document } from 'kedbackend/models';
import { OpenCloseBox } from "../../utility-components/open-close-box";

interface Props {
  options: Document[];
  title;
  markMode: boolean;
  markedIds: string[];
  onMarkChanged: (id: string, isMarked: boolean) => void;
}

interface State {
}

export class SelectRelatedDocs extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {};
  }

  render() {
    const {options, title, markedIds, markMode} = this.props;
    return <OpenCloseBox title={<p>{title}</p>} className="larger">
        {options.map(option => {
          const isMarked = markedIds.some(x => x === option.id);
          return <div className="align-horizontal" key={option.id} onClick={()=>
            markMode && this.props.onMarkChanged(option.id, !isMarked)}>
            <div className={"horizontalItem top spaced" +
              (markMode ? " selectable" : "") +
              (isMarked ? " markedGreen" : "")}>
              <p dangerouslySetInnerHTML={{__html: option.name}} />
              <br />
            </div>
          </div>;
        })}
    </OpenCloseBox>
  }
}