

export interface RepoCommunicator<T extends {id: string}> {
  query(): Promise<T[]>;
}

type Subscriber<T> = (items:T[]) => any;

export class Repo<T extends {id: string}> {
  listPromise: Promise<void>;
  items: T[];
  subscribers: Subscriber<T>[];

  constructor(public comm: RepoCommunicator<T>) {
    this.listPromise = null;
    this.items = null;
    this.subscribers = [];
  }

  subscribe(subscriber: Subscriber<T>) {
    return this.ensureHasData().then(()=>{
      subscriber(this.items);
      this.subscribers.push(subscriber);
    });
  }

  unsubscribe(subscriber: Subscriber<T>) {
    this.subscribers = this.subscribers.filter (s => s !== subscriber);
  }

  notifySubscribers() {
    this.subscribers.forEach(s => s(this.items));
  }

  ensureHasData() {
    if (!this.listPromise) this.refreshFromServer();
    return this.listPromise;
  }

  refreshFromServer() {
    this.listPromise = this.comm.query().then(items => {
      this.items = items;
      this.notifySubscribers();
    });
    return this.listPromise;
  }
  
  async update(item: T | T[]) {
    await this.ensureHasData();
    const items = Array.isArray(item) ? item : [item];
    this.items = this.items.map(it => {
      const item = items.find(({id}) => it.id === id);
      return item ?
        Object.assign({}, item) :
        it;
    });
    this.notifySubscribers();
  }

  insert(item: T | T[]) {
    return this.ensureHasData().then(()=>{
      this.items = this.items.concat(item);
      this.notifySubscribers();
    });
  }

  delete(id: string | string[]) {
    const ids = Array.isArray(id) ? id : [id];
    return this.ensureHasData().then(()=>{
      this.items = this.items.filter(it => !ids.some(id => it.id === id));
      this.notifySubscribers();
    });
  }
}
