import * as React from 'react';
import { L } from '../../../utils/utils';
import { Task, Course, CourseModule, Ability } from 'kedbackend/models';
import { Link } from 'react-router-dom';
import { EditTask } from '../modal-pages/edit-task';
import { ICourseEditorHost } from '../interfaces/course-editor-host';
import { updateModificationAndCreationStamps } from "../utils";
import env from '../../../globals/KED.env';
import { hasWriteAccess } from "../../../access-control";

interface Props {
  taskIds: string[];
  taskCustomizations?: { [taskId: string]: Partial<Task> }
  course: Course;
  module: CourseModule;
  idsNotCoveredByAnyTask: {[id: string]: true};
  onTaskAdded: (task: Task, taskCustomization?: Partial<Task>) => void;
  onTaskUpdated: (task) => void;
  onTaskCustomizationUpdated: (taskId, customization) => void;
  onTaskDeleted: (id) => void;
  onTaskReplaced: (id, task) => void;
  host: ICourseEditorHost;
}

interface State {

}

export class EditableTaskList extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {};
  }

  openTaskDialog(task: Task) {
    const {
      taskCustomizations,
      course,
      host,
      module,
      taskIds,
      onTaskAdded,
      onTaskUpdated,
      onTaskCustomizationUpdated,
      onTaskDeleted,
      idsNotCoveredByAnyTask,
      onTaskReplaced
    } = this.props;

    host.openDialog({
      Component: EditTask,
      props: {
        task,
        taskCustomization: taskCustomizations && taskCustomizations[task.id],
        module,
        course,
        host,
        idsNotCoveredByAnyTask,
        onSave: task => {
          const updatedTask = updateModificationAndCreationStamps(task, env.currentUser);
          onTaskUpdated(updatedTask);
          host.closeDialog();
        },
        onSaveCustomization: customization => {
          onTaskCustomizationUpdated(task.id, customization);
          host.closeDialog();
        },
        onDelete: () => {
          onTaskDeleted(task.id);
          host.closeDialog();
        },
        onReplace: (taskId: string, newTask: Task) => {
          onTaskReplaced(taskId, newTask);
          host.closeDialog();
        },
        onCancel: () => host.closeDialog()
      }
    });
  }

  render() {
    const props = this.props;
    return <div>
      <div className="taskContainer">
        {props.taskIds.map(id => props.course.tasks.find(t => t.id === id)).map(task => {
          const canUpdateTask = hasWriteAccess(task);

          return <div className="align-horizontal" key={task.id}>
            <div className="horizontalItem top">
              <div className={canUpdateTask ? "editItem" : "viewItem"} onClick={() =>
                this.openTaskDialog(task)} />
            </div>
            <div className="horizontalItem top">
              {task.content ?
                // Rather than linking to url, show the task's content (by opening
                // the task dialog)
                <a href="#" onClick={ev=>{
                  ev.preventDefault();
                  this.openTaskDialog(task);
                }}>{task.name}</a> :
                <a href={task.url} target="_blank">{task.name}</a>}
            </div>
          </div>;
        })}
      </div>
      <br />
      <a className="btn" onClick={() => {
        const {
          course,
          host,
          idsNotCoveredByAnyTask,
          module,
          onTaskAdded,
          onTaskCustomizationUpdated } = this.props;

        props.host.openDialog({
          Component: EditTask,
          props: {
            searchMode: true,
            module,
            course,
            host,
            idsNotCoveredByAnyTask,
            onSave: (choosenTask, taskCustomization) => {
              choosenTask = updateModificationAndCreationStamps(choosenTask, env.currentUser);
              onTaskAdded(choosenTask, taskCustomization);
              host.closeDialog();
            },
            onCancel: () => host.closeDialog()
          }
        });
      }}><i className="fa fa-search" aria-hidden /> Sk uppgift</a>
      &nbsp;
      <a onClick={() => {
        const { course, host, module, taskIds, onTaskAdded, onTaskUpdated, onTaskDeleted, idsNotCoveredByAnyTask } = this.props;
        host.openDialog({
          Component: EditTask,
          props: {
            module,
            course,
            host,
            idsNotCoveredByAnyTask,
            onSave: newTask => {
              newTask = updateModificationAndCreationStamps(newTask, env.currentUser);
              onTaskAdded(newTask);
              host.closeDialog();
            },
            onCancel: () => host.closeDialog()
          }
        });
      }} className="btn">
        <i className="fa fa-plus" aria-hidden /> Lgg till egen uppgift
      </a>
    </div >
  }
}