import './configure'; // Emulate server-side script
import '../global-setters/set-all'; // Client-side initialization
// </Initialization>

import env from '../globals/KED.env';
import * as React from 'react';
import { CourseBuilder } from '../components/course-builder';
import { ChooseUser } from './utils/choose-user';

export default class App extends React.Component<{}> {
  constructor(props: any) {
    super(props);
    if (!location.hash) location.hash = "#/";
  }

  render() {
    return env.currentUser ?
      <CourseBuilder
        viewCourseUrl="courseviewer.html"
        feedbackUrl="http://svt.se/nyheter" /> :
      <ChooseUser />
  }
}
