import { Repo, RepoCommunicator } from './repo';
import { Course, School } from 'kedbackend/models';
import env from '../globals/KED.env';
import { flatten, L } from "../utils/utils";

class SchoolCoursesRepo extends Repo<Course> {
  private _schoolId: string;
  getSchoolId() {
    return this.ensureHasData().then(()=>this._schoolId);
  }

  constructor(getSchoolName: ()=>string) {
    super({
      query: async () => {
        const schoolName = getSchoolName();
        const schools = await env.kedBackendClient.list<School>(
          "schools", {
            name: schoolName,
            include: ["courses"]
          });
        this._schoolId = schools.length > 0 && schools[0].id;
        if (!this._schoolId) throw new Error(L`Skolan ${schoolName} finns inte registrerad i systemet.
          Kontakta en administratr fr Kursbyggarverktyget och be om att lgga till skolan med namnet "${schoolName}"`);
        return flatten(schools.map(school => school.courses));
      }
    })
  }
}

export const mySchoolCoursesRepo = new SchoolCoursesRepo(()=>env.currentUser.school);
