import moment from 'moment';
import { Term, AcademicYear } from '../contracts/ked-models';

export interface SchoolMoment {
  academicYear: AcademicYear;
  term: Term;
  week?: number;
}

export function getFirstAndLastWeekOfTerm(term: Term) {
  return term === 'AT' ?
    [32, 51] :
    [1, 25];
}

export function getTermStartAndEnd(now: moment.Moment) : moment.Moment[] {
  return now.month() >= 7 ? // 7 = August in JS Dates and in moment as well!
    [moment(new Date(now.year(), 7, 1)), moment(new Date(now.year(), 11, 31))] : // aug1 - dec31
    [moment(new Date(now.year(), 0, 1)), moment(new Date(now.year(), 6, 31))]; // jan1 - july31
}

export function getSchoolMoment(m: moment.Moment): SchoolMoment {
  const thisYear = m.year();
  const isAutumn = m.month() >= 7;
  const [autumnYear, springYear] = isAutumn ?
    [thisYear, thisYear + 1] :
    [thisYear - 1, thisYear];
  const academicYear = '' + autumnYear + '/' + springYear;
  const term = isAutumn ? 'AT' : 'ST';
  const week = m.week();
  return { academicYear, term, week };
}

export function addYear(aYear: AcademicYear, numYearsToAdd: number): AcademicYear {
  return aYear.split('/')
    .map(yearStr => parseInt(yearStr) + numYearsToAdd)
    .map(year => '' + year)
    .join('/');
}

export function nextAcademicYear(aYear: AcademicYear): AcademicYear {
  return addYear(aYear, 1);
}

export function prevAcademicYear(aYear: AcademicYear): AcademicYear {
  return addYear(aYear, -1);
}
