export interface ParseQueryStringOptions {
  toLower?: boolean;
}

export function parseQueryString(locationSearch: string, options?: ParseQueryStringOptions): { [key: string]: string } {
  const {toLower} = (options ||{}) as ParseQueryStringOptions;
  const result = {};
  if (locationSearch && locationSearch.length > 1)
    locationSearch.substr(1)
      .split('&')
      .map(part => part.split('=').map(s => decodeURIComponent(s.trim())))
      .forEach(([key, value]) => result[toLower ? key.toLowerCase() : key] = value);
  return result;
}

function encodeParams(params: {[key: string]:string}) {
  return Object.keys(params).map(key => `${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`).join('&');
}

export function generateQueryString(params: {[key: string]:string}) {
  return "?" + encodeParams(params);
}

export function parseHashQueryString(locationHash: string, options?: ParseQueryStringOptions) {
  return parseQueryString(locationHash, options);
}

export function generateHashQueryString(params: {[key: string]:string}) {
  return "#" + encodeParams(params);
}

export function splitUrlAndQuery (urlWithPossibleQuery: string) {
  const pQuery = urlWithPossibleQuery.indexOf('?');
  return pQuery >= 0 ?
    [urlWithPossibleQuery.substr(0, pQuery), urlWithPossibleQuery.substr(pQuery)] :
    [urlWithPossibleQuery, ""];
}
