
export function capitalizeFirst (str) {
    for (let i=0, l=str.length; i<l; ++i) {
        if (str.charCodeAt(i) < 0x2000) {
            return str.substr(0, i) + str[i].toLocaleUpperCase() + str.substr(i + 1);
        }
    }
    return str;
}

export function extend(obj, extension) {
    if (typeof extension !== 'object') return obj;
    Object.keys(extension).forEach(key => {
        obj[key] = extension[key];
    });
    return obj;
}

export function clone<T,E> (obj : T, extension?: E) {
    let clone = {};
    Object.getOwnPropertyNames(obj).forEach(key => {
        Object.defineProperty(clone, key, Object.getOwnPropertyDescriptor(obj, key));
    });
    if (extension) extend(clone, extension);
    return clone as (T & E);
}

const concat = [].concat;
export function flatten<T> (a : (T | T[])[]) {
    return concat.apply([], a) as T[];
}

export function compareProp(prop:string) {
    return (a,b) => {
        const aProp = a[prop], bProp = b[prop];
        return aProp > bProp ? 1 : aProp < bProp ? -1 : 0;
    }
}

export function compareProps(props: string | string[], locales?: string | string[], options?: Intl.CollatorOptions) {
    props = Array.isArray(props) ? props : [props];

    const localeCompare = (a,b) =>
        typeof a === 'string' ?
            a.localeCompare(b, locales, options) :
            a < b ? -1 : a > b ? 1 : 0;

    return props
        .map(prop => (a,b) => localeCompare(a[prop], b[prop]))
        .reduce((cmp1, cmp2) =>
            (a,b) => cmp1(a,b) || cmp2(a,b));
}

export function L(text, ...args) {
    let first = text[0];
    return buildMessage(text, args);
}

function buildMessage (text, args) {
    let rv = text[0];
    for (let i=1, l=text.length; i<l; ++i) {
        rv += args[i-1] + text[i];
    }
    return rv;
}

export class TC<T> {
    constructor(template: T) {
        extend(this, template);
    }
}

export function dateTimeReviver (key, value) {
    var a;
    if (typeof value === 'string') {
        a = /\/Date\((\d*)\)\//.exec(value);
        if (a) {
            return new Date(+a[1]);
        }
    }
    return value;
}

export function showInfo (msg) {
    var event = new CustomEvent('info', { 'detail': msg });
    window.dispatchEvent(event);
}

export function showError (errMsg: string | Error) {
    const msg = typeof errMsg === 'string' ? errMsg : errMsg.message;
    var event = new CustomEvent('customerror', { 'detail': msg });
    console.error(errMsg);
    window.dispatchEvent(event);
}

export function maxLength(str: string, maxLen: number) {
    return str.length > maxLen ?
        str.substr(0, maxLen - 3) + "..." :
        str;
}

export function arrayToLookup<T>(a: T[], keyAccessor: (item: T) => string | number) {
    const result : {[key: string] : T[]} = {};
    for (let i=0, l=a.length; i<l; ++i) {
        const item = a[i];
        const key = keyAccessor(item);
        let array = result[key];
        if (array) array.push(item);
        else result[key] = [item];
    }
    return result;
}

export function arrayToMap<T>(a: T[], keyAccessor: (item: T) => string | number) {
    const result : {[key: string] : T} = {};
    for (let i=0, l=a.length; i<l; ++i) {
        const item = a[i];
        const key = keyAccessor(item);
        result[key] = item;
    }
    return result;
}

export function cherryPickProps<T> (obj: T, propsToPick: string[]): Partial<T> {
    const result = {};
    for (const param of propsToPick) {
      if (param in obj) result[param] = obj[param];
    }
    return result as Partial<T>;
}

export function distinct<T> (a: T[], keyAccessor: (item: T) => any) {
    const map = arrayToMap(a, keyAccessor);
    return Object.keys(map).map(key => map[key]);
}
