import * as React from 'react';
import { users } from '../data/users';

export function ChooseUser() {
  let select;
  return <div className="sv-layout">
    <h2>Vlj anvndare</h2>
    <table className="login-table"><thead><tr>
      <th>Namn</th>
      <th>Roller</th>
      <th>E-post</th>
      <th>Skola</th>
    </tr></thead>
      <tbody>
        {users.map(({ username, displayName, mail, school, roles }) =>
          <tr key={mail} onClick={() => location.search = `?user=${username}`}>
            <td>{displayName}</td>
            <td style={roles.length === 0 ? { fontStyle: 'italic' } : {}}>{roles.length === 0 ? "saknar roller" : roles.join(', ')}</td>
            <td>{mail}</td>
            <td style={school ? {} : { fontStyle: 'italic' }}>{school || "saknar skola"}</td>
          </tr>
        )}
      </tbody></table>
  </div>
};
