import { User } from "../contracts/ked-models";

export default function getUserClaims (user: User) {
  return [{
    type: "email",
    value: user.mail
  },{
    type: "school",
    value: user.school
  }].concat(user.roles.map(role => ({
    type: "role",
    value: role
  }))).concat(user.roles.map(role => ({
    type: "schoolRole",
    value: user.school + "/" + role
  })));
}
