import $ from 'jquery';

const parser = new DOMParser();

type HTMLPolicy = {[tagNameLower: string]: {[attributeLower: string]: boolean}};

/** Tags / Attributes Whitelist
 * 
 */
const HTML_WASH_POLICY: HTMLPolicy = {
  b: {},  // bold
  i: {},  // italic
  p: {},  // paragraph
  u: {},  // underline
  strike: {}, // strike-through
  pre: {}, // unformatted
  h1: {}, // heading 1
  h2: {}, // heading 2
  h3: {}, // heading 3
  h4: {}, // heading 4
  h5: {}, // heading 5
  img: {src: true, class: true, style: true, tabindex: true}, // image. We set style and tabindex ourselves. Accept that.
  a: {href: true, target: true}, // link
  ul: {}, // Unordered list
  ol: {}, // Ordered list
  li: {}, // list item
  hr: {}, // Horizontal rule
  br: {}, // Break line
  div: {},// Of some reason, divs are sometimes inserted when pressing ENTER in the wysiwyg editor.
  span: {},// Spans are harmless, just remove their style and other attributes and I feel great.
  // table tags:
  table: {border: true},
  tbody: {},
  thead: {},
  tfoot: {},
  tr: {},
  td: {headers: true, colspan: true, rowspan: true},
  th: {abbr: true, headers: true, scope: true, sorted: true, colspan: true, rowspan: true}
};

export function washHtml (html: string): string {
  const doc = parser.parseFromString(html, "text/html");
  const {childNodes} = doc.body;
  let modified = false;
  for (let i=0; i<childNodes.length; ++i) {
    if (washNode(childNodes.item(i))) {
      modified = true;
    }
  }
  return modified ?
    doc.body.innerHTML :
    html; // By returning the original HTML string, we spare the user from refreshing the edit area,
          // which would otherwise put the cursor at the top, losing the position where user where.
}

function washNode(node: Node | Element | HTMLElement): boolean {
  let modified = false;
  if (isElement(node)) {
    if (washElement(node)) {
      modified = true;
    }
  }
  if (washChildNodes(node)) {
    modified = true;
  }
  return modified;
}

function washChildNodes(node: Node) {
  let modified = false;
  const {childNodes} = node;
  for (let i=0; i<childNodes.length; ++i) {
    if (washNode(childNodes.item(i))) {
      modified = true;
    }
  }
  return modified;
} 

/** Replace an element with its child nodes.
 * 
 */
function removeMiddleElement(element: Element) {
  const {childNodes} = element;
  for (let i=0; i<childNodes.length; ++i) {
    element.parentNode.insertBefore(childNodes.item(i), element);
  }
  element.remove();
}

function washElement(element: Element): boolean {
  const policy = element.tagName && HTML_WASH_POLICY[element.tagName.toLowerCase()];
  if (!policy) {
    console.warn("Wysiwyg: not allowed tag", element.tagName);
    washChildNodes(element);
    removeMiddleElement(element);
    return true;
  }
  let modified = false;
  for (let i=0; i<element.attributes.length; ++i) {
    const attr = element.attributes.item(i);
    const allowed = attr.name && !!policy[attr.name.toLowerCase()];
    if (!allowed) {
      modified = true;
      console.warn("Wysiwyg: not allowed attribute", attr.name);
      element.removeAttribute(attr.name);
    }
  }
  return modified;
}

function isElement(node: Node) : node is Element {
  return !!(node as Element).tagName;
}
