
export function createUUID() {
  // Decent solution from http://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid-in-javascript
  var d = Date.now();
  var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    var r = (d + Math.random() * 16) % 16 | 0;
    d = Math.floor(d / 16);
    return (c === 'x' ? r : (r & 0x7 | 0x8)).toString(16);
  });
  return uuid;
}

export function avoidSimultanousCalls<T,A>(method: (...args:A[])=>Promise<T>) : (...args:A[])=>Promise<T> {
  let ongoingPromise: Promise<T> = null;
  return function() {
      if (!ongoingPromise) {
          ongoingPromise = method.apply(this, arguments).then(result=>{
              ongoingPromise = null;
              return result;
          });
      }
      return ongoingPromise;
  }
}
