/*
declare var Buffer; // node built-in


function basicAuthHeader(username, password) {
    return "Basic " + new Buffer(username + ":" + password).toString("base64");
}
*/

import {IsomorphicEnv} from './isomorphic-env';
import { createUUID } from "./utils";

export interface BearerTokenInfo {
    token: string;
    expires: number;
}

export interface BearerProvider {
    getBearer(): Promise<BearerTokenInfo>;
    refreshBearer(): Promise<BearerTokenInfo>;
    wantsRedirect?: boolean;
}

export class RestClient {
    authHeader?: string;
    tokenInfo?: BearerTokenInfo;
    bearerProvider?: BearerProvider;
    fetchOptions: RequestInit;

    constructor(
        public isomorphic: IsomorphicEnv,
        public baseUrl: string,
        public options: {
            username?,
            password?,
            bearer?,
            bearerProvider?: BearerProvider
        },
    ) {
        this.fetchOptions = {mode: 'cors'};
        this.authHeader = options.bearer ?
            "Bearer " + options.bearer :
            options.username ?
                "Basic " + isomorphic.btoa(options.username + ":" + (options.password || "")) :
                null;
        this.bearerProvider = options.bearerProvider || null;
    }

    async fetch(path: string, method: string, headers, query?, fetchOptions?: RequestInit): Promise<Response> {
        if (fetchOptions && fetchOptions.cache === 'no-cache') {
            // Workaround for back-button not respecting cache control in Chrome/Opera.
            // Append a query parameter to force a cache entry
            query = {...query, nocache: createUUID()}
        }
        const queryStr = query && Object.keys(query).map(key =>
            encodeURIComponent(key) +
            "=" +
            encodeURIComponent(query[key]))
            .join('&');

        let {authHeader, tokenInfo, bearerProvider} = this;

        // {username, password or bearer} not specified in options, but bearerProvider specified:
        if (!authHeader && !tokenInfo && bearerProvider) {
            this.tokenInfo = tokenInfo = await bearerProvider.getBearer();
        }
        // Either because we've just specified bearerProvider in options, OR, we've got a 401
        // in earlier response with www-authenticate: Bearer, and we had a BearerProvider,
        // so we saved the tokenInfo. Now use it regardless of whether username,password or bearer was given
        // to options in constructor:
        if (tokenInfo) {
            if (tokenInfo.expires < Date.now()) {
                console.log("Token expired. Refresh it:");
                this.tokenInfo = tokenInfo = await bearerProvider.refreshBearer();
            }
            authHeader = "Bearer " + tokenInfo.token;            
        }

        // In one way or another, we've concluded an Authorization header to use:
        if (authHeader) {
            headers.Authorization = authHeader;
        }

        const url = this.baseUrl + path + (queryStr ? "?" + queryStr : "");
        let res: Response = await this.isomorphic.fetch(url, { ...this.fetchOptions, headers, method, ...fetchOptions });
        if (res.status == 401 && this.bearerProvider) {
            const wwwauth = res.headers.get("www-authenticate");
            console.log(`Got ${res.status} from ${this.baseUrl + path}`);
            if (wwwauth && /Bearer/i.test(wwwauth)) {
                this.tokenInfo = await this.bearerProvider.refreshBearer();
                headers.Authorization = "Bearer " + this.tokenInfo.token;
                res = await this.isomorphic.fetch(url, { ...this.fetchOptions, headers, method, ...fetchOptions });
            }
        }
        return res;
    }

    get(path: string, query?, fetchOptions?: RequestInit) {
        return this.fetch(path, "GET", { Accept: "application/json; text/plain" }, query, fetchOptions);
    }

    post(path: string, data, fetchOptions?: RequestInit): Promise<Response> {
        return this.fetch(path, "POST", {
            "Content-Type": "application/json",
            "Accept": "application/json"
        }, null, {...fetchOptions, body: JSON.stringify(data)});
    }

    delete(path: string, query?, body?, fetchOptions?: RequestInit) {
        return this.fetch(path, "DELETE", { Accept: "application/json; text/plain" }, query, {...fetchOptions, body});
    }
}
