import * as React from 'react';
import { L } from '../../utils/utils';
import { HashRouter as Router, Route, Link, Redirect } from 'react-router-dom';
import { ErrorSuccessFeedback } from '../../utils/error-success-feedback';
import { Schools } from './schools';
import { NewSchool } from './schools/new-school';
import env from '../../globals/KED.env';

// Views
import { Subjects, ShowSubject } from './subjects';
import { Courses, EditCourse } from './courses';
import { EditSchool } from "./schools/edit-school";
import { CourseBanner } from "./courses/course-banner";
import { CreateNewCourse } from "./courses/create-new";
import { Spinner } from "./sub-components/spinner";

// Keep Session Alive
import { keepSessionAlive } from '../../utils/keep-session-alive';
keepSessionAlive();

interface Props {
  viewCourseUrl: string;
  feedbackUrl: string;
}

interface State {
}

export class CourseBuilder extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
  }

  render() {
    const isAuthorized = env.currentUser.roles.some(role => role === "ADMIN" || role === "EMPLOYEE");
    const isAdmin = env.currentUser.roles.some(role => role === "ADMIN");
    return <div className="course-builder">
      <Router>
        {isAuthorized ? <div>
          <Route exact path="/" render={({ match }) => isAdmin ?
            <Schools viewCourseUrl={this.props.viewCourseUrl} feedbackUrl={this.props.feedbackUrl} /> :
            <CreateNewCourse feedbackUrl={this.props.feedbackUrl} />} />
          <Route exact path="/schools" render={({ match }) =>
            <Schools
              viewCourseUrl={this.props.viewCourseUrl}
              feedbackUrl={this.props.feedbackUrl} />} />
          <Route exact path="/schools/new" render={()=><NewSchool feedbackUrl={this.props.feedbackUrl} />} />
          <Route exact path="/schools/:id/edit" render={({ match }) => <EditSchool
            id={match.params.id}
            title={L`Editera skola`}
            feedbackUrl={this.props.feedbackUrl} />} />
          <Route exact path="/subjects" render={()=><Subjects feedbackUrl={this.props.feedbackUrl} />} />
          <Route path="/subjects/:id" render={({ match }) => <ShowSubject
            id={match.params.id}
            feedbackUrl={this.props.feedbackUrl} />} />
          <Route exact path="/courses/new" render={()=> <CreateNewCourse
          feedbackUrl={this.props.feedbackUrl} />} />
          <Route path="/courses/create-from-template/:templateId" render={({match}) => <EditCourse
            templateId={match.params.templateId}
            view="courseBuilder"
            viewCourseUrl={this.props.viewCourseUrl}
            feedbackUrl={this.props.feedbackUrl}
          />}/>
          <Route path="/courses/:id/edit" render={({match}) => <EditCourse
            id={match.params.id}
            view="courseBuilder"
            viewCourseUrl={this.props.viewCourseUrl}
            feedbackUrl={this.props.feedbackUrl}
          />} />
        </div> : <div>
            <Route path="/" render={() => <div>
              <CourseBanner title="Kursbyggaren" activePage="courseBuilder" routes={{feedback: this.props.feedbackUrl}} />
              <h2>Behrighet saknas</h2>
              <p>Detta verktyg r endast till fr anstllda p Kunskapsskolan.</p>
              <h3>Anvndarattribut</h3>
              <table className="ked_boxed" style={{border: "1px solid #eee"}}>
                <thead>
                  <tr>
                    <th>Attribut</th>
                    <th>Vrde</th>
                  </tr>
                </thead>
                <tbody>
                  {Object.keys(env.currentUser).map(attr => <tr key={attr}>
                    <td>{attr}</td>
                    <td>{env.currentUser[attr]}</td>
                  </tr>)}
                </tbody></table>
            </div>} />
          </div>}
      </Router>
      <ErrorSuccessFeedback />
    </div>
  }
}
