import * as React from 'react';
import { Course } from 'kedbackend/models';
import { L } from '../../../utils/utils';
import { Link } from 'react-router-dom';

export const ListCourseTemplates = (props: { courses: Course[], selectedCourseId? }) => {
  const { courses } = props;
  return <div>
    <h2>{L`Vlj kursmall att redigera: `}</h2>
    <ul className="entity-list">
      {courses.map(({ id, name, code, publishable, points, subjectId }) =>
        <li key={id} className={
          (publishable ? "complete" : "incomplete") +
          (props.selectedCourseId === id ? " selected" : "")}>
          <Link to={`/course-templates/${subjectId}/${id}`}>{name} - {points}</Link></li>)}
    </ul>
  </div>
}

