import { Document, Course, KnowledgeRequirement, CourseModule, Task } from 'kedbackend/models';
import { flatten, L } from "../../../utils/utils";

export function getUncoveredKnowledgeRequirements(course: Course) {
  const uncoveredRequirements = course.knowledgeRequirements.reduce((result, item) => {
    result[item.id] = item;
    return result;
  }, {} as { [id: string]: KnowledgeRequirement });

  course.tasks.forEach(task => {
    task.knowledgeRequirements.forEach(kr => {
      delete uncoveredRequirements[kr.id];
    });
  });

  return Object.keys(uncoveredRequirements).map(id => uncoveredRequirements[id]);
}

export function getIdsNotCoveredByTasks(course: Course) : {[id: string]: true} {
  const uncoveredIds = {};
  // 1. Mark the ids of all knowledge requirements, abilities and central content for this course:
  course.knowledgeRequirements.forEach(r => {
    uncoveredIds[r.id] = true;
  });
  course.abilities.forEach(a => {
    uncoveredIds[a.id] = true;
  });
  course.centralContent.forEach(cc => {
    uncoveredIds[cc.id] = true;
  });
  // 2. List all tasks and unmark all ids that they refer to
  const { tasks } = course;
  tasks.forEach(task => {
    task.knowledgeRequirements.forEach(kr => {
      delete uncoveredIds[kr.id];
    });
    task.abilities.forEach(a => {
      delete uncoveredIds[a.id];
    });
    task.centralContent.forEach(cc => {
      delete uncoveredIds[cc.id];
    });
  });
  return uncoveredIds;
}

export function sanityCheck (course: Course) {
  function hasDuplicateTasks(course: Course) {
    const taskIds: {[id:string]: boolean} = {};
    return flatten(course.modules.map(module => module.taskIds.map(taskId => {
      if (taskIds[taskId]) {
        const task = course.tasks.find(t => t.id === taskId);
        return L`Samma uppgift frekommer flera gnger: "${task.name}"`;
      }
      taskIds[taskId] = true;
    }).filter(x => x)));
  }

  function tasksWithSameUrl(course: Course) {
    const taskUrls: {[url:string]: Task} = {};
    return course.tasks.map(task => {
      if (task.url) {
        if (taskUrls[task.url]) {
          return L`Tv uppgifter pekar p samma URL: "${taskUrls[task.url].name}" samt "${task.name}"`;
        }
        taskUrls[task.url] = task;
      }
    }).filter(x => x);  
  }

  function hasEmptyModuleNames(course: Course) {
    return course.modules.some (module => module.name === "") &&
      L`Det finns minst en modul utan angivet namn`;
  }

  function hasDuplicateModuleNames(course: Course) {
    const moduleNames: {[name: string]: true} = {};
    return course.modules.map(module => {
      if (module.name && moduleNames[module.name]) {
        return L`Modulen med namn ${module.name} frekommer flera gnger`;
      }
      if (module.name) moduleNames[module.name] = true;
    })
  }

  const checks = flatten([
    hasDuplicateTasks(course),
    tasksWithSameUrl(course),
    hasEmptyModuleNames(course),
    hasDuplicateModuleNames(course)
  ]);

  return checks.filter (result => result);
}


export function getTasksPerId (course: Course) {
  const result: {[id: string]: Task[]} = {};
  function add(id: string, task: Task) {
    const list = result[id] || (result[id] = []);
    list.push(task);
  }
  course.tasks.forEach(task => {
    task.abilities.forEach(a => add(a.id, task));
    task.centralContent.forEach(c => add(c.id, task));
    task.futureAbilities.forEach(fa => add(fa, task));
  });
  return result;
}
