import * as React from 'react';
import { CourseBanner } from "../courses/course-banner";
import env from '../../../globals/KED.env';
import { School } from 'kedbackend/models';
import { EditableSchoolList } from './editable-school-list';
import { showError, compareProp } from "../../../utils/utils";

interface Props {
  viewCourseUrl: string;
  feedbackUrl: string;
}

interface State {
  schools: School[] | null
}

export class Schools extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      schools: null
    }
  }

  componentDidMount() {
    this.load().catch(err => showError(err.message || err));
  }

  async load() {
    const schools = await env.kedBackendClient.list<School>("schools");
    this.setState({ schools });
  }

  render() {
    const { schools } = this.state;
    schools && schools.sort(compareProp("name"));
    return <div style={{ outline: 0 }}>
      <CourseBanner title="Skolor" activePage="schools" routes={{feedback: this.props.feedbackUrl}} />
      <div className="sv-row sv-layout">
        <div className="pagecontent sv-layout sv-spacer-20pxvt sv-skip-spacer">
          <EditableSchoolList schools={schools} viewCourseUrl={this.props.viewCourseUrl} />
        </div>
      </div>
    </div>

  }
}
