import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { Course, CourseModule, Image, Task, Document, Ability } from 'kedbackend/models';
import $ from 'jquery';
import { EditableResourceList } from '../sub-components/editable-resource-list';
import { EditableTaskList } from '../sub-components/editable-task-list';
import update from 'react-addons-update';
import { ICourseEditorHost } from '../interfaces/course-editor-host';
import { RenameableText } from '../../utility-components/renameable-text';
import { RemoveItem } from '../sub-components/remove-item';
import { createUUID } from 'kedbackend/client';
import { L, showError } from "../../../utils/utils";
import moment from 'moment';
import { Wysiwyg } from '../../utility-components/wysiwyg';
import actions_swedish from '../../utility-components/wysiwyg/actions-sv';
import { OpenCloseBox } from '../../utility-components/open-close-box';

interface Props {
  course: Course;
  module: CourseModule;
  host: ICourseEditorHost;
  idsNotCoveredByAnyTask: {[id: string]: true};
  onReorder: (direction: 1 | -1) => void;
  getOrderedModules: ()=>CourseModule[];
}

interface State {
  editMandatoryContent: boolean;
}

const DEFAULT_KNOWLEDGE_MATRIX = [{
  abilityIds: [],
  EIds: [],
  CIds: [],
  AIds: []
}];

export class CourseModuleComponent extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      editMandatoryContent: false
    }
  }

  getModuleIndex() {
    const {course, module} = this.props;
    return course.modules.findIndex(m => m.id === module.id);
  }

  getModuleOrderIndex() {
    const {course, module} = this.props;
    const orderedModules = this.props.getOrderedModules();
    return orderedModules.findIndex(m => m.id === module.id);
  }

  updateModule(moduleUpdates) {
    const {module} = this.props;
    const updatedModule = update(module, moduleUpdates);
    const moduleIndex = this.getModuleIndex();
    this.props.host.update({ modules: { $splice: [[moduleIndex, 1, updatedModule]] } });
  }

  /* This method works for both adding new tasks to the task table
  and adding references to existing tasks. The magic of that has to do with
  whether task.$meta = 'add' or not and is done in EditCourse.save()
  */
  onTaskAdded (task: Task, taskCustomization: Partial<Task> | null) {
    // Add task ID to module taskIds list:
    const courseUpdates:any = {};
    const taskIds = this.props.module.taskIds.slice();
    taskIds.push(task.id);
    // Add physical Task to Course.tasks.
    if (!this.props.course.tasks.some(t => t.id ===task.id)) {
      courseUpdates.tasks = {$push: [task]};
    }
    const customizations = {...(this.props.module.taskCustomizations || {})};
    if (taskCustomization) customizations[task.id] = taskCustomization;
    const moduleIndex = this.getModuleIndex();
    courseUpdates.modules = {};
    courseUpdates.modules[moduleIndex] = {
      taskIds: {$set: taskIds},
      taskCustomizations: {$set: customizations}
    };
    // Update module
    this.props.host.update(courseUpdates);
  }

  onTaskUpdated (task: Task) {
    if (!task.$meta) throw new Error(L`Hoppsan, blev lite knas. Fel ID: ${'Pa7Dq'}`);
    const taskIndex = this.props.course.tasks.findIndex(t => t.id === task.id);
    if (taskIndex >= 0) {
      this.props.host.update({tasks: {$splice: [[taskIndex, 1, task]]}});
    }
  }

  onTaskCustomized (taskId: string, customization: Partial<Task>) {
    const customizations = {...(this.props.module.taskCustomizations || {})};
    customizations[taskId] = customization;
    this.updateModule({taskCustomizations: {$set: customizations}});
  }

  onTaskDeleted (id: string) {
    // Remove task ID from module taskIds list.
    // This depends on the fact that EditCourse._setState() maintains
    // removed Task links (which it actually does (or should do at least)).
    this.updateModule({
      taskIds: {$set: this.props.module.taskIds.filter(tid => tid !== id)},
      taskCustomizations: {
        $apply: customizations => {
          const copy = {...customizations};
          delete copy[id];
          return copy;
        }
      }
    });
  }

  onTaskReplaced (id: string, task: Task) {
    const idxTask = this.props.module.taskIds.indexOf(id);
    if (idxTask < 0) {
      showError(L`Kunde inte spara uppgift. Felkod d72kQ`);
      return;
    }
    const moduleUpdates = {
      // Replace old id with new id:
      taskIds: {$splice: [[idxTask, 1, task.id]]},
      taskCustomizations: {
        $apply: customizations => {
          const copy = {...customizations};
          delete copy[id];
          return copy;
        }
      }
    }
    const updatedModule = update(this.props.module, moduleUpdates);
    const moduleIndex = this.getModuleIndex();
    this.props.host.update({
      // Apply moduleUpdates from above:
      modules: { $splice: [[moduleIndex, 1, updatedModule]] },
      // Add the new task to Course.tasks.
      // Don't remove the old one! Why? Because it may be referenced
      // by another module in same course! Let EditCourse._setState()
      // do the "garbage collection" instead.
      tasks: { $set: this.props.course.tasks.concat(task)}
    });
  }

  render() {
    const { course, host, module, idsNotCoveredByAnyTask } = this.props;
    const moduleIndex = this.getModuleIndex();
    const moduleOrderedIndex = this.getModuleOrderIndex();
    let knowledgeMatrixDiv;

    const updateModule = this.updateModule.bind(this);
    let week = 1;
    const startWeekOptions = ["--"];
    const endWeekOptions = ["--"];
    for (let i=0; i<51; ++i) {
      startWeekOptions.push(''+ week);
      endWeekOptions.push(''+ ++week);
    }

    return <div className="course-module ked_boxed" style={{position: 'relative'}}>
      <h3><RenameableText eager={true}
        clickAdvertiseText="Namnls modul"
        maxChars={100}
        text={module.name}
        onChange={newName => updateModule({name: {$set: newName}})} />
      </h3>
      <div className="align-horizontal spaced">
        <div className="horizontalItem center">
          <p>Startvecka</p>
        </div>
        <div className="horizontalItem center">
          <select value={module.startWeek || "--"}
            onChange={ev => updateModule({startWeek: {
              $set: ev.target.value === '--' ?
                undefined :
                parseInt(ev.target.value)
            }
          })}>
            {startWeekOptions.map(o => <option key={o} value={o}>{o}</option>)}
          </select>
        </div>
        <div className="horizontalItem center">
          <p>Slutvecka</p>
        </div>
        <div className="horizontalItem center">
          <select value={module.endWeek || "--"}
            onChange={ev => updateModule({endWeek: {
              $set: ev.target.value === '--' ?
                undefined :
                parseInt(ev.target.value)
            }
          })}>
            {endWeekOptions.map(o => <option key={o} value={o}>{o}</option>)}
          </select>
        </div>
      </div>
      <h4>Kursmodulens introduktion</h4>
      <Wysiwyg
        actions={[
          "bold",
          "italic",
          "underline",
          "strikethrough",
          "heading2",
          "heading3",
          "olist",
          "ulist",
          "line",
          "link",
          "image"]}
        defaultActions={actions_swedish}
        html={module.mandatoryContent}
        onChange={html => {
          updateModule({mandatoryContent: {$set: html}})
        }} />

      <h4>Uppgifter</h4>
      <EditableTaskList
        taskIds={module.taskIds}
        taskCustomizations={module.taskCustomizations}
        course={course}
        module={module}
        host={host}
        idsNotCoveredByAnyTask={idsNotCoveredByAnyTask}
        onTaskAdded={(task: Task, taskCustomization: Partial<Task>) => this.onTaskAdded(task, taskCustomization)}
        onTaskUpdated={(task: Task) => this.onTaskUpdated(task)}
        onTaskCustomizationUpdated={(taskId: string, customization: Partial<Task>) =>
          this.onTaskCustomized(taskId, customization)}
        onTaskDeleted={(id: string) => this.onTaskDeleted(id)}
        onTaskReplaced={(id: string, task: Task) => this.onTaskReplaced(id, task)} />

      <h4>Modulresurser</h4>
      <p className="subHeader">(Eventuella resuser fr redovisning av modulen samt fr modulen som helhet)</p>
      <EditableResourceList
        resources={module.resources}
        onUpdate={resourcesUpdates => updateModule({ resources: resourcesUpdates })}
        host={host} />
      <div style={{position: 'absolute', top: 0, right: 0}}>
        {moduleOrderedIndex > 0 && <span>
          <i className="fa fa-arrow-circle-up selectable"
            aria-hidden="true"
            onClick={()=>this.props.onReorder(-1)} /> </span>}
        {moduleOrderedIndex < course.modules.length - 1 && <span>
          <i className="fa fa-arrow-circle-down selectable"
            aria-hidden="true"
            onClick={()=>this.props.onReorder(1)} /> </span>}
        <RemoveItem
          title="Ta bort modulen"
          style={{display: 'inline-block'}}
          onClick={()=>confirm(L`Ta bort modul ${module.name}?`) && host.update({modules: {$splice: [[moduleIndex, 1]]}})} />
      </div>
    </div>
  }
}

