import * as React from 'react';
import { L } from '../../../utils/utils';
import { Course, Subject } from 'kedbackend/models';
import env from '../../../globals/KED.env';
export { EditCourse } from './edit-course';
import { HashRouter as Router, Route, Link, Redirect } from 'react-router-dom';
import { CourseBanner } from "./course-banner";
import { ListCourseTemplates } from './list-course-templates';
import { Spinner } from "../sub-components/spinner";

interface Props {
  selectedCourseId?: string;
  selectedCoursePublishable?: string;
  feedbackUrl: string;
}

interface State {
  subjects: Subject[] | null;
}

export class Courses extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      subjects: null
    };
  }

  componentWillMount() {
    this.load();
  }

  async load() {
    const subjects = await env.kedBackendClient.list<Subject>('subjects', { include: 'standardCourses' });
    this.setState({ subjects });
  }

  render() {
    const courses: Course[] = [];
    this.state.subjects && this.state.subjects.forEach(s => courses.push.apply(courses, s.standardCourses));
    courses.sort((c1, c2) => c1.name > c2.name ? 1 : c1.name < c2.name ? -1 : 0);
    return <div>
      <CourseBanner
        title={L`Kursmallar`}
        activePage="templateBuilder"
        routes={{feedback: this.props.feedbackUrl}}
      />
      {!this.state.subjects ?
        <p><Spinner />V.g. vnta medan kurser laddas</p> :
        <ListCourseTemplates courses={courses} />}
      </div>
  }
}
