import * as React from 'react';
import { L } from '../../utils/utils';
import $ from 'jquery';

interface Props {
    text: string;
    tag?: 'input' | 'textarea'
    eager?: boolean;
    onChange: (newName: string) => void;
    placeholder?: string; // If text is empty, this will be the placeholder
    clickAdvertiseText?: string;
    maxChars?: number;
    readOnly?: boolean;
}

interface State {
    editMode: boolean;
    runtimeStyle: Object;
    text: string | null;
}

/** 
 * Makes a text element (such as an A or P tag) possible to replace with an input element of the same CSS style, for example by clicking on it.
 * 
*/
export class RenameableText extends React.Component<Props,State> {
    label: HTMLElement;
    input: HTMLInputElement | HTMLTextAreaElement;

    constructor(props: Props) {
        super(props);
        this.state = {
            editMode: false,
            runtimeStyle: null,
            text: props.text || ""
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.text !== nextProps.text) {
            this.setState({text: nextProps.text || ""});
        }
    }

    render() {
        const text = this.state.text || "";
        return <div
                className={"renameable-text" + (text ? "" : " unnamed") + (this.props.readOnly ? " disabled" : "")}
                tabIndex={!this.state.editMode && 0}
                onFocus={(e)=>{this.rename()}}
                style={{width: "100%"}}
            >
            <div
                ref={label => this.label = label}                
                style={{
                    display: this.state.editMode ? 'none' : ''
                }}
            >
                {text || this.props.clickAdvertiseText}
            </div>
            {this.props.tag === 'textarea' ?
                <textarea ref={input => this.input = input}
                    maxLength={this.props.maxChars}
                    style={{...this.state.runtimeStyle, display: this.state.editMode ? '' : 'none'}}
                    onFocus={ev=>$(ev.target).select()}
                    onBlur={this.onBlur.bind(this)}
                    value={text}
                    onKeyDown={this.onKeyDown.bind(this)}
                    onChange={this.onChange.bind(this)}
                    tabIndex={this.state.editMode && 0}
                    placeholder={this.props.placeholder}
                /> : 
                <input ref={input => this.input = input}
                    type="text"
                    maxLength={this.props.maxChars}
                    style={{...this.state.runtimeStyle, display: this.state.editMode ? '' : 'none'}}
                    onFocus={ev=>$(ev.target).select()}
                    onBlur={this.onBlur.bind(this)}
                    value={text}
                    onKeyDown={this.onKeyDown.bind(this)}
                    onChange={this.onChange.bind(this)}
                    tabIndex={this.state.editMode ? 0 : undefined}
                    placeholder={this.props.placeholder}
                />
            }
        </div>
    }

    onKeyDown(e: KeyboardEvent) {
        if (e.keyCode === 27) { // ESC
            this.input.value = this.props.text || "";
            this.input.blur();
            e.stopPropagation();
        } else if (e.keyCode === 13) { // ENTER
            this.propagateOnChange(this.input.value);
            this.setState({ editMode: false, text: this.input.value });
        }
    }

    onBlur(e: Event) {
        this.propagateOnChange(this.input.value);
        this.setState({ editMode: false, text: this.input.value });
    }

    propagatedOnChange: string;

    propagateOnChange(newValue) {
        if (this.propagatedOnChange != newValue) {
            this.propagatedOnChange = newValue;
            this.props.onChange(newValue);
        }
    }

    onChange(e: Event) {
        this.setState({ text: this.input.value });
        if (this.props.eager) this.propagateOnChange(this.input.value);
    }

    rename(txt?: string) {
        if (this.state.editMode || this.props.readOnly) return;
        txt = txt || this.state.text || "";
        const label = $(this.label);
        this.setState({
            text: txt,
            editMode: true,
            runtimeStyle: {
                textAlign: label.css('textAlign'),
                fontSize: label.css('font-size'),
                paddingTop: label.css('paddingTop'),
                paddingBottom: label.css('paddingBottom'),
                paddingLeft: label.css('paddingLeft'),
                paddingRight: label.css('paddingRight'),
                marginTop: label.css('marginTop'),
                marginBottom: label.css('marginBottom'),
                marginLeft: label.css('marginLeft'),
                marginRight: label.css('marginRight'),
                borderRadius: label.css('borderRadius'),
                backgroundColor: label.css('backgroundColor'),
                lineHeight: label.css('lineHeight'),
                fontWeight: label.css('fontWeight'),
                outLine: 0,
                borderStyle: 'none',
                height: "100%",
                width: "100%",
                boxSizing: "border-box",
            }
        }, ()=>$(this.input).focus());
    }
}
