import { KedBackendClient, BearerProvider, BearerTokenInfo, IsomorphicEnv } from '../ked-backend-client';
import { BearerStorageSessionStorage } from "./bearer-storage-sessionstorage";
import { avoidSimultanousCalls } from '../ked-backend-client/utils';
import { KedModelMigratorMixin } from '../ked-model-migrator';
KedModelMigratorMixin(KedBackendClient.prototype);

export const storage = new BearerStorageSessionStorage();

const timeOfPageLoad = Date.now();

export class WebServerBearerProvider implements BearerProvider {
    tokenInfo: BearerTokenInfo;
    wantsRedirect: boolean;

    constructor (
        public tokenPath: string,
        public tokenResponseMapper: (responseText: string) => BearerTokenInfo,
        public tokenId: string)
    {
        this.tokenInfo = {token: null, expires: 0};
        this.getBearer = avoidSimultanousCalls(this.getBearer.bind(this));
        this.refreshBearer = avoidSimultanousCalls(this.refreshBearer.bind(this));
    }

    async getBearer() {
        if (!this.tokenInfo.token) {
            if (this.tokenId) this.tokenInfo = await storage.load(this.tokenId);
        }
        if (this.tokenInfo.expires < Date.now()) {
            await this.refreshBearer();
        }
        return this.tokenInfo;
    }

    async refreshBearer() {
        const res = await fetch(this.tokenPath, {
            headers: {Accept: "text/plain; application/json"},
            redirect: 'manual',
            cache: 'no-cache',
            credentials: "same-origin"
        });
        if (res.status === 302 || (!res.status && res.type === "opaqueredirect")) {
            // User session timed out and server wants to redirect entire page to login page
            // Time to reload current page to force a redirect of the entire page instead for
            // just redirecting to /api/token or whatever.
            if (Date.now() - timeOfPageLoad > 60000) {// prohibit endless loop of reloading self.
                this.wantsRedirect = true; // So that listeners to onbeforeunload could show alternate message.
                console.log("Redirect wanted. Reload page.")
                window.location.reload(true);
            }
        }
        if (res.status != 200) throw new Error("HTTP"+res.status + " " + await res.text());
        this.tokenInfo = this.tokenResponseMapper(await res.text());
        storage.save(this.tokenId, this.tokenInfo);
        return this.tokenInfo;
    }
}

export const isomorphic = {fetch: fetch.bind(self), btoa: btoa.bind(self)};

export class KedBackendClientWeb extends KedBackendClient {
    constructor(
        apiBaseUrl: string,
        providerOrTokenPath: string | BearerProvider,
        options?: {
            tokenResponseMapper?: (responseText: string) => BearerTokenInfo,
            tokenId?: string
        })
    {
        const bearerProvider = typeof providerOrTokenPath !== 'string' ?
            providerOrTokenPath :
            new WebServerBearerProvider(
                providerOrTokenPath,
                (options && options.tokenResponseMapper) || (x=>({token: x, expires: Date.now() + 59 * 60 * 60})),
                options && options.tokenId);
        super(isomorphic, bearerProvider, apiBaseUrl);
    }
}
