export interface AccessClaim {
  type: string;
  value: string;
}

export type AccessRight = 'R' | 'W' | 'S';

export class DocumentAccess {
  constructor(
    public accessClaimType: string,
    public accessClaimValue: string,
    public right: AccessRight
  ) { }

  static fromString(ac: string) {
    if (!ac) return null;
    var split = ac.split(':');
    if (split.length < 3) throw new Error("Invalid access string: " + ac);
    var claimType = DocumentAccess.unescape(split[0]);
    var claimValue = DocumentAccess.unescape(split[1]);
    var right = split[2];
    if (right !== 'R' && right !== 'W' && right !== 'S')
      throw new Error("Invalid access string: " + ac);
    return new DocumentAccess(
      claimType,
      claimValue,
      right);
  }

  static escape(accessComponent: string) {
    return accessComponent.replace(/\%/g, "%25").replace(/\:/g, "%3A");
  }

  static unescape(accessComponent: string) {
    return accessComponent.replace(/\%3A/g, ":").replace(/\%25/g, "%");
  }

  toString() {
    return DocumentAccess.escape(this.accessClaimType) + ":" +
      DocumentAccess.escape(this.accessClaimValue) + ":" +
      this.right;
  }

  static fromStringArray(acl: string[]) {
    return acl
      .map(ac => DocumentAccess.fromString(ac))
      .filter(ac => ac);
  }

  static toStringArray(acl: DocumentAccess[]) {
    return acl.map(ac => ac.toString());
  }
}

export function hasAccess(acl: DocumentAccess[], userClaims: AccessClaim[], requestedRight: AccessRight) {
  if (userClaims.some(claim => claim.type === 'role' && claim.value === "ADMIN"))
    return true;

  return acl.some(a =>
    userClaims.some(c =>
      a.accessClaimType === c.type &&
      a.accessClaimValue === c.value && (
        (a.right === 'R' && requestedRight === 'R') ||
        (a.right === 'W' && ['R','W'].indexOf(requestedRight) >= 0) ||
        (a.right === 'S'))));
}
