import { parseQueryString, generateQueryString, splitUrlAndQuery } from "../utils/query-string";
import { KedBackendClientWeb, WebServerBearerProvider, isomorphic, storage } from 'kedbackend/clientweb';
import { BearerProvider, KedBearerProvider } from 'kedbackend/client';
import cfg from '../globals/KED.cfg';
import env from '../globals/KED.env';
import { User } from "../contracts/ked-models";
import { IMPERSONATION_QUERY_PARAMS } from "../access-control/index";
import { cherryPickProps } from "../utils/utils";

function getMergedTokenPath(tokenPath: string, locationSearch: string, scopes: string[]) {
  // Merge configured query params of token path with params given to current page
  const currentQuery = parseQueryString(locationSearch);
  const impersonationProps = cherryPickProps(currentQuery, IMPERSONATION_QUERY_PARAMS);
  const [tokenPathWithoutQuery, tokenQueryString] = splitUrlAndQuery(tokenPath);
  const tokenPathQuery = parseQueryString(tokenQueryString);
  return tokenPathWithoutQuery + generateQueryString({
    ...tokenPathQuery,
    ...impersonationProps,
    scopes: scopes.join(',')
  });
}

function getTokenId(mergedTokenPath: string, userEmail: string) {
  return mergedTokenPath + "/" + userEmail;
}

function saveUserInfo(user: User, tokenId: string) {
  env.currentUser = user;
  sessionStorage.setItem("userInfo" + tokenId, JSON.stringify(user));
}

function loadUserInfo(tokenId: string) {
  const storedSessionUser = sessionStorage.getItem("userInfo" + tokenId);
  if (storedSessionUser) {
    env.currentUser = JSON.parse(storedSessionUser);
  }
}

function createBearerProvider(mergedTokenPath: string, userEmail: string) {
  const tokenId = getTokenId(mergedTokenPath, userEmail);
  return new WebServerBearerProvider(mergedTokenPath, responseText => {
    const res = JSON.parse(responseText) as { ok: boolean, error?: string, token?: string, user?: User };
    if (!res.ok) throw new Error(res.error);
    if (res.user) {
      saveUserInfo(res.user, tokenId);
    }
    return { token: res.token, expires: Date.now() + 59 * 60 * 1000 };
  }, tokenId);
}

function createGoogleTokenProvider(mergedTokenPath: string, userEmail: string) {
  const tokenId = getTokenId(mergedTokenPath, userEmail);
  return new WebServerBearerProvider(mergedTokenPath, responseText => {
    const res = JSON.parse(responseText) as { ok: boolean, error?: string, token?: string, user?: User };
    if (!res.ok) throw new Error(res.error);
    return { token: res.token, expires: Date.now() + 59 * 60 * 1000 };
  }, tokenId);
}

function createTestTokenProvider(tokenUrl: string, user: User, scopes: string[]) {
  return new KedBearerProvider(
    isomorphic,
    storage,
    tokenUrl + user.mail + location.search,
    cfg.KED_CLIENT_ID,
    cfg.KED_CLIENT_SECRET,
    tokenUrl, {
      email: user.mail.toLowerCase(),
      roles: user.roles,
      school: user.school,
      schoolType: user.schoolType,
      scopes
    });
}

// env.currentUser.mail is set by SiteVision server initially.
// A response from /api/token may change the mail attribute of the current
// user, so env.currentUser.mail may be different after getting a response.
// However, the initial value is valuable always in order to distinguish the case
// when one normal user logs out and another user logs in.
const initialUserEmail = env.currentUser && env.currentUser.mail; // Initial value of mail. May change.
if (initialUserEmail) {
  // KED
  if (cfg.KED_TOKEN_PATH) {
    //
    //
    // Production / SiteVision proxied /api/token to request tokens from:
    //
    //
    const mergedTokenPath = getMergedTokenPath(
      cfg.KED_TOKEN_PATH,
      location.search, [
        "kedbackend",
        "EDS",
      ]);
    env.bearerProvider = createBearerProvider(mergedTokenPath, initialUserEmail);
    loadUserInfo(getTokenId(mergedTokenPath, initialUserEmail));

    // Google
    const googleMergedPath = getMergedTokenPath(
      cfg.KED_TOKEN_PATH,
      location.search, [
        "https://www.googleapis.com/auth/calendar.readonly",
      ]);
    env.googleTokenProvider = createGoogleTokenProvider(googleMergedPath, initialUserEmail);
  } else if (cfg.KED_TOKEN_URL && cfg.KED_CLIENT_ID && cfg.KED_CLIENT_SECRET) {
    //
    //
    // Test - go directly to KEDAUTH server to retrieve tokens
    //
    //
    env.bearerProvider = createTestTokenProvider(cfg.KED_TOKEN_URL, env.currentUser, [
      "kedbackend",
      "EDS",
    ]);
    env.googleTokenProvider = createTestTokenProvider(cfg.KED_TOKEN_URL + "/google", env.currentUser, [
      "https://www.googleapis.com/auth/calendar.readonly",
    ]);
  } else {
    throw new Error(`Missing configuration parameter KED_TOKEN_PATH`);
  }
}
