import moment from 'moment';
import { SchoolMoment, getSchoolMoment, addYear } from './school-moment';
import { AcademicYear, Term } from '../contracts/ked-models';

type Moment = moment.Moment;

function isSchoolMoment(obj: Date | SchoolMoment): obj is SchoolMoment {
  return 'academicYear' in obj;
}

export class SchoolTerm {
  academicYear: AcademicYear;
  term: Term;

  constructor(dateOrSchoolMoment: Date | SchoolMoment) {
    const schoolMoment = isSchoolMoment(dateOrSchoolMoment) ?
      dateOrSchoolMoment : getSchoolMoment(moment(dateOrSchoolMoment));
    this.academicYear = schoolMoment.academicYear;
    this.term = schoolMoment.term;
  }

  get year() {
    return parseInt(this.academicYear
        .split('/')[this.term === 'AT' ? 0 : 1]);
  }

  nextTerm() {
    return new SchoolTerm(this.term === 'AT' ?
      {
        term: 'ST',
        academicYear: this.academicYear
      } :
      {
        term: 'AT',
        academicYear: addYear(this.academicYear, 1)
      });
  }

  prevTerm() {
    return new SchoolTerm(this.term === 'AT' ?
      {
        term: 'ST',
        academicYear: addYear(this.academicYear, -1)
      } :
      {
        term: 'AT',
        academicYear: this.academicYear
      });
    
  }

  toLocaleString(locales: string[], shortYear?: boolean) {
    let year = this.term === 'AT' ?
      this.academicYear.split('/')[0]:
      this.academicYear.split('/')[1];

    if (shortYear)
      year = year.substr(2);

    for (let locale of locales) {
      switch (locale.split('-')[0].split('_')[0].toLowerCase()) {
        case 'en':
          return `${this.term === 'AT' ? "AT" : "ST"} ${year}`;
        case 'sv':
          return `${this.term === 'AT' ? "HT" : "VT"} ${year}`;
      }
    }
  }
}
