import * as React from 'react';
import { L } from '../../../utils/utils';
import { SkolverketSubject, SkolverketCourse } from './skolverket-subject';
import $ from 'jquery';
import env from '../../../globals/KED.env';
import { Subject, Course, Ability, KnowledgeRequirement, CentralContent } from 'kedbackend/models';
import { MutationRequest, BatchRunner, createUUID } from 'kedbackend/client';
import { SubjectToImport } from './subject-to-import';
import { loadCourse } from '../utils';

interface Props extends SkolverketSubject {
  onCancel: () => void;
  onImportSuccess: (subject: string) => void;
}

interface Change {
  change?: string;
  content?: string;
  mutations: (r: BatchRunner) => void;
}

interface State {
  showFullText: boolean;
  changes: Change[];
  isWorking: boolean,
  isImporting: boolean
}

export class UploadedSubject extends React.Component<Props, State> {
  purposeElem: HTMLElement;
  coursesElems: { [courseCode: string]: { course: SkolverketCourse, elem: HTMLElement } };
  centralContentElems: HTMLElement[];
  knowledgeRequirementElems: HTMLElement[];

  constructor(props: Props) {
    super(props);
    this.coursesElems = {};
    this.centralContentElems = [];
    this.knowledgeRequirementElems = [];
    this.state = {
      showFullText: false,
      changes: [],
      isWorking: true,
      isImporting: false
    };
  }

  async componentDidMount() {
    this.addClassesForCherryPickedElements();
    try {
      const changes = await this.diffWithExisting();
      this.setState({ changes });
    } finally {
      this.setState({ isWorking: false });
    }
  }

  addClassesForCherryPickedElements() {
    const abilities = $(this.purposeElem).find('li').first().parent().children('li');
    abilities.addClass('ability');
    const centralContents = $(this.centralContentElems).find('li');
    centralContents.addClass('central-content');
    const knowledgeRequirements = $(this.knowledgeRequirementElems).find('p');
    knowledgeRequirements.addClass('knowledge-requirement');
    const all = $([abilities, centralContents, knowledgeRequirements]);
    all.addClass('marked-area');
  }

  cherryPickData(): SubjectToImport {
    //
    // Reads elements (tagged by classes in addClassesForCherryPickedElements()) and converts their inner HTML to the corresponding
    // model SubjectToImport. Note that if we need to adjust how to pick the right LI or P elements, we
    // will only need to change the code in addClassesForCherryPickedElements(), not this code.
    //
    const skolSubject = this.props;
    const abilities = Array.from($(this.purposeElem).find('li').first().parent().children('li'));

    return {
      name: skolSubject.name,
      code: skolSubject.code,
      abilities: abilities.map((a, i) => `${i + 1}. ${$(a).html()}`),
      courses: Object.keys(this.coursesElems)
        .map(courseCode => this.coursesElems[courseCode])
        .map(({ course, elem }) => ({
          name: course.name,
          code: course.code,
          points: course.points,
          centralContent: Array.from($(elem).find('.central-content')).map(c => $(c).html()),
          knowledgeRequirements:
          Array.from($(elem).find('.grade-step-E .knowledge-requirement')).map(r => $(r).html())
            .map(html => ({ gradeStep: "E", html }))
            .concat(
            Array.from($(elem).find('.grade-step-C .knowledge-requirement')).map(r => $(r).html())
              .map(html => ({ gradeStep: "C", html })))
            .concat(
            Array.from($(elem).find('.grade-step-A .knowledge-requirement')).map(r => $(r).html())
              .map(html => ({ gradeStep: "A", html })))
        }))
    };
  }

  async diffWithExisting(): Promise<Change[]> {
    const subjectToImport = this.cherryPickData();
    let changes: Change[] = [];
    // Find existing Subject
    const existingSubjects = await env.kedBackendClient.list<Subject>("subjects");
    let existingSubject = existingSubjects.filter(s => s.code === this.props.code)[0];
    let centralContent = [] as CentralContent[],
      knowledgeRequirements = [] as KnowledgeRequirement[];

    let createTime = Date.now();
    function ct() {
      const rv = createTime;
      createTime += 2;
      return rv;
    }

    if (existingSubject) {
      // Include abilities and standardCourses with the found Subject:
      existingSubject = await env.kedBackendClient.get(
        'subjects',
        existingSubject.id, { include: "courseTemplates" });
      // Lazily load all central contents and knowledgeRequirements on
      // each course template
      const expandedCourseTemplates = await Promise.all(
        existingSubject.courseTemplates.map(ct => env.kedBackendClient.get<Course>("courses", ct.id, {
          include: [
            'centralContent',
            'knowledgeRequirements'
          ]
        })));

      for (const courseTemplate of expandedCourseTemplates) {
        // We don't have to include graphs. We already know that all courses
        // with the same name and subjectCode will point to the same centralContent and
        // same knowledgeRequirements.

        // Now go through the XML data and record the order of centralContent
        // and knowledgeRequirements:
        const xmlCourse = subjectToImport.courses.find(c => c.name === courseTemplate.name);

        const centralContentOrder = xmlCourse.centralContent
          .map(html => courseTemplate.centralContent
            .filter(cc => cc.name === html)
            .map(cc => cc.id)[0]);

        if (centralContentOrder.some(id => !id)) throw new Error(
          "Could not lookup all central contents!");

        const knowledgeRequirementsOrder = xmlCourse.knowledgeRequirements
          .map(xmlKR => courseTemplate.knowledgeRequirements
            .filter(kr => kr.name === xmlKR.html && kr.gradeStep === xmlKR.gradeStep)
            .map(kr => kr.id)[0]);

        if (knowledgeRequirementsOrder.some(id => !id)) throw new Error(
          "Could not lookup all knowledge requirements!");

        let derivedCourses = await env.kedBackendClient.list<Course>(
          "courses", { name: courseTemplate.name });
        // Just for sure - filter away courses with same name but different course code...
        derivedCourses = derivedCourses.filter(c => c.subjectCode === subjectToImport.code);

        for (const course of derivedCourses) {
          if (!course.centralContentOrder ||
            !course.knowledgeRequirementsOrder ||
            course.centralContentOrder.join(',') !== centralContentOrder.join(',') ||
            course.knowledgeRequirementsOrder.join(',') !== knowledgeRequirementsOrder.join(',')) {
            course.centralContentOrder = centralContentOrder;
            course.knowledgeRequirementsOrder = knowledgeRequirementsOrder;
            changes.push({
              change: "Uppdatera ordningen fr centralt innehll och kunskapskrav",
              content: `${course.name} p ${course.school} (${course.description})`,
              mutations: r => {
                r.put("courses", { ...course });
              }
            });
          }
        }
      }
    } else {
      // Subject not imported before. Add it!
      const newSubject: Subject = existingSubject = {
        id: createUUID(),
        acl: ["role:USER:R"],
        code: subjectToImport.code,
        name: subjectToImport.name,
        publishable: false,
        dateTime: ct()
      };
      changes.push({
        change: "Nytt mne",
        content: `${subjectToImport.name} (${subjectToImport.code})`,
        mutations: r => r.add("subjects", newSubject)
      });

      const subjectAbilities = [];

      for (let a of subjectToImport.abilities) {
        const newAbility: Ability = {
          id: createUUID(),
          name: a,
          acl: ["role:USER:R"],
          dateTime: ct()
        };

        subjectAbilities.push(newAbility); // To refer from in courses later on!

        changes.push({
          change: "Ny frmga",
          content: a,
          mutations: r => {
            r.add("abilities", newAbility);
            r.link("subjects", newSubject.id, "abilities", newAbility.id, "abilities");
          }
        });
      }

      for (let c of subjectToImport.courses) {
        const newCourse: Course = {
          id: createUUID(),
          subjectCode: newSubject.code,
          tags: ["sub:" + newSubject.code],
          dateTime: ct(),
          isTemplate: true,
          acl: ["role:EMPLOYEE:R"], // Standardkurser endast synliga fr lrare
          name: c.name,
          code: c.code,
          points: c.points,
          modules: [],
          subjectId: newSubject.id,
          resources: [],
          createdBy: { name: env.currentUser.displayName, url: "mailto:" + env.currentUser.mail },
          createdDate: Date.now(),
          knowledgeRequirementsOrder: [],
          centralContentOrder: []
        };

        changes.push({
          change: "Ny kurs",
          content: c.name,
          mutations: r => {
            r.add("courses", newCourse);
            r.link("subjects", newSubject.id, "courses", newCourse.id, "courseTemplates");
          }
        });

        for (let html of c.centralContent) {
          // Check if there exists an identical central content on previous course first
          let newCentralContent = centralContent.filter(cc => cc.name === html)[0];
          if (!newCentralContent) {
            newCentralContent = {
              id: createUUID(),
              dateTime: ct(),
              name: html,
              acl: ["role:USER:R"]
            };
            centralContent.push(newCentralContent);

            changes.push({
              change: "Nytt centralt innehll",
              content: html,
              mutations: r => {
                r.add("central-content", newCentralContent);
                r.link("courses", newCourse.id, "central-content", newCentralContent.id, "centralContent");
              }
            });
          } else {

            changes.push({
              mutations: r => {
                r.link("courses", newCourse.id, "central-content", newCentralContent.id, "centralContent");
              }
            });
          }
          // Register the order in which this central content appeared in the XML:
          newCourse.centralContentOrder.push(newCentralContent.id);
        }

        for (let cr of c.knowledgeRequirements) {
          let newKnowledgeRequirement = knowledgeRequirements.filter(kr => kr.name === cr.html && kr.gradeStep === cr.gradeStep)[0];
          if (!newKnowledgeRequirement) {
            newKnowledgeRequirement = {
              id: createUUID(),
              dateTime: ct(),
              name: cr.html,
              gradeStep: cr.gradeStep,
              acl: ["role:USER:R"]
            };

            knowledgeRequirements.push(newKnowledgeRequirement);

            changes.push({
              change: "Nytt kunskapskrav fr betyget " + cr.gradeStep,
              content: cr.html,
              mutations: r => {
                r.add("knowledge-requirements", newKnowledgeRequirement);
                r.link("courses", newCourse.id, "knowledge-requirements", newKnowledgeRequirement.id, "knowledgeRequirements");
              }
            });
          } else {
            changes.push({
              mutations: r => {
                r.link("courses", newCourse.id, "knowledge-requirements", newKnowledgeRequirement.id, "knowledgeRequirements");
              }
            })
          }
          // Register the order in which this knowledge requirement appeared in the XML:
          newCourse.knowledgeRequirementsOrder.push(newKnowledgeRequirement.id);
        }
        // Build knowledge matrix

        // Link directly from course template to all abilities that the subject has:
        for (const a of subjectAbilities) {
          changes.push({
            mutations: r => {
              r.link("courses", newCourse.id, "abilities", a.id, "abilities");
            }
          })
        }
      }
    }
    return changes;
  }

  cancel() {
    this.props.onCancel();
  }

  async import() {
    this.setState({ isWorking: true, isImporting: true });
    const allMutations = this.state.changes.map(change => change.mutations);
    try {
      await env.kedBackendClient.do(r => {
        allMutations.forEach(mut => mut(r));
      });
    } finally {
      this.setState({ isWorking: false, isImporting: false });
    }
    this.props.onImportSuccess(this.props.name);
  }

  fixL(html: string) {
    return html;//.replace('<l fromat="OL">')
  }

  render() {
    return <div><h1>{L`Uppladdat mne`}</h1>
      {this.state.isWorking ? <p>Arbetar...</p> :
        this.state.isImporting ? <p>Importerar...</p> :
          this.state.changes.length === 0 ?
            <div>
              <p>mnet redan importerat.</p>
              <button onClick={() => this.cancel()}> OK </button>
            </div> :
        <div>
          <table><thead><tr>
          <th colSpan={2}>Granskning av ndringar i grund-data</th>
        </tr><tr>
            <th>ndring</th>
            <th>Innehll</th>
          </tr></thead><tbody>
            {this.state.changes.filter(change => change.change).map(change => <tr key={change.change + change.content}>
              <td>{change.change}</td>
              <td dangerouslySetInnerHTML={{ __html: change.content }} />
            </tr>)}
          </tbody></table>
        <button onClick={() => this.cancel()} disabled={this.state.isImporting}>Avbryt</button>
        <button onClick={() => this.import()} disabled={this.state.isImporting}>Importera</button>
        <br />
        </div>} 
      <button
        onClick={() => this.setState({ showFullText: !this.state.showFullText })}>
        {this.state.showFullText ?
          L`Dlj nedan` :
          L`Visa hela texten frn Skolverket`}
      </button>

      {/* The table below is NOT just for show!
          Functionality is dependent on its existence for importing stuff using jQuery to cherry-pick data. */}          
      <table style={{ display: this.state.showFullText ? '' : 'none' }}><tbody><tr>
        <th>
          Namn
          </th>
        <td>
          {this.props.name}
        </td>
      </tr><tr>
          <th>
            mneskod
          </th>
          <td>
            {this.props.code}
          </td>
        </tr><tr>
          <th>
            Syfte
          </th>
          <td ref={elem => this.purposeElem = elem} dangerouslySetInnerHTML={{ __html: this.fixL(this.props.purpose) }} />
        </tr><tr>
          <th>
            Kurser
          </th>
          <td>
            {this.props.courses.map(course => <table key={course.code} ref={elem => this.coursesElems[course.code] = { course, elem }}><tbody>
              <tr><th>Kursens namn</th><td>{course.name}</td></tr>
              <tr><th>Kurskod</th><td>{course.code}</td></tr>
              <tr><th>Pong</th><td>{course.points}</td></tr>
              <tr><th>Centralt innehll</th><td ref={elem => this.centralContentElems.push(elem)} dangerouslySetInnerHTML={{ __html: this.fixL(course.centralContent) }} /></tr>
              <tr><th>Kunskapskrav Betyg E</th><td className="grade-step-E" ref={elem => this.knowledgeRequirementElems.push(elem)} dangerouslySetInnerHTML={{ __html: this.fixL(course.knownledgeRequirements.filter(r => r.gradeStep === 'E')[0].text) }} /></tr>
              <tr><th>Kunskapskrav Betyg C</th><td className="grade-step-C" ref={elem => this.knowledgeRequirementElems.push(elem)} dangerouslySetInnerHTML={{ __html: this.fixL(course.knownledgeRequirements.filter(r => r.gradeStep === 'C')[0].text) }} /></tr>
              <tr><th>Kunskapskrav Betyg A</th><td className="grade-step-A" ref={elem => this.knowledgeRequirementElems.push(elem)} dangerouslySetInnerHTML={{ __html: this.fixL(course.knownledgeRequirements.filter(r => r.gradeStep === 'A')[0].text) }} /></tr>
            </tbody></table>)}
          </td>
        </tr></tbody></table>
    </div>
  }
}
