import * as React from 'react';
import { Resource } from 'kedbackend/models';
import { RemoveItem } from "./remove-item";

interface Props {
  teachers: Resource[];
  onUpdate: (teachersUpdates) => void;
}

interface State {
  addEmtpyRow: boolean;
}

export class EditableTeacherList extends React.Component<Props,State> {
  nameElem: HTMLInputElement;

  constructor(props: Props) {
    super(props);
    this.state={addEmtpyRow: props.teachers.length === 0};
  }

  render() {
    let {teachers, onUpdate} = this.props;
    const {addEmtpyRow} = this.state;
    if (addEmtpyRow) teachers = teachers.concat({name: "", url: ""});

    return <div className="teachers-list">
      <h3>Ansvariga lrare</h3>
      <table tabIndex={1} style={{width: "100%"}} onBlur={ev=>
        (!ev.relatedTarget ||
        (ev.relatedTarget as any).tagName !== 'INPUT') &&
        this.props.teachers.length > 0 &&
        this.setState({addEmtpyRow: false})
      }>
        <thead>
          {teachers.length > 0 && <tr>
            <th style={{width: "20%"}}>Namn</th>
            <th>Lnk eller E-postadress</th>
          </tr>}
        </thead>
        <tbody>
          {teachers.map((teacher, idx) => 
          <tr key={idx}>
            <td><input type="text" ref={elem => this.nameElem = elem} value={teacher.name} autoFocus={!teacher.name && idx > 0} onChange={
              ev=>{
                if (addEmtpyRow && idx == teachers.length - 1) {
                  onUpdate({$push: [{...teacher, name: ev.target.value}] });
                  this.setState({addEmtpyRow: false});
                } else if (ev.target.value || teacher.url) {
                  onUpdate({$splice: [[idx, 1, {...teacher, name: ev.target.value}]] });
                } else {
                  if (this.props.teachers.length === 1)
                    this.setState({addEmtpyRow: true});
                  onUpdate({$splice: [[idx, 1]]});
                }
              }
            }/></td>
            <td><input
              type="text"
              style={{width: "100%"}}
              value={teacher.url}
              onKeyDown={ev =>
                ev.which === 9 &&
                !ev.shiftKey &&
                idx === teachers.length - 1 &&
                (teachers.length < 1 || !!teachers[teachers.length - 1].name &&
                  (this.setState({addEmtpyRow: true}),ev.preventDefault()))}
              onChange={ev => {
                if (addEmtpyRow && idx == teachers.length - 1) {
                  onUpdate({$push: [{...teacher, url: ev.target.value}]})
                  this.setState({addEmtpyRow: false});
                } else if (ev.target.value || teacher.name) {
                  onUpdate({$splice: [[idx, 1, {...teacher, url: ev.target.value}]]});
                } else {
                  if (this.props.teachers.length === 1)
                    this.setState({addEmtpyRow: true});
                  onUpdate({$splice: [[idx, 1]]});
                }
              }
              
            }/></td>
          </tr>)}
        </tbody>
      </table>
      <br/>
      <a className="btn" onClick={() =>
        this.state.addEmtpyRow ?
          this.nameElem.focus() :
          this.setState({addEmtpyRow: true})}>
            <i className="fa fa-user-plus" aria-hidden /> Lgg till ansvarig lrare</a>
    </div>
  }
}