import * as React from 'react';
import { HashRouter as Router, Route, Link, Redirect } from 'react-router-dom';
import { ICourseEditorHost } from "../interfaces/course-editor-host";
import { Course } from 'kedbackend/models';
import { readBlobAsText, allowCopy, readBlobAsDataUrl } from "../utils";
import { showError, L } from "../../../utils/utils";
import { createUUID } from "kedbackend/client";
import { hasAccess } from "../../../access-control";
import env from '../../../globals/KED.env';

type Callback = () => void;

interface Props {
  title: string,
  activePage: BannerMenuItem,
  callbacks?: { [menuName: string]: Callback },
  routes: { [menuName: string]: string } & { feedback: string },
  backgroundImage?: string,
  isTemplate?,
  host?: ICourseEditorHost,
  course?: Course,
  origCourse?: Course
}

type BannerMenuItem =
  "courseBuilder" |
  "studentPage" |
  "templateBuilder" |
  "subjects" |
  "schools";

const AdminMenuItems = [
  { name: "schools", text: L`Skolor`, route: "/schools" },
  { name: "subjects", text: L`mnen`, route: "/subjects" },
  //{name: "templateBuilder", text: L`Mallbyggaren`, route: "/course-templates"},
]

const NonAdminMenuItems = [
  { name: "courseBuilder", text: L`Kursbyggaren`, route: "/courses/new" },
  { name: "studentPage", text: L`Elevsida`, route: "" },
  { name: "feedback", text: <span><i className="fa fa-commenting-o" aria-hidden="true"></i> Feedback</span>, route: "https://kg.kunskapsporten.se/kursbyggaren/" }
]


async function onDropImage(ev: React.DragEvent<HTMLDivElement>, host: ICourseEditorHost, course: Course, origCourse: Course) {
  ev.stopPropagation();
  ev.preventDefault();
  const files = ev.dataTransfer.files;
  let url = null;
  const items = Array.from(ev.dataTransfer.items);
  if (files.length > 0) {
    const file = files[0];
    if (file.size > 2 * 1024 * 1024) {
      showError(L`Kan inte ladda upp bilder strre n 2 MB`);
      return;
    }
    url = await readBlobAsDataUrl(file);
  } else {
    for (let i = 0; i < items.length; ++i) {
      const item = items[i];
      if (item.type.match('^text/uri-list')) {
        // URI
        url = await new Promise(resolve => item.getAsString(resolve));
      }
    }
  }
  if (url == null) {
    showError(L`Kunde inte hitta ngon bild i innehllet`);
    return;
  }
  setCourseImage(course, origCourse, host, url);
}

export function setCourseImage(
  course: Course,
  origCourse: Course,
  host: ICourseEditorHost,
  url: string
) {
  const imageId = createUUID();
  host.update({
    imageId: { $set: imageId },
    images: {
      $set: [{
        id: imageId,
        url,
        acl: [
          `role:USER:R`,
          `schoolRole:${env.currentUser.school}/EMPLOYEE:S`
        ],
        $meta: 'add'
      }]
    }
  });
}

