export const CourseBanner = (props: Props) => {
  const { title, isTemplate, host, course, origCourse, backgroundImage, activePage } = props;
  const isAdmin = env.currentUser.roles.some(role => role === "ADMIN");
  let menuItems = NonAdminMenuItems;
  if (isAdmin) menuItems = AdminMenuItems.concat(menuItems);

  return <div className="sv-row sv-layout sv-skip-spacer">
    <div className="sv-layout sv-skip-spacer sv-column-12">
      <div className="sv-script-portlet sv-portlet sv-skip-spacer">
        {backgroundImage && <style>{`
        .pageHeader {
          background-image: url('${backgroundImage}') !important;
        }
      `}</style>}
        <div className="pageHeader"
          onDragOver={course && allowCopy}
          onDrop={course && (ev => onDropImage(ev, host, course, origCourse))}>
          <a>
            <h1>{title}</h1>
          </a>
          <div className="buttonsField">
            <div className="buttonsContainer">
              <div className="align-horizontal"></div>
              <div className='horizontalMenu'>
                <ul>
                  {menuItems.map(item => ({
                    item,
                    isActive: activePage === item.name,
                    callback: props.callbacks && props.callbacks[item.name],
                    givenRoute: (props.routes && props.routes[item.name]),
                    defaultRoute: item.route
                  })).filter(x => x.callback || x.defaultRoute || x.givenRoute).map(({ item, isActive, callback, defaultRoute, givenRoute }) => {
                    return <li
                      key={item.name}
                      className={isActive ? "activePage" : ""}
                      onClick={callback}
                    >
                      {callback ?
                        <a>{item.text}</a> :
                        defaultRoute || givenRoute ?
                          givenRoute || /^http[s]\:\/\//i.test(defaultRoute) ?
                            <a href={givenRoute || defaultRoute}>{item.text}</a> :
                            <Link to={defaultRoute}>{item.text}</Link> :
                          <a>{item.text}</a>}
                    </li>;
                  })}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
}
