import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { Course, CourseModule, Ability, Task, Document } from 'kedbackend/models';
import { createUUID } from 'kedbackend/client';
import { KnowledgeMatrix } from '../sub-components/knowledge-matrix';
import update from 'react-addons-update';
import { Dialog, ICourseEditorHost } from "../interfaces/course-editor-host";
import { hasWriteAccess } from '../../../access-control';
import { SelectRelatedDocs } from '../sub-components/select-related-docs';
import validUrl from 'valid-url';
import { L, showError } from "../../../utils/utils";
import { CachedResponse } from "../../../utils/cached-response";
import { EditableResourceList } from "../sub-components/editable-resource-list";
import { dtFormat } from "../utils";
import { futureAbilities } from "../../../contracts/ked-models";
import env from '../../../globals/KED.env';
import { Spinner } from "../sub-components/spinner";
import { Wysiwyg } from '../../utility-components/wysiwyg';
import actions_swedish from '../../utility-components/wysiwyg/actions-sv';
import ReactDatePicker from 'react-datepicker';
import moment from 'moment';
import { Features } from '../../../features';

interface Props {
  searchMode?: boolean;
  task?: Task;
  taskCustomization?: Partial<Task>;
  module: CourseModule;
  course: Course;
  host: ICourseEditorHost;
  idsNotCoveredByAnyTask: { [id: string]: true };
  onSave: (task: Task, taskCustomization: Partial<Task>) => Promise<any>;
  onSaveCustomization: (taskCustomization: Partial<Task>) => Promise<any>;
  onReplace: (taskId: string, task: Task) => Promise<any>;
  onCancel: () => void;
  onDelete: () => void;
}

interface State {
  title: string;
  task: Task;
  taskCustomization: Partial<Task>
  futureAbilitiesOpen: boolean;
  searchResult?: Task[];
  searchMarkedIds?: string[];
  isSearching: boolean;
  previewTaskContent?: string;
}

export class EditTask extends React.Component<Props, State> implements Dialog {
  searchResponse?: CachedResponse<Task[]>;
  dialogElem: HTMLDivElement;
  features = new Features();

  setStatePromised(state: Partial<State>) {
    return new Promise(resolve => super.setState(() => state as State, resolve));
  }

  constructor(props: Props) {
    super(props);
    const writeAccess = !props.task || hasWriteAccess(props.task);
    this.state = {
      title: props.task ?
        writeAccess ?
          L`Redigera uppgift till "${props.module.name}"` :
          L`Visa uppgift till "${props.module.name}"` :
        props.searchMode ?
          L`Sk ny uppgift till "${props.module.name}"` :
          L`Lgg till egen uppgift till "${props.module.name}"`,
      futureAbilitiesOpen: false,
      taskCustomization: props.taskCustomization || {},
      task: props.task ?
        // A task was given. Clone it and add $meta: 'update' to it
        // to inform EditCourse.save() to update it on its own entity.
        {
          $meta: props.task.$meta || 'update',
          ...props.task
        } : props.searchMode ? null :
          // No task was given. Set $meta = 'add' to inform EditCourse.save()
          // to add the task to the tasks table.
          {
            id: createUUID(),
            $meta: 'add',
            tags: ["sub:" + props.course.subjectCode],
            school: !props.course.isTemplate && env.currentUser.school,
            resources: [],
            abilities: [],
            centralContent: [],
            futureAbilities: [],
            knowledgeRequirements: [],
            name: "",
            url: "",
            acl: props.course.isTemplate ? [
              `role:USER:R`// (OK to keep after kursbygg-changes). Templates (which will be empty!) will not have tasks. If they had, might not tasks should not be possible to modify. (An ADMIN could belong to a school! Other non-admins at that school should not be able to edit it)
            ] : [
                `role:USER:R`,
                `schoolRole:${env.currentUser.school}/EMPLOYEE:S`
              ]
          },
      searchMarkedIds: props.searchMode && [],
      searchResult: props.searchMode && [],
      isSearching: props.searchMode
    };
    if (props.searchMode) {
      this.searchResponse = new CachedResponse(() =>
        env.kedBackendClient.list<Task>("tasks", {
          tags: `sub:${props.course.subjectCode}`,
          include: ['knowledgeRequirements', 'abilities', 'centralContent'],
          flags: ['includeIdsOnly']
        })); // TODO: includeIds istead of include?
    }
  }

  async componentDidMount() {
    if (this.props.searchMode) {
      this.setStatePromised({ isSearching: true });
      try {
        await this.searchResponse.query();
        if (this.state.searchMarkedIds && this.state.searchMarkedIds.length === 0) {
          await this.updateSearchResult(this.state.searchMarkedIds);
        }
      } catch (err) {
        showError(err);
      } finally {
        await this.setStatePromised({ isSearching: false });
      }
    }
  }

  async updateSearchMarkedIds(searchMarkedIds: string[]) {
    const setStatePromise = this.setStatePromised({ task: null, searchMarkedIds, previewTaskContent: null });
    if (!this.searchResponse.result) {
      await this.searchResponse.promise;
      // Refresh searchMarkedIds now as user might have clicked around while waiting for
      // tasks to load...
      await setStatePromise; // So we know state has been reflected...
      searchMarkedIds = this.state.searchMarkedIds;
    }
    await this.updateSearchResult(searchMarkedIds);
  }

  async updateSearchResult(searchMarkedIds: string[]) {
    const allTasksForSubject = await this.searchResponse.query();

    const searchResult = allTasksForSubject.filter(t =>
      searchMarkedIds.every(id =>
        t.knowledgeRequirements.some(kr => kr.id === id) ||
        t.abilities.some(a => a.id === id) ||
        t.centralContent.some(c => c.id === id)));

    this.setStatePromised({
      searchResult
    });
  }

  onMarkChanged(id: string, isMarked: boolean) {
    const { searchMode } = this.props;
    if (searchMode) {
      const { searchMarkedIds } = this.state;
      this.updateSearchMarkedIds(isMarked ?
        searchMarkedIds.concat(id) :
        searchMarkedIds.filter(x => x !== id)).catch(showError);
      return;
    }
    const { course } = this.props;
    const { task } = this.state;
    // One of the following three will be found:
    const ability = course.abilities.find(a => a.id === id);
    const knowledgeRequirement = course.knowledgeRequirements.find(r => r.id === id);
    const centralContent = course.centralContent.find(cc => cc.id === id);

    const [collectionProp, item] =
      ability ?
        ["abilities", ability] :
        knowledgeRequirement ?
          ["knowledgeRequirements", knowledgeRequirement] :
          centralContent ?
            ["centralContent", centralContent] :
            [null, null];

    if (!collectionProp) {
      console.error("Could not find marked id " + id);
      return;
    }

    const updatedCollection: Document[] = task[collectionProp].slice();
    if (isMarked)
      updatedCollection.push(item);
    else
      updatedCollection.splice(updatedCollection.findIndex(doc => doc.id === id), 1);

    const updatedTask = { ...task };
    updatedTask[collectionProp] = updatedCollection;

    this.setStatePromised({
      task: updatedTask
    });
  }

  async save() {
    const { task, taskCustomization } = this.state;
    const writeAccess = hasWriteAccess(task);
    if (writeAccess) {
      if (!task.url && !task.content)
        throw new Error(L`Uppgiften mste antingen ha en URL eller ett innehll`);
      if (task.url && !validUrl.isUri(task.url))
        throw new Error(L`Angiven URL '${task.url}' r ogiltig. Den ska brja med http: eller https:`);
      if (this.props.task && this.props.task.id !== task.id) {
        // Replace existing task with copy
        await this.props.onReplace(this.props.task.id, task);
      } else {
        // Update existing task
        await this.props.onSave(task, null);
      }
    } else if (this.props.searchMode) {
      await this.props.onSave(task, taskCustomization);
    } else {
      if (this.props.course.isTemplate && task.$meta === 'add')
        throw new Error(L`Du saknar behrighet fr att spara nya versioner av uppgifter i kursmallen`);

      await this.props.onSaveCustomization(taskCustomization);
    }
  }

  async copyTask() {
    const taskCopy = {
      ...this.state.task,
      ...(this.state.taskCustomization || {}),
      id: createUUID(),
      acl: this.props.course.isTemplate ? [
        `role:USER:R` // (OK to keep but this check might become unnescesary if we totally remove the possibility to edit templates) Template tasks should not be possible to modify. (An ADMIN could belong to a school! Other non-admins at that school should not be able to edit it)
      ] : [
          `role:USER:R`,
          `schoolRole:${env.currentUser.school}/EMPLOYEE:S`
        ]
    };
    taskCopy.$meta = 'add';

    await this.setStatePromised({
      task: taskCopy,
      title: L`Redigera kopierad uppgift till "${this.props.module.name}"`,
      taskCustomization: undefined
    });
  }

  getTaskProp<T extends keyof Task>(taskProp: T) : Task[T] {
    const {task, taskCustomization} = this.state;
    const customization = taskCustomization && taskCustomization[taskProp];
    return customization !== undefined ?
      customization :
      task[taskProp];
  }

  updateTaskOrCustomization (taskProp: keyof Task, updates) {
    const {task, taskCustomization} = this.state;
    const writeAccess = task && hasWriteAccess(task);
    if (writeAccess && (!taskCustomization || taskCustomization[taskProp] === undefined)) {
      const newTask = {...task};
      newTask[taskProp] = update(task[taskProp], updates);
      this.setStatePromised({task: newTask});
    } else {
      const newCustomization = {...taskCustomization};
      newCustomization[taskProp] = update (
        newCustomization[taskProp] || task[taskProp],
        updates
      );
      this.setStatePromised({taskCustomization: newCustomization});
    }
  }

  render() {
    const { course, module, onCancel, onDelete, idsNotCoveredByAnyTask, searchMode } = this.props;
    const { task, taskCustomization, title, searchResult, searchMarkedIds, isSearching } = this.state;
    const markedIds = searchMode ?
      searchMarkedIds :
      task.centralContent.map(x => x.id)
        .concat(task.abilities.map(x => x.id))
        .concat(task.knowledgeRequirements.map(x => x.id));

    const writeAccess = task && hasWriteAccess(task);
    const taskUrlValid = task && validUrl.isUri(task.url);
    const {features} = this;

    return <div className="sv-spacer-20pxvt sv-vertical sv-layout sv-skip-spacer">
      <div className="pagecontent sv-layout sv-spacer-20pxvt sv-skip-spacer">
        <div className="sv-text-portlet sv-use-margins sv-skip-spacer">
          <div className="sv-text-portlet-content">
            <h1 className="h1">{title}</h1>
          </div>
        </div>
        <div className="sv-fluid-grid sv-grid-ksgs12 sv-layout">
          <div className="sv-layout sv-skip-spacer sv-column-6">
            <div className="sv-html-portlet sv-portlet sv-skip-spacer">
              <div className="ked_boxed">
                <h3>{searchMode ?
                  "Filtrera p kunskapskrav" : writeAccess ?
                    "Vlj kunskapskrav" :
                    "Kunskapskrav"}</h3>
                {searchMode ?
                  <p>
                    Markera de kunskapskrav som uppgiften mste tcka.
                  </p> : writeAccess &&
                  <p>
                    Markera de kunskapskrav som din uppgift tcker.
                  </p>
                }
                <KnowledgeMatrix
                  knowledgeRequirements={course.knowledgeRequirements}
                  markedIds={markedIds}
                  idsToMarkNotOk={idsNotCoveredByAnyTask}
                  markMode={searchMode || writeAccess}
                  onMarkChanged={this.onMarkChanged.bind(this)}
                />
              </div>
            </div>

            <div className="sv-html-portlet sv-portlet">
              <SelectRelatedDocs
                options={course.abilities}
                title={writeAccess || searchMode ? "Vlj frmgor" : "Frmgor"}
                markedIds={markedIds}
                markMode={writeAccess || searchMode}
                onMarkChanged={this.onMarkChanged.bind(this)} />
            </div>

            <div className="sv-html-portlet sv-portlet">
              <SelectRelatedDocs
                options={course.centralContent}
                title={writeAccess || searchMode ? "Vlj centralt innehll" : "Centralt innehll"}
                markedIds={markedIds}
                markMode={writeAccess || searchMode}
                onMarkChanged={this.onMarkChanged.bind(this)} />
            </div>

            {!searchMode && <div className="sv-html-portlet sv-portlet"><div className={"openClose larger" + (this.state.futureAbilitiesOpen ? " open" : "")}>
              <div className="openHeader" onClick={() => this.setStatePromised({ futureAbilitiesOpen: !this.state.futureAbilitiesOpen })}>
                {writeAccess ?
                  <p>Vlj ytterligare alternativ</p> :
                  <p>Ytterligare alternativ</p>}
              </div>
              <div className="openContent">
                <h4>Framtidsfrmgor</h4>

                <div className="taskContainer">
                  {futureAbilities.map(fa => {
                    const selected = task.futureAbilities.indexOf(fa) >= 0;
                    return <div className="align-horizontal" key={fa}>
                      <div className="horizontalItem top">
                        <div
                          className={"checkBox" + (selected ? " checked" : "")}
                          onClick={writeAccess ? () => {
                            const updatedTask = { ...task };
                            updatedTask.futureAbilities =
                              selected ?
                                task.futureAbilities.filter(a => a != fa) :
                                task.futureAbilities.concat(fa);
                            this.setStatePromised({ task: updatedTask });
                          } : undefined} />
                      </div>
                      <div className="horizontalItem top">{fa}
                      </div>
                    </div>
                  })}
                </div>
              </div>

            </div></div>}
          </div>
          <div className="sv-layout sv-column-6">
            <div className="sv-html-portlet sv-portlet sv-skip-spacer">
              {isSearching && <p><Spinner /> Laddar uppgifter...</p>}
              {searchResult ? <div className={"task-search-result" + (!this.state.task && !this.state.previewTaskContent ? " fixed" : "")}>
                {searchMarkedIds.length === 0 ?
                  <h2>Samtliga uppgifter fr mnet</h2> :
                  <h2>Uppgifter som tcker valda filter</h2>}
                <div className="taskContainer">
                  {searchResult.map(foundTask => <div key={foundTask.id} className="align-horizontal">
                    <div className="horizontalItem top">
                      <div
                        className={"checkBox" + (task && task.id === foundTask.id ? " checked" : "")}
                        onClick={() => findDOMNode(this).parentElement.scrollTop = 0 || this.setStatePromised({ task: foundTask, previewTaskContent: null })} />
                    </div>
                    <div className="horizontalItem top">
                      {foundTask.content ?
                        <a href="#" onClick={ev => {
                          this.setState({ previewTaskContent: foundTask.content, task: null });
                          ev.preventDefault();
                          ev.stopPropagation();
                        }}>{foundTask.name}</a> :
                        <a href={foundTask.url} target="_blank">{foundTask.name}</a>}
                    </div>
                  </div>)}
                </div>
                <br />
                <hr />
              </div> : task && <div>
                <h2>Uppgiftens namn och lnk</h2>
                <div className="align-horizontal spaced">
                  <div className="horizontalItem top">
                    <p>Uppgiftens namn:</p>
                  </div>
                  <div className="horizontalItem top">
                    <input autoFocus type="text" size={35} value={task.name} disabled={!writeAccess} readOnly={!writeAccess} onChange={ev => {
                      const newTask = { ...task };
                      newTask.name = ev.target.value;
                      this.setStatePromised({ task: newTask });
                    }} />
                  </div>
                </div>
                <div className="align-horizontal spaced">
                  <div className="horizontalItem top">
                    <p>URL:</p>
                  </div>
                  <div className={"horizontalItem top"}>
                    <input type="text" size={35} value={task.url} disabled={!writeAccess} readOnly={!writeAccess} onChange={ev => {
                      const newTask = { ...task };
                      newTask.url = ev.target.value;
                      this.setStatePromised({ task: newTask });
                    }} />
                  </div>
                </div>

                {features.AssignmentTasks && <><h2>Inlmningsuppgift</h2>
                <div className="align-horizontal">
                  <div className="horizontalItem top">
                    <div className={"checkBox" + (this.getTaskProp("assignment") ? " checked" : "")}
                      onClick={() => {
                        this.updateTaskOrCustomization("assignment", {$set: !this.getTaskProp("assignment")});
                      }}
                    />
                  </div>
                  <div className="horizontalItem top">
                      <p>Den hr uppgiften r en inlmningsuppgift</p>
                  </div>
                </div>
                {this.getTaskProp("assignment") && <>
                  <p>Inlmningsdatum</p>
                    <div className="align-horizontal">
                      <div className="horizontalItem top">
                        <ReactDatePicker
                          selected={this.getTaskProp("deadline") && moment(this.getTaskProp("deadline"))}
                          dateFormat="YYYY-MM-DD"
                          locale={"sv"}
                          popperPlacement="bottom-start"
                          onChange={value => {
                            this.updateTaskOrCustomization("deadline", {$set: value.format("YYYY-MM-DD")});
                          }}
                        />
                      </div>
                      <div className="horizontalItem top">
                        {this.getTaskProp("deadline") && <a className="deleteDate" href="#" title="Ta bort inlmningsdatum" onClick={ev=>{
                          ev.preventDefault();
                          this.updateTaskOrCustomization("deadline", {$set: null});
                        }}></a>}
                      </div>
                    </div>
                </>}
              </>}
              </div>}

              {this.state.previewTaskContent ? 
                <div dangerouslySetInnerHTML={{__html: this.state.previewTaskContent}} />
               : task && <>
                    <h3>Uppgiftens lrandeml</h3>
                    <div>
                      <textarea
                        className="inputTextBox learning-goal-box"
                        disabled={!writeAccess}
                        value={task.learningGoal}
                        onChange={ev => {
                          const newTask = { ...task };
                          newTask.learningGoal = ev.target.value;
                          this.setStatePromised({ task: newTask });
                        }}
                      />
                    </div>

                    <br />
                    {(writeAccess || !!task.content) && <>
                      <h3>Uppgiftens innehll</h3>
                        {writeAccess && <p>
                          Om uppgiften inte lnkar till ngot dokument kan du lgga innehll hr.
                        </p>}
                        <Wysiwyg
                          actions={[
                            "bold",
                            "italic",
                            "underline",
                            "strikethrough",
                            "heading2",
                            "heading3",
                            "olist",
                            "ulist",
                            "line",
                            "link",
                            "image"]}
                          defaultActions={actions_swedish}
                          readOnly={!writeAccess}
                          html={task.content}
                          onChange={html => {
                            const newTask = { ...task };
                            newTask.content = html;
                            this.setState({ task: newTask });
                          }}
                        />
                    </>}

                  {taskCustomization && taskCustomization.resources ?
                    <h3>Resurser fr uppgiften (modifierad fr denna kursinstans)</h3> :
                    <h3>Resurser kopplade till uppgiften</h3>}

                  <EditableResourceList
                    resources={
                      (taskCustomization && taskCustomization.resources) || task.resources}
                    host={this.props.host}
                    onUpdate={resourceUpdates => {
                      this.updateTaskOrCustomization("resources", resourceUpdates);
                    }} />
                </>}
            </div>
            {task && <div className="sv-html-portlet sv-portlet"><hr />
              <h3>Uppgiftens status</h3>

              {task.createdBy && task.createdDate && <p>
                Uppgiften skapades {dtFormat(task.createdDate)} av <strong>{task.createdBy.name}</strong>.
              </p>}
              {task.modifiedBy && task.modifiedDate && <p>
                Uppgiften redigerades senast {dtFormat(task.modifiedDate)} av <strong>{task.modifiedBy.name}</strong>.
              </p>}

              <br />
              {!!this.props.onDelete && <div
                className="btn btn-warning btn-large pull-right"
                onClick={() => this.props.onDelete()}>
                Ta bort frn kursmodul
              </div>}
              <div className="pull-right">&nbsp;</div>
              <div className="btn btn-warning btn-large pull-right" onClick={() => this.props.onCancel()}>
                Avbryt
              </div>

              <div className="btn btn-large" onClick={() => this.save().catch(showError)}>
                Spara
              </div>
              {!writeAccess && <div className="btn btn-large" onClick={() => this.copyTask().catch(showError)}>
                Skapa kopia av den hr uppgiften
              </div>}
            </div>}
          </div>
        </div>
        <div className="sv-layout">
          <div className="sv-html-portlet sv-portlet sv-skip-spacer"></div>
        </div>
      </div></div>
  }

}