import * as React from 'react';
import { Course, Ability, Subject } from 'kedbackend/models';
import { EditableResourceList } from '../sub-components/editable-resource-list';
import { dtFormat } from '../utils';
import { CourseModuleList } from './course-module-list';
import { SchoolCourses } from '../sub-components/school-courses';
import { ICourseEditorHost } from '../interfaces/course-editor-host';
import { getUncoveredKnowledgeRequirements, sanityCheck, getIdsNotCoveredByTasks } from './business-logic';
import { setCourseImage } from "./course-banner";
import { Courses } from "./index";
import { ListCourseTemplates } from "./list-course-templates";
import { Spinner } from '../sub-components/spinner';
import { EditableTeacherList } from '../sub-components/editable-teacher-list';
import { showError, L } from "../../../utils/utils";
import { WeightedItemsTable } from '../sub-components/weighted-items-table';
import { Wysiwyg } from '../../utility-components/wysiwyg/index';
import actions_swedish from '../../utility-components/wysiwyg/actions-sv';
import { EditableWorkFlowLink } from '../sub-components/editable-workflow-link';
import { RenameableText } from '../../utility-components/renameable-text';

interface Props {
  course: Course;
  origCourse: Course;
  host: ICourseEditorHost;
};

interface State {
  changeImageUrl: boolean;
}

export class InnerEditCourse extends React.Component<Props,State> {
  props: Props;
  state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      changeImageUrl: false
    };
  }

  setImageUrl(imageUrl: string) {
    const { course } = this.props;
    if (!course.imageId) {

    }
  }

  render() {
    const { course, origCourse, host } = this.props;
    const uncoveredKnowledgeRequirements = getUncoveredKnowledgeRequirements(course);
    const idsNotCoveredByAnyTask = getIdsNotCoveredByTasks(course);
    const sanityCheckWarnings = sanityCheck(course);
    const courseImage = course.images.find(img => img.id === course.imageId);
    const courseImageIsUploaded = courseImage && courseImage.url.startsWith('data:');

    return <div className="sv-row sv-layout sv-skip-spacer">
      <div className={"sv-layout sv-skip-spacer sv-column-7"}>
        <div className="sv-html-portlet sv-portlet sv-skip-spacer">
          {course.isTemplate && <h1>Kursmall</h1> }
          <h2>Kursvergripande</h2>
          <hr />
          <div className="ked_boxed">
            <div>
              <h3>Kursens bild</h3>
              {courseImageIsUploaded ?
                <p>Kursens bild ligger i databasen. Fr att ndra bild mste du dra och slppa en ny bild ovanp den gamla hgst upp p denna sida.
                  Bilden kan antingen vara frn en annan webbsida. Lokala bildfiler gr ocks bra att dra och slppa.</p>
                 : courseImage ? <div><textarea
                  value={courseImage.url}
                  style={{width: '100%', height: '30px'}}
                  onChange={ev=>setCourseImage(course, origCourse, host, ev.target.value)}/>
                  <p>Bilden som visas hgst upp hmtas frn angiven URL.
                    Fr att ndra bild kan du redigera URL ovan. Ett annat stt att ndra bild r att dra och slppa en ny bild ver den gamla hgst upp p sidan.</p>
                </div> :
                  <p>Kursen saknar egen bild.
                    Lgg till en bild genom att dra och slppa en bild p kurs-bannern hgst upp p den hr sidan.</p>}
              <br/>
            </div>

            {!course.isTemplate && <div>
              <h3>Beskrivning</h3>
              <p className="subHeader">(Beskrivning av kursen. Fr att skilja mellan olika varianter av samma gymnasiekurs)</p>
              <textarea className="inputTextBox inputTextSmall"
                value={course.description}
                onChange={ev => host.update({ description: { $set: ev.target.value } })} />
              <br />
            </div>}            

            <div>
              <h3>Upptaktstext</h3>
              <p className="subHeader">(texten ska visas p kursens upptaktssektion)</p>
              <Wysiwyg
                html={course.runUpText}
                defaultActions={actions_swedish}
                actions={[
                  "bold",
                  "italic",
                  "underline",
                  "strikethrough",
                  "heading2",
                  "heading3",
                  "olist",
                  "ulist",
                  "line",
                  "link",
                  "image"]}
                onChange={newHtml => host.update({ runUpText: {$set: newHtml}})} />
              <br />
            </div>

            <h3>Kursresurser</h3>
            <p className="subHeader">(Resurser fr upptakt och kursen som helhet)</p>
            <EditableResourceList
              resources={course.resources}
              onUpdate={resourcesUpdates => host.update({ resources: resourcesUpdates })}
              host={host} />
            <br />
            <h3>Arbetsgng</h3>
            <p className="subHeader">(Eventuell lnk till arbetsgng fr kursen)</p>
            <EditableWorkFlowLink
              url={course.workFlowLink}
              onUpdate={updates => host.update({ workFlowLink: updates })}
              host={host} />
            <br />
            {!course.isTemplate && <div>
              <h3>Google Classroom</h3>
              <div className="align-horizontal spaced">
                <div className="horizontalItem top">
                  <input
                    placeholder="Google classroom URL"
                    type="text"
                    size={30}
                    value={course.googleClassRoomURL}
                    onChange={ev => host.update({googleClassRoomURL: {$set: ev.target.value}})}/>
                </div>
                <div className="horizontalItem top">
                  <input
                    placeholder="kod"
                    type="text"
                    size={8}
                    value={course.googleClassRoomCode}
                    onChange={ev => host.update({googleClassRoomCode: {$set: ev.target.value}})} />
                </div>
              </div>
            </div>}

            <br />
            {!course.isTemplate && <><EditableTeacherList 
              teachers={course.responsibleTeachers}
              onUpdate={teachersUpdate => {
                host.update({responsibleTeachers: teachersUpdate});
              }} />
            <br />
            <h3>Inlmnings E-post</h3>
            <table tabIndex={1} style={{width: '100%'}}>
              <thead><tr><th>
                E-postaddress
              </th></tr></thead>
              <tbody>
                <tr>
                  <td>
                    <RenameableText
                      eager={true}
                      clickAdvertiseText="Klicka hr fr att ange vem som tar emot inlmningsuppgifter"
                      text={course.assignmentEmails ? course.assignmentEmails[0] : ""}
                      placeholder="Ange mottagare av inlmningsuppgifter"
                      tag="input"
                      onChange={value => host.update({assignmentEmails: {$set: [value]}})}
                    />
                  </td>
                </tr>
              </tbody>
            </table>
            </>}

          </div>
          <div className="sv-html-portlet sv-portlet" id="svid12_6ab4811f15b13c706b43bb47">
            <div id="Kursensmoduler">
            </div>
            <h2>Kursens moduler</h2>
            <hr />
          </div>
          <CourseModuleList
            host={host}
            course={course}
            idsNotCoveredByAnyTask={idsNotCoveredByAnyTask} />
          
          <div>
            <br/>
            <WeightedItemsTable course={course}/>
          </div>

          <div className="sv-html-portlet sv-portlet">
            <h2>Kursens status</h2>
            <hr />
            {course.createdBy && course.createdDate && <p>
              Kursen skapades {dtFormat(course.createdDate)} av <strong>{course.createdBy.name}</strong>.
            </p>}
            {course.modifiedBy && course.modifiedDate && <p>
              Kursen redigerades senast {dtFormat(course.modifiedDate)} av <strong>{course.modifiedBy.name}</strong>.
            </p>}
            {course.active ? <p>
              Kursen r <strong>AKTIV</strong> sedan {dtFormat(course.activatedDate)} av <strong>{course.activatedBy.name}</strong>.
            </p> : <p>
                Kursen r <strong>INAKTIV</strong>{course.inactivatedBy ? <span>
                  sedan {dtFormat(course.inactivatedDate)} av <strong>{course.inactivatedBy.name}</strong>.</span> : <span>.</span>
                }
              </p>}
            {(uncoveredKnowledgeRequirements.length === 0 ?
              <p className="markedGreen">Modulernas kunskapskrav tcker skolverkets krav fr denna kurs</p> :
              <div className="markedRed warning-box"><div className="warningFlag">
                <i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
              </div><p>Modulernas kunskapskrav tcker inte skolverkets krav fr denna kurs.</p>
              </div>)}
            {sanityCheckWarnings.map(warning => <div key={warning} className="markedRed warning-box"><div className="warningFlag">
                <i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
              </div><p>{warning}</p></div>)}
            <br />
            {!course.isTemplate && (origCourse ? <div
              className="btn btn-warning btn-large pull-right"
              onClick={()=>{
                if (confirm(L`Bekrfta borttagning av kursen "${course.name} - ${course.description}" fr skolan ${course.school}\n\nKursen kommar att raderas helt!\n\nr du sker?`))
                  host.deleteCourse().catch(showError);
              }}
            >Ta bort kurs</div>:<div
              className="btn btn-warning btn-large pull-right"
              onClick={()=>history.go(-1)}
            >Avbryt
              </div>)}
            <div className="btn btn-large" onClick={() => host.save()}>
              Spara
            </div>
            {course.active ?
              <div className="btn btn-info btn-large"
                onClick={() => host.inactivateAndSave()}>
                Inaktivera och spara
              </div> :
              <div className="btn btn-info btn-large"
                onClick={() => host.activateAndSave()}>
                Aktivera och spara
              </div>}
          </div>
        </div>
      </div>
      <div className="sv-layout sv-column-5">
        <SchoolCourses />
      </div>
    </div>
  }
}