import * as React from 'react';
import {Resource} from 'kedbackend/models';
import {Link} from 'react-router-dom';
import {ICourseEditorHost} from '../interfaces/course-editor-host';
import {EditResource} from '../modal-pages/edit-resource';
import { updateModificationAndCreationStamps } from '../utils';
import env from '../../../globals/KED.env';

interface Props {
  resources: Resource[];
  onUpdate: (resourcesUpdates) => void;
  host: ICourseEditorHost;
}

interface State {

}

export class EditableResourceList extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state={};
  }

  render() {
    const {host, onUpdate, resources} = this.props;
    return <div>
      <div className="taskContainer">
        {resources.map((resource, idx) => 
          <div className="align-horizontal" key={idx}>
            <div className="horizontalItem top">
              <a className="editItem" onClick={()=>host.openDialog({
                Component: EditResource,
                props: {
                  title: "Redigera resurs",
                  resource,
                  onSave: editedResource => {
                    const updatedResource = updateModificationAndCreationStamps(editedResource, env.currentUser);
                    onUpdate({$splice: [[idx, 1, updatedResource]]});
                    host.closeDialog();
                  },
                  onDelete: ()=>{
                    onUpdate({$splice: [[idx, 1]]});
                    host.closeDialog();
                  }
                }
              })}></a>
            </div>
            <div className="horizontalItem top">
              <a href={resource.url} target="_blank" >{resource.name}</a>
            </div>
          </div>)}
     </div>
    <a className="btn" onClick={()=>host.openDialog({
      Component: EditResource,
      props: {
        title: "Lgg till resurs",
        onSave: newResource => {
          const updatedResource = updateModificationAndCreationStamps(newResource, env.currentUser);
          onUpdate({$push: [updatedResource]})
          host.closeDialog();
        }
      }
    })}><i className="fa fa-paperclip" aria-hidden></i> Lgg till resurs</a>
    </div>
  }
}
