import * as React from 'react';
import { L } from '../../../utils/utils';
import { Resource } from 'kedbackend/models';
import { Dialog } from '../interfaces/course-editor-host';
import validUrl from 'valid-url';

interface Props {
  url?: string;
  onSave: (newUrl: string) => void;
  onDelete?: ()=>void;
}

interface State {
  url: string;
}

export class EditWorkflowLink extends React.Component<Props,State> implements Dialog {
  constructor(props: Props) {
    super(props);
    this.state = {url: this.props.url || ''};
  }

  save() {
    const {url} = this.state;
    if (!validUrl.isUri(url)) throw new Error(L`Angiven URL '${url}' r ogiltig. Ska brja med http: eller https:`);
    this.props.onSave(url);
  }

  render() {
    const {onSave, onDelete} = this.props;
    const state = this.state;
    const isValidUrl = validUrl.isUri(this.state.url);

    return <div><div className="sv-html-portlet sv-portlet sv-skip-spacer">
      <h2>Koppla en arbetsgng till kursen</h2>
      <p>Det gr bara att lgga in en arbetsgng per kurs.</p>
      <br />
      <div className="align-horizontal">
        <div className="horizontalItem top">Lnk:</div>
        <div className="horizontalItem top ">
          <input type="text"
            tabIndex={1}
            size={50}
            value={state.url}
            onChange={ev=>this.setState({url: ev.target.value})} />
        </div>
        <div className="horizontalItem top spaced"></div>
      </div>
      <br /></div>
      <div className="sv-html-portlet sv-portlet" id="svid12_492422d515badf36646e4ff1">
        {onDelete && <div tabIndex={2} className="btn btn-warning btn-large pull-right" onClick={()=>onDelete()}>
          Ta bort arbetsgng
        </div>}
        <a
          tabIndex={1}
          className={"btn btn-large" + (isValidUrl ? "" : " btn-inactive")}
          onClick={isValidUrl && (() => onSave(state.url))}>Spara</a>
      </div>
    </div>
  }
}
