import * as React from 'react';
import { L } from '../../../utils/utils';
import { Subject } from 'kedbackend/models';
import env from '../../../globals/KED.env';
import { HashRouter as Router, Route, Link, Redirect } from 'react-router-dom';
import { CourseBanner } from "../courses/course-banner";
import { Spinner } from "../sub-components/spinner";

interface Props {
  id: string
  feedbackUrl: string
}

interface State {
  subject: Subject
}

export class ShowSubject extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {subject: null};
  }

  componentWillMount() {
    this.load();
  }

  async load() {
    const subject = await env.kedBackendClient.get<Subject>('subjects', this.props.id, { include: 'courseTemplates' });
    this.setState({ subject });
  }

  render() {
    if (!this.state.subject) return <p><Spinner /></p>;
    const { name, code, courseTemplates } = this.state.subject;
    return <div><CourseBanner
      title={name}
      activePage="subjects"
      routes={{feedback: this.props.feedbackUrl}}
       />
      <h2>Kurser fr mnet {name} ({code})</h2>
      <ul className="entity-list">
        {courseTemplates.map(({id, name, code, publishable, points}) =>
          <li key={id} className="complete">
            {name} - {points}p
          </li>)}
      </ul>
    </div>;
  }
}
