
export class CachedResponse<T> {
    promise: Promise<T> | null;
    result: T | null;
    constructor(public queryer: ()=>Promise<T>) {
        this.promise = null;
        this.result = null;
    }

    query() {
        return this.promise ?
            this.promise :
            (this.promise = this.queryer().then(x => this.result = x));
    }

    reset() {
        this.promise = null;
    }
}
