export {HttpError}

export interface ListOptions {
    from?: number,
    to?: number,
    include?: string | string[],
    role?: string | string[],
    hasEdgesTo?: string | string[],
    hasEdgesFrom?: string | string[],
    ids?: string[],
    name?: string,
    tags?: string | string[],
    flags?: QueryFlag[],
    cacheBust?: string | number
};

export type QueryFlag = 'idsOnly' | 'includeIdsOnly' | 'idsAndNamesOnly' | 'includeIdsAndNamesOnly'; // | 'otherFlag' | 'thirdFlag';...

export class KedBackendClient {
    http: RestClient;

    constructor (
        isomorphic: IsomorphicEnv,
        bearerProvider: BearerProvider,
        baseUrl: string)
    {
        this.http = new RestClient(
            isomorphic,
            baseUrl,
            {bearerProvider});
    }

    async getMyClaims(table?: string, fetchOptions?: RequestInit) : Promise<any> // TODO: Specify what you get here
    {
        const res = await this.http.get("me/claims/" + (table ||""), null, fetchOptions);
        if (res.status != 200) throw new HttpError(res.status, await res.text());
        return await res.json();
    }

    async get<T = any>(table, id, options?: {include?: string | string[]}, fetchOptions?: RequestInit) : Promise<T> {
        const res = await this.http.get(`${table}/${id}`, options, fetchOptions);
        if (res.status != 200) throw new HttpError(res.status, await res.text());
        return await res.json();
    }    

    async list<T = any>(table: string, options?: ListOptions, fetchOptions?: RequestInit) : Promise<T[]>
    {
        const res = await this.http.get(`${table}`, options, fetchOptions);
        if (res.status != 200) throw new HttpError(res.status, await res.text());
        return await res.json();
    }

    async batch(reqs: MutationRequest[], fetchOptions?: RequestInit) : Promise<BatchResponse> {
        // Reorder operations so that 'add's come first and 'delete's come last:
        reqs = reqs.slice().sort((req1, req2) =>
            req1.op === 'add' ? -1 : req2.op === 'add' ? 1 : 
            req1.op === 'delete' ? 1 : req2.op === 'delete' ? -1 : 0);
        const res = await this.http.post(`batch`, reqs, fetchOptions);
        if (res.status != 200) throw new HttpError(res.status, await res.text());
        return await res.json();
    }

    async do(scopeFn: (r:BatchRunner)=>void) : Promise<BatchResponse> {
        const runner = new BatchRunner();
        scopeFn(runner);
        return await this.batch(runner.mutationRequests);
    }

    async deleteRealm(realm: string) : Promise<number> {
        const res = await this.http.delete("realms/" + realm);
        if (res.status != 200) throw new HttpError(res.status, await res.text());
        return await res.json();
    }

    add(table:string, doc) {
        return this.do(r=>r.add(table, doc));
    }
    put(table:string, doc) {
        return this.do(r=>r.put(table, doc));
    }
    delete (table:string, id:string) {
        return this.do(r=>r.delete(table, id));
    }
    share(table:string, id:string, acl: string[]) {
        return this.do(r=>r.share(table, id, acl));
    }
    unshare (table:string, id:string, acl: string[]) {
        return this.do(r=>r.unshare(table, id, acl));
    }
    link (sourceTable:string, sourceId:string, targetTable:string, targetId:string, label:string) {
        return this.do(r=>r.link(sourceTable, sourceId, targetTable, targetId, label));
    }
    link2 (sourceTable:string, sourceId:string, targetId:string, label:string) {
        return this.do(r=>r.link2(sourceTable, sourceId, label, targetId));
    } 
    unlink (sourceTable:string, sourceId:string, targetTable:string, targetId:string, label:string) {
        return this.do(r=>r.unlink(sourceTable, sourceId, targetTable, targetId, label));
    }
    unlink2 (sourceTable:string, sourceId:string, targetId:string, label:string) {
        return this.do(r=>r.unlink2(sourceTable, sourceId, label, targetId));
    }
}

export class BatchRunner {
    mutationRequests: MutationRequest[];

    constructor() {
        this.mutationRequests = [];
    }

    add (table:string, doc) {
        this.mutationRequests.push({op: 'add', table, doc});
        return this;
    }
    put (table:string, doc) {
        doc = {...doc};
        delete doc.acl; // Forbidden to send acl with put() calls.
        this.mutationRequests.push({op: 'put', table, doc});
        return this;
    }
    delete (table:string, id:string) {
        this.mutationRequests.push({op: 'delete', table, id});
        return this;
    }
    share (table:string, id:string, acl:string[]) {
        this.mutationRequests.push({op: 'share', table, id, acl});
        return this;
    }
    unshare (table:string, id:string, acl:string[]) {
        this.mutationRequests.push({op: 'unshare', table, id, acl});
        return this;
    }
    link (sourceTable:string, sourceId:string, targetTable:string, targetId:string, label:string) {
        this.mutationRequests.push({op: 'link', sourceTable, sourceId, targetTable, targetId, label});
        return this;
    }
    link2 (sourceTable:string, sourceId:string, label:string, targetId:string) {
        this.mutationRequests.push({op: 'link', sourceTable, sourceId, targetId, label});
    }
    unlink (sourceTable:string, sourceId:string, targetTable:string, targetId:string, label:string) {
        this.mutationRequests.push({op: 'unlink', sourceTable, sourceId, targetTable, targetId, label});
        return this;
    }
    unlink2 (sourceTable:string, sourceId:string, label:string, targetId:string) {
        this.mutationRequests.push({op: 'unlink', sourceTable, sourceId, targetId, label});
        return this;
    }
}

export interface AddRequest {
    op: 'add';
    table: string;
    doc: {id?: string, acl?: string[], name?: string, dateTime?: number};
}

export interface PutRequest {
    op: 'put';
    table: string;
    doc: {id: string, name?: string, dateTime?: number};
}

export interface DeleteRequest {
    op: 'delete';
    table: string;
    id: string;
}

export interface ShareRequest {
    op: 'share';
    table: string;
    id: string;
    acl: string[];
}

export interface UnshareRequest {
    op: 'unshare';
    table: string;
    id: string;
    acl: string[];
}

export interface LinkRequest {
    op: 'link';
    sourceTable: string;
    sourceId: string;
    targetTable?: string;
    targetId: string;
    label: string;
}

export interface UnlinkRequest {
    op: 'unlink';
    sourceTable: string;
    sourceId: string;
    targetTable?: string;
    targetId: string;
    label: string;
}

export type MutationRequest =
    AddRequest |
    PutRequest |
    DeleteRequest |
    ShareRequest |
    UnshareRequest |
    LinkRequest |
    UnlinkRequest;

export interface BatchResponse {
    newEtags: {[id:string] : string};
}

