import * as React from 'react';
import {Resource} from 'kedbackend/models';
import {Link} from 'react-router-dom';
import {ICourseEditorHost} from '../interfaces/course-editor-host';
import {EditResource} from '../modal-pages/edit-resource';
import { updateModificationAndCreationStamps } from '../utils';
import env from '../../../globals/KED.env';
import { EditWorkflowLink } from '../modal-pages/edit-workflow-link';

interface Props {
  url: string;
  onUpdate: (updates) => void;
  host: ICourseEditorHost;
}

export class EditableWorkFlowLink extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
    this.state={};
  }

  render() {
    const {host, onUpdate, url} = this.props;
    return <div>
      <div className="taskContainer">
        {url &&
          <div className="align-horizontal">
            <div className="horizontalItem top">
              <a className="editItem" onClick={()=>host.openDialog({
                Component: EditWorkflowLink,
                props: {
                  url,
                  onSave: newUrl => {
                    onUpdate({$set: newUrl});
                    host.closeDialog();
                  },
                  onDelete: ()=>{
                    onUpdate({$set: undefined});
                    host.closeDialog();
                  }
                }
              })}></a>
            </div>
            <div className="horizontalItem top">
              <a href={url} target="_blank" >Nuvarande arbetsgng</a>
            </div>
          </div>
        }
     </div>
    {!url && <a className="btn" onClick={()=>host.openDialog({
      Component: EditWorkflowLink,
      props: {
        onSave: newUrl => {
          onUpdate({$set: newUrl})
          host.closeDialog();
        }
      }
    })}><i className="fa fa-paperclip" aria-hidden></i> Koppla en arbetsgng till kursen</a>}
    </div>
  }
}
