import $ from 'jquery';

const HEARTBEAT_INTERVAL = 5 * 60 * 1000; // 5 minutes.
const HEARTBEAT_URL = "/4.4b4cce2a160e445593f16043.html"; // Could be any protected resource. BUGBUG: Configurable.

let lastActivity = Date.now();

export function keepSessionAlive() {
  setInterval(onTimeout, HEARTBEAT_INTERVAL);
  setTimeout(()=>$('body')
    .mousemove(onUserActive)
    .keypress(onUserActive)
    .scroll(onUserActive), 100);
}

function onUserActive() {
  lastActivity = Date.now();
}

function onTimeout() {
  const inactivityTime = Date.now() - lastActivity;
  if (inactivityTime < HEARTBEAT_INTERVAL) {
    heartbeat();
  }
}

async function heartbeat() {
  console.log(`Sending heartbeat request to ${HEARTBEAT_URL}`);
  try {
    const res = await fetch(HEARTBEAT_URL, {
      cache: 'no-cache',
      credentials: 'same-origin'
    });
    console.log(`Response from ${HEARTBEAT_URL}: ${res.status} ${res.statusText}`);
  } catch (err) {
    console.warn(`Request to ${HEARTBEAT_URL} failed: ${err}`);
  }
}
