import cfg from '../globals/KED.cfg';
import env from '../globals/KED.env';

/* Contains ENV-based config, replaced using rollup-plugin-replace by its corresponding
 * ENV-variables.
 *
 * See 
*/
declare var process;

cfg.ENVIRONMENT = process.env.ENVIRONMENT; // "production", "development" or "test"
cfg.KED_TOKEN_PATH = null ; // Must be null in test environment for set-bearer-providers.ts to work accordingly!
cfg.KED_TOKEN_URL = process.env.KED_TOKEN_URL; // "https://kedauthtest.azurewebsites.net/token"
cfg.KED_CLIENT_ID = process.env.KED_CLIENT_ID; // "devclient", "testclient", "..."
cfg.KED_CLIENT_SECRET = process.env.KED_CLIENT_SECRET;
cfg.KED_API_URL = process.env.KED_API_URL; // "https://kedbackendtest.azurewebsites.net/api/"
cfg.EDS_API_URL = process.env.EDS_API_URL; // "https://edsportalowinapi.azurewebsites.net/api/"

import { users } from './data/users';
import { parseQueryString } from '../utils/query-string';
const username = parseQueryString (location.search).user;
if (username) {
  const user = users.find(u => u.username === username);
  if (user) {
    env.currentUser = user;
  }
}

const { role, school } = parseQueryString(location.search);
if (env.currentUser) {
  if (role) {
    env.currentUser.roles = role.split(',');
  }
  if (school) {
    env.currentUser.school = school;
  }
}
