export type Term = 'AT' | 'ST';
export type AcademicYear = string; // "2016/2017", "2017/2018", ...
export type SchoolGrade = number; // 0-9 (grundskola), 10-13 (gymnasium)
export type Grade = 'A' | 'B' | 'C' | 'D' | 'E';

export interface User {
    username?: string;
    mail: string;
    displayName: string;
    roles: string[];
    schoolGrade?: number;
    school?: string;
    schoolType?: string;
}

export interface LearningModuleTasks {
    moduleName: string;
    url?: string;
    subject: SchoolSubject;
    commonTasks?: LearningTask[];
    learningGoals: LearningGoal[];
}

export interface LearningGoal {
    name: string;
    tasks?: LearningTask[];
}

export interface CustomGoal {
  name: string,
  url: string,
  course: string,
  description: string
}


