export interface LearningTask {
    name: string;
    url?: string;
    description?: string;
    done?: boolean;
}

export interface LearningCourse {
    name: string;
    url?: string;
}

export interface SchoolSubject {
    name: string;
    course: LearningCourse;
}

export class Course {
    //Schema: "[year+term+schoolYear+name],sortorder";
    id?: string;
    //aYear: string; // Academic Year / Lsr / "2016/2017", "2017/2018", ...
    //schoolGrade: number; // rskurs (0-9 grundskola, 10-13 gymnasium). 100 = ej bundet till rskurs.
    //term?: Term; // AT (Autumn Term) / ST (Spring Term)
    name: string;
    points?: number;
    modules?: CourseModule[];
    learningGoals?: LearningGoal[];
    url?: string;
}


export interface CourseModule {
    id?: string;
    courseId: string;
    name: string;
    readonly url: string;
}

export interface TargetGrade {
    id?: string;
    user: string;
    courseId?: string;
    subjectId?: string;
    aYear?: AcademicYear; // Lsr, tex "2016/2017". null means final goal.
    term?: Term; // null means final goal.
    grade: Grade;
}


